
import React, { useState, useEffect, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link, useLocation } from 'react-router-dom';
import CreatePDL from '../modals/CreatePDL';
import EditPDL from '../modals/EditPDL';
import ConfirmArchive from '../modals/ConfirmArchive';
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';
import FilterDropdown from '../modals/FilterDropdown';
import RetrieveModal from '../modals/RetrieveModal';
import DeleteModal from '../modals/DeleteModal';
import {useReactToPrint} from 'react-to-print';
function PDLList() {
  const componentPDF = useRef();
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const day = String(currentDate.getDate()).padStart(2, '0');
  
  const formattedDate = `${year}-${month}-${day}`;
  
  const hours = String(currentDate.getHours()).padStart(2, '0');
  const minutes = String(currentDate.getMinutes()).padStart(2, '0');
  const formattedTime = `${hours}:${minutes}`;

  const [pdlData, setPdlData] = useState([]);
  const [selectedPdlId, setSelectedPdlId] = useState(null);
  const [primaryID, setPrimaryID] = useState(null);
  const [sortedPdlData, setSortedPdlData] = useState([]);
  const [selectedOption, setSelectedOption] = useState(''); // Set the default option to "option4"
  const [isCreatePDLModalOpen, setCreatePDLModalOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage] = useState(25);
  const [activeTab, setActiveTab] = useState("pdlList");
  const [selectedRows, setSelectedRows] = useState([]);
  const [selectMultipleEnabled, setSelectMultipleEnabled] = useState(false);
  const [filterOptions, setFilterOptions] = useState({
    name: [''],
    balance: ['Highest', 'Lowest', 'No Balance'], // Example for the 'Balance' column
    type: ['Regular', 'LGBT', 'Senior', 'PWD'], // Example for the 'Type' column
  });
  const [selectedFilter, setSelectedFilter] = useState({
    name: '',
    balance: '',
    type: '',
    // Add more columns as needed
  });
  const [selectedFilterLabel, setSelectedFilterLabel] = useState({
    name: 'Name', // Default label for the "Name" column
    balance: 'Balance', // Default label for the "Balance" column
    type: 'Type', // Default label for the "Type" column
    // Add more columns as needed
  });
  const [isFilterDropdownOpen, setFilterDropdownOpen] = useState(false);
  const [filterDropdownColumn, setFilterDropdownColumn] = useState('');
  const [typeOfArchive, setTypeOfArchive] = useState('');
  const [isRetrieveModalOpen, setRetrieveModalOpen] = useState(false);
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);

  useEffect(() => {
    // Make an API call to fetch PDL data
    getData();
    eraseFilters();
    setFilterDropdownOpen(false);
    setSelectMultipleEnabled(false);
  }, [activeTab]); // Empty dependency array means this effect runs once on component mount

  const getData = () => {
    axios.get(activeTab === 'pdlList' ? 
    `${domain}/index.php?action=getTableData` :
    `${domain}/index.php?action=getArchivedTableData`)
      .then(response => {
        setPdlData([]);
        setSortedPdlData([]);
        if (Array.isArray(response.data)) {
          setPdlData(response.data);
          setSortedPdlData(response.data); 
          setFilterOptions(prevFilterOptions => ({
            ...prevFilterOptions,
            name: response.data.map(pdl => pdl.pdl_lastname),
          }));
        } else {
          
        }
    })
    .catch(error => console.error('Error fetching PDL data:', error));
  }

  const filteredData = sortedPdlData.filter((pdl) => {
    const isNameMatch =
      pdl.pdl_lastname.toUpperCase().includes(selectedFilter.name.toUpperCase()) ||
      `${pdl.pdl_lastname.toUpperCase()}, ${pdl.pdl_firstname} ${pdl.pdl_middlename}`
        .toUpperCase()  
        .includes(selectedFilter.name.toUpperCase());

      const isBalanceMatch =
        selectedFilter.balance === '' ||
        (selectedFilter.balance === 'Highest' && parseFloat(pdl.balance) > 0) ||
        (selectedFilter.balance === 'Lowest' && parseFloat(pdl.balance) > 0) ||
        (selectedFilter.balance === 'No Balance' && parseFloat(pdl.balance) === 0);

        const isTypeMatch = selectedFilter.type === '' ||
        (parseInt(pdl.age) >= 58 && selectedFilter.type === 'Senior') ||
        (pdl.medical_condition !== 'None' && selectedFilter.type === 'PWD') ||
        (pdl.other_gender !== '' && selectedFilter.type === 'LGBT') ||
        (pdl.medical_condition === 'None' && pdl.other_gender === '' && parseInt(pdl.age) < 58 && selectedFilter.type === 'Regular');
      

    return isNameMatch && isBalanceMatch && isTypeMatch;
  });

  filteredData.sort((a, b) => {
    const balanceA = a.balance;
    const balanceB = b.balance;
  
    if (selectedFilter.balance === 'Highest') {
      return balanceB - balanceA;
    } else if (selectedFilter.balance === 'Lowest') {
      return balanceA - balanceB;
    }
  
    // For 'No Balance' or other cases, maintain the original order
    return 0;
  });

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };
  
  
  const openCreatePDLModal = () => {
    setCreatePDLModalOpen(true);
  };
  const closeCreatePDLModal = () => {
    setCreatePDLModalOpen(false);
    getData();
  };

  const [isEditPDLModalOpen, setEditPDLModalOpen] = useState(false);
  const openEditPDLModal = (id, e, primID) => {
    setEditPDLModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
    const newUrl = `?pdlNo=${id}`
    window.history.pushState({}, null, newUrl);
  };
  const closeEditPDLModal = () => {
    setEditPDLModalOpen(false);
    const newUrl = `?`
    window.history.pushState({}, null, newUrl);
    getData();
  };

  const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
  const openArchiveModal = (id, e, primID, type) => {
    setTypeOfArchive(type);
    
    if(type === 'Single'){
      setArchiveModalOpen(true);
      setSelectedPdlId(id);
      setPrimaryID(primID);
    }
    else{
      setArchiveModalOpen(true);
    }
    
    
  };
  const closeArchivePDLModal = (id) => {
    setArchiveModalOpen(false);
    getData();
    setSelectedRows([]);
    setSelectMultipleEnabled(false);
  };

  const openRetrieveModal = (primID, e, id) => {
    setRetrieveModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
  }

  const closeRetrieveModal = () => {
    setRetrieveModalOpen(false);
    getData();
  }
  
  const openDeleteModal = (primID, e, id) => {
    setDeleteModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
  }
  const closeDeleteModal = () => {
    setDeleteModalOpen(false);
    getData();
  }
  const [logo, setLogo] = useState(null);
  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setLogo(fileName);
      }
    )

   }, [])

  const selectMultiple = () => {
    setSelectMultipleEnabled((prevState) => !prevState);
    if(!selectMultipleEnabled){
      setSelectedRows([]);
      setSelectedFilterLabel(({
        name: 'Name', // Default label for the "Name" column
        balance: 'Balance', // Default label for the "Balance" column
        type: 'Type', // Default label for the "Type" column
        // Add more columns as needed
      }))
      setSelectedFilter({
        name: '',
        balance: '',
        type: '',
        // Add more columns as needed
      })
      ;
    }
  }

    const totalEntries = filteredData ? filteredData.length : 0;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    let entriesToShow = [];
    if (Array.isArray(filteredData)) {
      entriesToShow = filteredData.slice(
        (currentPage - 1) * entriesPerPage,
        currentPage * entriesPerPage
      );
  }

  const toggleFilterDropdown = (column) => {
    setFilterDropdownOpen(!isFilterDropdownOpen);
    setFilterDropdownColumn(column);
  };

  const handleFilterSelect = (option, column) => {
    // Implement logic to filter data based on the selected option
    // You can use this information to filter your data accordingly

    // Update the selected filter state for the specific column
    setSelectedFilter({
      ...selectedFilter,
      [column]: option,
    });

    // Update the label for the corresponding column
    switch (column) {
      case 'name':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          name: option,
        });
        break;
      case 'balance':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          balance: option,
        });
        break;
      case 'type':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          type: option,
        });
        break;
      // Add cases for other columns if needed
      default:
        break;
    }
    setFilterDropdownOpen(false);
  };


const renderFilterDropdown = (column) => {
    if (isFilterDropdownOpen && filterDropdownColumn === column) {
      return (
        <FilterDropdown
          options={filterOptions[column]}
          onSelect={(option) => handleFilterSelect(option, column)}
          type={column}
          typeOfLastName='pdl'
        />
      );
    }
    return null;
  };




  const generateReport = useReactToPrint({
    content: () => componentPDF.current,
    documentTitle: "PDL List",
  });/*= () => {
      if(pdlData){
        const action = "generatePDLReport";
        const actionData = { action };
        
        axios.post(domain, actionData)
            .then(response => {
                // Handle successful response, if needed
                 // Response.data will contain the file URL
                 const fileUrl = response.data.fileUrl;
                const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
                const newWindow = window.open(formattedFileUrl, '_blank');

                // Ensure the new window is fully loaded before attempting to print
                newWindow.onload = () => {
                    newWindow.print();
                };
            })
            .catch(error => {
                // Handle error, if needed
                console.error('Error generating report:', error);
            });
      }
  }*/
  const tabs = [
    { label: "PDL List", id: "pdlList" },
    { label: "Archived PDLs", id: "archivedItems" },
    // Add more tabs as needed
  ];
  const getCellStyleForType = (type) => {
    // Map type to corresponding CSS class
    switch (type) {
      case 'Regular':
        return dst.Regular;
      case 'LGBT':
        return dst.LGBT;
      case 'Senior':
        return dst.Senior;
      case 'PWD':
        return dst.PWD;
      default:
        return ''; // Use a default style if needed
    }
  };
  const eraseFilters = () => {
    setSelectedFilter({
      name: '',
      balance: '',
      type: '',
      // Add more columns as needed
    });
    setSelectedFilterLabel(({
      name: 'Name', // Default label for the "Name" column
      balance: 'Balance', // Default label for the "Balance" column
      type: 'Type', // Default label for the "Type" column
      // Add more columns as needed
    }));
  }
  return (
    <div className="px-5 pb-5">
      <div className='row' style={{}}>
        <div className={`col-5 d-flex justify-content-start ${dst.tabs}`}>
        {tabs.map((tab) => (
              <button
                key={tab.id}
                className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.label}
              </button>
            ))}
          </div>
        </div>
        <div className={`${dst.con} p-4`}>
          {activeTab === 'pdlList' ? (
          <div className={`${dst.row} mt-1`}>
            <div className='row d-flex align-items-center position-relative'>
                <div className='col-4 d-flex align-items-center mb-3'>
                <i className='fa-solid fa-person'></i>
                <h6 className='fw-bold fs-5 m-0 mx-2'>List of PDLs</h6>
                </div>
                <div className='col-8 d-flex justify-content-end'>
                <div className='mx-4 d-flex align-items-center' style={{fontSize: '12px'}}>
                    <div >
                      <div className='row d-flex'>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('Senior')}`}>
                          Senior
                        </div>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('LGBT')}`}>
                          LGBT
                        </div>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('PWD')}`}>
                          PWD
                        </div>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('Regular')}`}>
                          Regular
                        </div>
                      </div>
                    </div>
                  </div>
                {!selectMultipleEnabled ? (<><button className={`${dst.tableBtn} me-2`} 
                    style={{left: '0'}}type='button' onClick={generateReport}>
                  <i className={`fa-regular fa-file ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Generate Report</span>
                  </button>
                  <button className={`${dst.tableBtn} me-2`} style={{left: '0'}}type='button' onClick={openCreatePDLModal}>
                  <i className={`fa-regular fa-plus ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Add PDL</span>
                  </button></>) : selectMultipleEnabled && selectedRows.length > 0 ?
                  (<><button className={`${dst.tableBtn} me-2`} 
                  style={{left: '0'}}type='button' 
                  onClick={(e) => openArchiveModal(null, e, null, 'Batch')}>
                  <i className={`fa-solid fa-box-archive ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Archive</span>
                  </button></>) : null
                }
                {
                selectedFilterLabel.name !== 'Name' || 
                selectedFilterLabel.balance !== 'Balance' ||
                selectedFilterLabel.type !== 'Type' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
                <button className={`${dst.tableBtn} me-2`} style={{left: '0', 
                backgroundColor: selectMultipleEnabled ? '#0099ff' : '',
                color: selectMultipleEnabled ? '#fff' : '',
                border: selectMultipleEnabled ? 'none' : ''
                }}type='button' onClick={selectMultiple}>
                <i className={`fa-solid fa-clipboard-check ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Select Multiple</span>
                </button>
              </div>
              <div className={`col-12 mx-auto`} >
              <table
                  className={`${dst.cleanTable} ${dst.selectableTable}  w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    fontSize: 'small',
                    overflow: 'auto',
                    fontSize: '14px',
                    border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                  }}
                >
                <thead>
                  <tr style={{fontSize: '16px'}}>
                    {selectMultipleEnabled && (<th scope="col">
                      Select.
                    </th>)}
                    <th scope="col">
                      PDL No.
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.name}
                      {!selectMultipleEnabled &&(<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('name')}></i>
                      {renderFilterDropdown('name')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.balance}
                      {!selectMultipleEnabled && (<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('balance')}></i>
                      {renderFilterDropdown('balance')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.type}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('type')}></i>
                      {renderFilterDropdown('type')}</>)}
                    </th>
                    {!selectMultipleEnabled && (<th scope="col" className={dst.exclude} >
                      Actions
                    </th>)}
                  </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow.length != 0 ? (<>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const pdl = entriesToShow[index];
                    const hasData = pdl !== undefined;
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    const isSelected = selectedRows.includes(parseInt(pdl?.pdl_id));
                    const handleRowClick = () => {
                      if (selectMultipleEnabled) {
                        
                        if (hasData) {
                          const pdlId = parseInt(pdl.pdl_id); // Get the PDL ID
                          if (isSelected) {
                            setSelectedRows(selectedRows.filter((selectedId) => selectedId !== pdlId));
                            
                          } else {
                            setSelectedRows([...selectedRows, pdlId]);
                          }
                          
                        }
                      } else {
                        // If selectMultipleEnabled is false, clear the selected rows
                        setSelectedRows([]);
                      }
                    };
                    return (
                      <tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                      ${isSelected && selectMultipleEnabled ? dst.selectedRow : ''}`}
                      onClick={handleRowClick}
                      style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                      >
                        {selectMultipleEnabled && (<td>{hasData ? 
                        (<i className={`fa-regular ${isSelected ? 'fa-square-check' : 'fa-square'} fs-3 text-center`}>

                        </i>) : ''}</td>)}
                        <td>{hasData ? `PDL-${pdl.id}` : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            <>
                              {pdl.pdl_lastname && pdl.pdl_lastname.toUpperCase()}, {pdl.pdl_firstname} {pdl.pdl_middlename}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>{hasData ? (pdl.formatted_balance != 0 ? `PHP ${pdl.formatted_balance}` : 'No Balance') : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                          <div className='d-flex align-items-center justify-content-center'>
                            {parseInt(pdl.age) >= 58 && (
                            <div className={`${dst.typeCell} ${getCellStyleForType('Senior')}`}>
                              
                            </div>
                            )
                            }
                            {pdl.medical_condition != 'None' && (
                            <div className={`${dst.typeCell} ${getCellStyleForType('PWD')}`}>
                              
                            </div>
                            )
                            }
                            {pdl.other_gender != '' && (
                            <div className={`${dst.typeCell} ${getCellStyleForType('LGBT')}`}>
                              
                            </div>
                            )
                            }
                            {pdl.medical_condition === 'None' &&
                            pdl.other_gender === '' &&
                            parseInt(pdl.age) < 58 ? (
                            <div className={`${dst.typeCell} ${getCellStyleForType('Regular')}`}>
                              
                              </div>
                            ) : ''
                            }
                            
                          </div>
                          
                          ) : ''}</td>
                        {!selectMultipleEnabled && (<td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons} ${dst.exclude}`}>
                              <Link exact to={`/dashboard/pdlprofile?pdlId=${pdl.pdl_id}`}>
                                <p>
                                  <i className={`fa-solid fa-eye fa-sm ${dst.iconHover}`}></i>
                                  <span className={dst.iconTooltip}>View</span>
                                </p>
                              </Link>
                              <p onClick={(e) => openEditPDLModal(pdl.id, e, pdl.pdl_id)}>
                                <i className={`fa-solid fa-pen-to-square fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Edit</span>
                              </p>
                              <p onClick={(e) => openArchiveModal(pdl.id, e, pdl.pdl_id, 'Single')}>
                                <i className={`fa-solid fa-box-archive fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Archive</span>
                              </p>
                            </div>
                          )}
                        </td>)}
                      </tr>
                    );
                  })}</>) : (
                  <tr>
                    <td style={{ padding: '150px', textAlign: 'center' }} colSpan={6}>
                      Nothing to show
                    </td>
                  </tr>)}
                </tbody>
                </table>
                <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '1%', marginBottom: '5px'}}>
                  <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
                </div>
              </div>
            </div>
          </div>

          ) : (<div className={`${dst.row} mt-1`}>
          <div className='row d-flex align-items-center position-relative'>
              <div className='col-4 d-flex align-items-center mb-3'>
              <i className='fa-solid fa-person'></i>
              <h6 className='fw-bold fs-5 m-0 mx-2'>Archived PDLs</h6>
              </div>
              <div className='col-8 d-flex justify-content-end'>
              {
                selectedFilterLabel.name !== 'Name' || 
                selectedFilterLabel.balance !== 'Balance' ||
                selectedFilterLabel.type !== 'Type' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
            </div>
            <div className='col-12 mx-auto'>
            <table
                className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                style={{
                  borderCollapse: 'collapse',
                  color: '#48505E',
                  fontSize: 'small',
                  overflow: 'auto',
                  fontSize: '14px',
                  border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                }}
              >
              <thead>
                <tr style={{fontSize: '16px'}}>
                  {selectMultipleEnabled && (<th scope="col">
                    Select.
                  </th>)}
                  <th scope="col">
                    PDL No.
                  </th>
                  <th scope="col" >
                    {selectedFilterLabel.name}
                    {!selectMultipleEnabled &&(<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                    onClick={() => toggleFilterDropdown('name')}></i>
                    {renderFilterDropdown('name')}</>)}
                  </th>
                  <th scope="col" >
                  {selectedFilterLabel.balance}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                    onClick={() => toggleFilterDropdown('balance')}></i>
                    {renderFilterDropdown('balance')}</>)}
                  </th>
                  <th scope="col" >
                  {selectedFilterLabel.type}
                  {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                    onClick={() => toggleFilterDropdown('type')}></i>
                    {renderFilterDropdown('type')}</>)}
                  </th>
                  <th scope="col">
                    Date Archived
                  </th>
                  <th scope='col'>
                    Action
                  </th>
                </tr>
              </thead>
              <tbody className={dst.shrinkMedia}>
              {entriesToShow != 0 ? (<>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const pdl = entriesToShow[index];
                    const hasData = pdl !== undefined;
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    const isSelected = selectedRows.includes(parseInt(pdl?.pdl_id));
                    const handleRowClick = () => {
                      if (selectMultipleEnabled) {
                        
                        if (hasData) {
                          const pdlId = parseInt(pdl.pdl_id); // Get the PDL ID
                          if (isSelected) {
                            setSelectedRows(selectedRows.filter((selectedId) => selectedId !== pdlId));
                            
                          } else {
                            setSelectedRows([...selectedRows, pdlId]);
                          }
                          
                        }
                      } else {
                        // If selectMultipleEnabled is false, clear the selected rows
                        setSelectedRows([]);
                      }
                    };
                  return (
                    <tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                    ${isSelected && selectMultipleEnabled ? dst.selectedRow : ''}`}
                    onClick={handleRowClick}
                    style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                    >
                      {selectMultipleEnabled && (<td>{hasData ? 
                      (<i className={`fa-regular ${isSelected ? 'fa-square-check' : 'fa-square'} fs-3 text-center`}>

                      </i>) : ''}</td>)}
                      <td>{hasData ? `PDL-${pdl.id}` : ''}</td>
                      <td style={cellStyle}>
                        {hasData ? (
                          <>
                            {pdl.pdl_lastname && pdl.pdl_lastname.toUpperCase()}, {pdl.pdl_firstname} {pdl.pdl_middlename}
                          </>
                        ) : (
                          ''
                        )}
                      </td >
                      <td style={cellStyle}>{hasData ? (pdl.formatted_balance != 0 ? `PHP ${pdl.formatted_balance}` : 'No Balance') : ''}</td>
                      <td style={cellStyle}>
                          {hasData ? (
                          <div className='d-flex'>
                            {parseInt(pdl.age) >= 58 && (
                            <div className={`${dst.typeCell} ${getCellStyleForType('Senior')}`}>
                              Senior
                            </div>
                            )
                            }
                            {pdl.medical_condition != 'None' && (
                            <div className={`${dst.typeCell} ${getCellStyleForType('PWD')}`}>
                              PWD
                            </div>
                            )
                            }
                            {pdl.other_gender != '' && (
                            <div className={`${dst.typeCell} ${getCellStyleForType('LGBT')}`}>
                              LGBT
                            </div>
                            )
                            }
                            {pdl.medical_condition === 'None' &&
                            pdl.other_gender === '' &&
                            parseInt(pdl.age) < 58 ? (
                            <div className={`${dst.typeCell} ${getCellStyleForType('Regular')}`}>
                              Regular
                              </div>
                            ) : ''
                            }
                            
                          </div>
                          
                          ) : ''}</td>
                      <td>{hasData ? pdl.date_archived : ''}</td>
                     <td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons}`}>
                              <p onClick={(e) => openRetrieveModal(pdl.pdl_id, e, pdl.id)}>
                                <i className={`fa-solid fa-rotate-left fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Retrieve</span>
                              </p>
                              <p onClick={(e) => openDeleteModal(pdl.pdl_id, e, pdl.id)}>
                                <i className={`fa-solid fa-trash fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Delete</span>
                              </p>
                            </div>
                          )}
                        </td>
                    </tr>
                  );
                })}</>) : (<tr>
                  <td style={{ padding: '150px', textAlign: 'center' }} colSpan={6}>
                    Nothing to show
                  </td>
                </tr>)}

              </tbody>
              </table>
              <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '1%', marginBottom: '5px'}}>
                <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
              </div>
            </div>
          </div>
        </div>
          )}
        </div>
        {isCreatePDLModalOpen && <CreatePDL onClose={closeCreatePDLModal} isVisible={isCreatePDLModalOpen}/>}
        {isEditPDLModalOpen && <EditPDL onClose={closeEditPDLModal} pdlId={selectedPdlId} primaryID={primaryID}/>}
        {isArchiveModalOpen && <ConfirmArchive 
          onClose={closeArchivePDLModal} 
          primaryPDLID={typeOfArchive === 'Single' ? primaryID : null} 
          archiveType="PDL" 
          archiveCount={typeOfArchive}
          rowsToArchivePDL={typeOfArchive !== 'Single' ? selectedRows : null}/>}
          {isRetrieveModalOpen && (
          <RetrieveModal
          onClose={closeRetrieveModal}
          primaryPDLID={primaryID} 
          pdlID={selectedPdlId}
          retrieveType="PDL"
          style={{
            // Apply your conditional styles here
            opacity: isRetrieveModalOpen ? 1 : 0,
            transition: 'opacity 0.5s ease-in-out', // Add transition for a fading effect
          }}
        />
      )} 
      {isDeleteModalOpen && <DeleteModal primaryPDLID={primaryID}
       onClose={closeDeleteModal}/>}
       <div ref={componentPDF} style={{position: 'absolute', width: '100%', zIndex: '-99'}}>
          <div className='m-5'>
            <div className='row d-flex justify-content-start align-items-center'>
              <div className='col-1 justify-content-start'>
              <img className={dst.logo} src={`../logo/${logo}`} alt="BJMP Logo" 
              style={{width: '80px', height: '80px'}}/>
              </div>
              <div className='col-11 text-start'>
                <div className='mx-4'>
                  <h5 className='fw-bold'>Bureau of Jail Management and Penology Region III</h5>
                  <h6 className='fw-bold'>List of PDLs</h6>
                  <p>Date Today: {formattedDate} {formattedTime}</p>
                </div>
                
              </div>
              
            </div>
          
          <table
              className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                style={{
                  borderCollapse: 'collapse',
                  color: '#48505E',
                  fontSize: 'small',
                  overflow: 'auto',
                  fontSize: '14px',
                  border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                }}
              >
              <thead>
                <tr style={{fontSize: '16px'}}>
                  <th scope="col">
                    PDL No.
                  </th>
                  <th scope="col" >
                    {selectedFilterLabel.name}
                  </th>
                  <th scope="col" >
                  {selectedFilterLabel.balance}
                  </th>
                  <th scope="col" >
                  {selectedFilterLabel.type}
                  </th>
                </tr>
              </thead>
              <tbody className={dst.shrinkMedia}>
              {entriesToShow.map((pdl, index) => (
                  <tr
                    key={index}
                    className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}`}
                    
                    style={{ cursor: selectMultipleEnabled && pdl ? 'pointer' : 'inherit' }}
                  >
                    {selectMultipleEnabled && (
                      <td>
                        {pdl ? (
                          <i className={`fa-regular  fs-3 text-center`}></i>
                        ) : (
                          ''
                        )}
                      </td>
                    )}
                    <td>{pdl ? `PDL-${pdl.id}` : ''}</td>
                    <td>
                      {pdl ? (
                        <>
                          {pdl.pdl_lastname && pdl.pdl_lastname.toUpperCase()}, {pdl.pdl_firstname} {pdl.pdl_middlename}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {pdl ? (pdl.formatted_balance != 0 ? `PHP ${pdl.formatted_balance}` : 'No Balance') : ''}
                    </td>
                    <td>
                      {pdl ? (
                        <div className='d-flex'>
                          {parseInt(pdl.age) >= 58 && (
                            <div>Senior</div>
                          )}
                          {pdl.medical_condition !== 'None' && (
                            <div>PWD</div>
                          )}
                          {pdl.other_gender !== '' && (
                            <div>LGBT</div>
                          )}
                          {pdl.medical_condition === 'None' && pdl.other_gender === '' && parseInt(pdl.age) < 58 ? (
                            <div>Regular</div>
                          ) : (
                            ''
                          )}
                        </div>
                      ) : (
                        ''
                      )}
                    </td>
                  </tr>
                ))}

              </tbody>
              </table>
          </div>
       
       </div>
       
       </div>
  );
}

export default PDLList;
