import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';
import ItemContainer from './subsections/ItemContainer';
import PageNavigator from '../navbars/PageNavigator';
import Success from '../modals/SuccessfulAction';

export default function ItemSelection({getData, 
  posElements, getStockData, itemData}) {

    const [activeQtyContainer, setActiveQtyContainer] = useState(null);
    const [searchInput, setSearchInput] = useState('');
    const [selectedCategory, setSelectedCategory] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const entriesPerPage = 60;
    const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);

    // Calculate the number of empty entries to display
    const emptyEntriesCount = Math.max(entriesPerPage - itemData.length, 0);

    const handleQtyContainerClick = (index) => {
        if (index !== activeQtyContainer) {
          setActiveQtyContainer(index);
        }
      };
      const filteredData = itemData
      .filter((item) =>
        `${item.prodType} - ${item.prodName} - ${item.stock_id} - ${item.id}`
          .toLowerCase()
          .includes(searchInput.toLowerCase())
      )
      .filter((item) =>
        selectedCategory ? item.category === selectedCategory : true
      );
    const uniqueCategories = [...new Set(itemData.map(item => item.category))];
    const totalPages = Math.ceil(filteredData.length / entriesPerPage);

    const startIndex = (currentPage - 1) * entriesPerPage;
    const endIndex = startIndex + entriesPerPage;
  
    const paginatedData = filteredData.slice(startIndex, endIndex);
  
    const handlePageChange = (newPage) => {
      setCurrentPage(newPage);
    };

      const openSuccessModal = () => {
          setSuccessModalOpen(true);
      };
      const closeSuccessModal = () => {
        setSuccessModalOpen(false);
        getData();
        getStockData();
      };

    return (
      <div className='px-5'>
        <div className={`${dst.con} p-4 mt-4 mb-4`}>
          <div className='row d-flex align-items-center position-relative'>
            <div className='col-6 d-flex align-items-center mb-3'>
            <i className="fa-solid fa-cash-register"></i>
              <h6 className='fw-bold fs-5 m-0 mx-2'>Point of Sales</h6>
            </div>
            <div className='col-6 d-flex align-items-center mb-3'>
              <select
              className={dst.sort}
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
            >
              <option value="">All Categories</option>
              {uniqueCategories.map((category) => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>
              <i className="fa-solid fa-magnifying-glass fa-sm mx-2" 
              style={{ color: '#858D9D' }}
                ></i>
                          <input
                    className={dst.search}
                    placeholder="Search..."
                    value={searchInput}
                    onChange={(e) => setSearchInput(e.target.value)}
                    />
            </div>
          </div>
          <div className='row mx-2 d-flex align-items-center position-relative'>
            <div className={dst.itemListContainer}>
              <div className='row'>
                {/* Display items */}
                {filteredData.map((item) => (
                  <div key={`${item.prodType} - ${item.prodName} 
                  - ${item.stock_id} - ${item.id} - ${item.category}`} className='col-2 mt-2'>
                    <ItemContainer item={item} 
                    index={item.stock_id}
                    qtyState={activeQtyContainer === item.stock_id}
                    onQtyContainerClick={handleQtyContainerClick}
                    openSuccessModal={openSuccessModal}
                    posElements={posElements}/>
                  </div>
                ))}
                {/* Display empty entries */}
                {Array.from({ length: emptyEntriesCount }).map((_, index) => (
                  <div key={`empty-${index}`} className='col-2 mt-2'>
                    <div className={dst.emptyEntryIndicator}></div>
                  </div>
                ))}
              </div>
              <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '1%', marginBottom: '5px'}}>
              <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
                </div>
            </div>
          </div>
        </div>
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
      </div>
    );
  }
