import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';
import DeleteModal from '../modals/DeleteModal';
import PageNavigator from '../navbars/PageNavigator';
import PurchaseDetails from '../modals/PurchaseDetails';

export default function SecondSidebar({
  toggleCheckoutSize, toggleCheckOut, posElements, getData,
getStockData}) {
    const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);
    const [primaryID, setPrimaryID] = useState(null);
    const [isPurchaseModalOpen, setPurchaseModalOpen] = useState(false);
    const [priceVal, setPriceVal] = useState(0.00);
      const [changeQtyState, setChangeQtyState] = useState({
        onState: false,
        id: null,
        remainingStock: null
      }
    )
    const [quantity, setQuantity] = useState(0);
    const [errorMessage, setErrorMessage] = useState("");
    const handleChange = (e) => {
      const name = e.target.name;
      const value = parseInt(e.target.value, 10);

      // Check if the value is within the desired range
      if (value >= 1 && value <= parseInt(changeQtyState.remainingStock)) {
        setQuantity(value);
      }
    };
   
    const totalSum = posElements.reduce((sum, item) => {
      const price = parseFloat(item.formatted_price);
      const quantity = parseInt(item.quantity, 10);
  
      if (!isNaN(price) && !isNaN(quantity)) {
        sum += price * quantity;
        
      }
  
      return sum;
    }, 0);

    useEffect(() => {
      setPriceVal(totalSum.toFixed(2));
    }, [totalSum]);

    const setChangeQuantity = (item, e, state) => {
      if (state) {
        const newUrl = `?checkoutItem=${item}`;
        axios.get(`${domain}/index.php?action=getSpecificPOSElement&email=${localStorage.getItem('email')}&itemID=${item}`)
          .then(response => {
            if (response.data) {
              setChangeQtyState({
                id: response.data[0].item_id,
                remainingStock: response.data[0].remaining_stocks
              });
              setQuantity(parseInt(response.data[0].quantity));
            }
          });
        window.history.pushState({}, null, newUrl);
      } else {
        setChangeQtyState({ id: null, remainingStock: null });
        const newUrl = `?`;
        window.history.pushState({}, null, newUrl);
      }
      console.log(changeQtyState);
    };
    

    const changeQuantityData = (item, e) => {
      const changeQty = {
        quantity: quantity,
        action: 'updateQuantity',
        email: localStorage.getItem('email'),
        itemID: item
      }
  
      axios.post(domain, changeQty).then(response => {
              axios.get(`${domain}/index.php?action=getSpecificPOSElement&email=${localStorage.getItem('email')}&itemID=${item}`)
              .then(response => {
                if(response.data){
                    setChangeQtyState({onState: false, id: null, remainingStock: null});
                    getData();
                }
              })
            })
            .catch(error => {
              console.error('Error adding quantity:', error);
            });
    }
    const openDeleteModal = (primID, e) => {
      setDeleteModalOpen(true);
      setPrimaryID(primID);
    }
    const closeDeleteModal = () => {
      setDeleteModalOpen(false);
      getData();
    }
    const itemsPerPage = 10; // Number of items to display per page
    const [currentPage, setCurrentPage] = useState(1);
  
    const handlePageChange = (pageNumber) => {
      setCurrentPage(pageNumber);
    };
  
    const paginatedData = posElements.slice(
      (currentPage - 1) * itemsPerPage,
      currentPage * itemsPerPage
    );
    
    const goToCheckout = () => {
      if(paginatedData.length <= 0){
          setErrorMessage('Please add something in the checkout menu');
      }
      else{
        setErrorMessage('');
        setPurchaseModalOpen(true);
      }
    }

    const closePurchaseModal = () => {
      setPurchaseModalOpen(false);
      getData();
      getStockData();
    }
    return (
      <div className={`${dst.checkoutList} ${toggleCheckOut ? dst.CheckOpen : ''}`}>
        <div className={dst.container}>
        <div className='row d-flex align-items-center position-relative'>
              <div className='col-4 d-flex align-items-center mb-1'>
                <i className='fa-solid fa-receipt'></i>
                <h6 className='fw-bold fs-5 m-0 mx-2'>Checkout List</h6>
              </div>
              <div className='col-8 d-flex align-items-center justify-content-end'>
                <button
                  onClick={toggleCheckoutSize}
                  className={`${dst.collapseButton} ${dst.iconHover}`}
                  style={{ left: '-415px' }}
                >
                  {!toggleCheckOut ? (
                    <i className='fa-solid fa-chevron-right'></i>
                  ) : (
                    <i className='fa-solid fa-chevron-left'></i>
                  )}
                </button>
                <span className={dst.iconTooltip} 
                style={{ transform: 'translateX(-425px) translateY(40px)' }}>
                  {toggleCheckOut ? 'Open' : 'Close'} Checkout Menu
                </span>
              </div>
            </div>
            <div style={{ transform: 'translateY(-35px)' }}>
                <div className='row' >
                <table
                  className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    overflow: 'auto',
                    fontSize: '12px',
                  }}
                >
                <thead>
                  <tr style={{fontSize: '12px'}}>
                    <th scope="col">
                      ID
                    </th>
                    <th scope="col">
                      Product
                    </th>
                    <th scope="col">
                      Price
                    </th>
                    <th scope="col">
                      Quantity
                    </th>
                    <th scope="col">
                      Actions
                    </th>
                    </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {Array.from({ length: 10 || 1 }, (_, index) => {
                    const s = paginatedData[index];
                    const hasData = s !== undefined;
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    return(
                    <tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                    `}>
                      <td>{hasData ? `I-${s.item_id}` : ''}</td>
                      <td style={cellStyle}>
                          {hasData ? (
                            <>
                              {s.prodType} - {s.prodName}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>
                          {hasData ? (
                            <>
                              PHP{s.formatted_price}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>
                        {hasData ? (
                              <>
                                {hasData ? (
                                    <>
                                      {!changeQtyState.id || changeQtyState.id !== s.item_id ? (
                                        <>{s.quantity}</>
                                      ) : (
                                        <>
                                          <input
                                            type="number"
                                            name="quantity"
                                            id="quantity"
                                            onChange={handleChange}
                                            style={{
                                              width: '60px',
                                              height: '15px',
                                              appearance: 'textfield',
                                              '-moz-appearance': 'textfield'
                                            }}
                                            value={quantity}
                                            min="0"
                                          />
                                        </>
                                      )}
                                    </>
                                  ) : (
                                    ''
                                  )}
                              </>                                  
                            ) : (
                            ''
                          )}
                        </td >
                        <td>
                        {hasData ? (
                            !changeQtyState.id || changeQtyState.id !== s.item_id ? (
                              
                              <div className={`${dst.actionButtons} ${dst.exclude}`}>
                                
                              <p onClick={(e) => setChangeQuantity(s.item_id, e, true)}>
                                <i className={`fa-solid fa-pen-to-square fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Edit</span>
                              </p>
                              <p onClick={(e) => openDeleteModal(s.temp_id, e)}>
                                <i className={`fa-solid fa-trash fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Remove</span>
                              </p>
                            </div>
                            ) : (<div className={`${dst.actionButtons}`}>
                              
                            <p onClick={(e) => changeQuantityData(s.item_id, e)}>
                              <i className={`fa-solid fa-check fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Confirm</span>
                            </p>
                            <p onClick={(e) => setChangeQuantity(s.item_id, e, false)}>
                            <i className={`fa-solid fa-x fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Discard</span>
                            </p>
                          </div>)
                          ) : (
                            ''
                          )}
                        </td>
                    </tr>);

                })}
                </tbody>
                </table>
                <PageNavigator
                    totalPages={Math.ceil(posElements.length / itemsPerPage)}
                    currentPage={currentPage}
                    onPageChange={handlePageChange}
                  />
                </div>
                
                <hr></hr>
                
                <div className='row d-flex align-items-center'>
                  <div className='col-6 d-flex justify-content-start'>
                    Total: PHP {totalSum.toFixed(2)}
                  </div>
                  <div className='col-6 d-flex justify-content-end'>
                    <button
                     onClick={goToCheckout}
                     className={`${dst.add}`}>Check Out</button>
                  </div>
                </div>
                <p className='mt-2' style={{color: 'red', fontSize: '12px', margin: '0'}}>{errorMessage}</p>
            </div>
            
        </div>
        {isDeleteModalOpen && <DeleteModal primaryPDLID={primaryID}
       onClose={closeDeleteModal} posPrimaryID={primaryID}/>}
       {isPurchaseModalOpen && <PurchaseDetails onClose={closePurchaseModal} 
       priceVal={priceVal} posElements={posElements}  />}
      </div>
    );
  }
