import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import RegistrationForm from '../components/auth/RegistrationForm';
import LoginForm from '../components/auth/LoginForm';
import Dashboard from '../pages/Dashboard';
import PDLList from '../pages/PDLList';
import PDLProfile from '../pages/PDLProfile';
import Inventory from '../pages/Inventory';
import LoadPDL from '../pages/LoadPDL';
import PointOfSales from '../pages/PointOfSale';
import AuditLog from '../pages/AuditLog';
import UserList from '../pages/UserList';
import ForgotPassword from '../components/auth/ForgotPassword';
import ResetPassword from '../components/auth/ResetPassword';
import { useState, useMemo } from 'react';

const useSidebar = () => {
    const [isSidebarOpen, setIsSidebarOpen] = useState(true);
    const toggleSidebar = () => {
      setIsSidebarOpen(!isSidebarOpen);
    };
    return { isSidebarOpen, toggleSidebar };
  };
  
const RoutesWithSidebar = () => {
    const { isSidebarOpen, toggleSidebar } = useSidebar();

    const routes = useMemo( () => [
        {
            name: 'Login',
            path: '/',
            component: <LoginForm />
        },
        {
            name: 'Login',
            path: '/login',
            component: <LoginForm />
        },
        {
            path: '/register',
            component: <RegistrationForm  />
        },
        {
            path: '/forgotpassword',
            component: <ForgotPassword />
        },
        {
            path: '/resetpassword',
            component: <ResetPassword />
        },
        {
            name: 'Dashboard',
            path: '/dashboard',
            component: <Dashboard isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar} />
        },
        {
            name: 'PDL List',
            path: '/dashboard/pdls',
            component: <PDLList isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        },
        {
            name: 'Inventory',
            path: '/dashboard/inventory',
            component: <Inventory isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        },
        {
            name: 'Load PDL',
            path: '/dashboard/loadpdl',
            component: <LoadPDL isSidebarOpen={isSidebarOpen}  toggleSidebar={toggleSidebar}/>
        },
        {
            name: 'Point-of-Sales',
            path: '/dashboard/pointofsales',
            component: <PointOfSales isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        },
        {
            name: 'Audit Trail',
            path: '/dashboard/audit',
            component: <AuditLog isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        },
        {
            name: 'PDL Profile',
            path: '/dashboard/pdlprofile',
            component: <PDLProfile isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        },
        {
            name: 'User List',
            path: '/dashboard/users',
            component: <UserList isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        },
    ])

    return routes.map((route) => (
            <Routes>
                <Route key={route.name} path={route.path} element={route.component} />
            </Routes>
      ));
}



export default RoutesWithSidebar;