import React, { useState, useRef,useEffect  } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import Fingerprint from './FingerprintIDScan';
import axios from 'axios';
import domain from '../../constants/domain';
import {v4 as uuidv4} from 'uuid';

export default function EditUser({ onClose, userEmail }) {
  const [formData, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState("");
  const [imageSrc, setImageSrc] = useState(null);
  const [tempImgSrc, setTempImageSrc] = useState(null);
  const fileInputRef = useRef(null);
  const [selectedUserType, setSelectedUserType] = useState('');
  const [optionDisplayForUser, setOptionDisplayForUser] = useState([]);
  const [selectBranchState, setSelectBranchState] = useState(optionDisplayForUser.length != 0 ? false : true);
  const [selectBranch, setSelectBranch] = useState("");
  const action = 'updateUserInfo';
  useEffect(() => {
      axios.get(`${domain}/index.php?action=getLoggedUserData&email=${userEmail}`)
      .then(
        response => {
          if(response.data){
            setFormData(response.data[0]);
            const imageUrl = response.data[0].valid_ID;
            const fileName = imageUrl.split('/').pop();
            setTempImageSrc(fileName);
          }
        }
      )
      .catch(error => console.error("Error getting user data: ", error));
      axios.get(`${domain}/index.php?action=getUserList`)
      .then(response => {
        if (!response.data.fail) {
          // Update the state with the fetched PDL values
          setOptionDisplayForUser(response.data);
          setSelectBranch(response.data.bjmp_branch);
        }
      })
      .catch(error => console.error('Error fetching PDL values:', error));
  }, [])

  const handleSvgClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click(); // Trigger click on the file input
    }
  };
  
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;
    if (e.target.type === 'file') {
      const reader = new FileReader();
      const file = e.target.files[0];
      reader.onload = (e) => {
        setImageSrc(e.target.result);
        setTempImageSrc(null);
        setFormData(values => ({ ...values, action, validID: file.name, valid_ID: value })); // Use e.target.files[0]
      };
      if (value) {
        reader.readAsDataURL(value);
    
      }
      return;
    }
    if(name === 'userType'){
      setSelectedUserType(value);
      setFormData(values => ({action, ...values, userType: value}));
    }
    if(name === 'bjmp_branch'){
      setSelectBranch(value);
      setFormData(values => ({action, ...values, branch: value}));
    }
    setFormData(values => ({action, ...values, [name]: value}));
    console.log(formData);
};
  
const handleSubmit = async (e) => {
  e.preventDefault();
  const { emailAdd, password, userType, firstName, middleName, lastName, contact_number, validID, valid_ID, complete_address, bjmp_branch, position } = formData;
  const updatedFormData = {
    ...formData,
    action: action, // or whatever action you want
  };
  const fileUp = new FormData();
  fileUp.append('valid_ID', valid_ID);
  fileUp.append('email', emailAdd);
  fileUp.append('action', 'uploadUserImage');

  if (!emailAdd || !password || !userType || !firstName || !middleName || !lastName || !contact_number || !valid_ID || !complete_address || !bjmp_branch || !position) {
    setErrorMessage('Please fill in all required fields.');
    return; // Do not proceed to the next step
  }
  else{
        setErrorMessage('');
        setErrorMessage('');
        axios.post(`${domain}/index.php?action=updateUserInfo&entry=${formData.userid}`, updatedFormData)
        .then(response => {
          if(response.data){
            axios.post(`${domain}/index.php?action=updateUserImage&entry=${formData.userid}`, fileUp)
            .then(respo => {
                if(respo.data){
                  onClose();
                }
            }).catch(error => console.error("Error: ", error));
          }
        }).catch(error => console.error('Error: ', error));
      }

};
 
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'addPdl') {
      onClose(); // Close the modal
    }
  };

  const setAddNewBranchType = () => {
    setSelectBranchState((prevState) => !prevState);
  }


  const rectWidth = imageSrc || tempImgSrc ? 162 : 502; // Adjust width based on imageSrc
  const rectHeight = imageSrc || tempImgSrc ? 162 : 162; // Adjust height based on imageSrc
  const preDeterminedOptions = [
    {
      label: 'Main Regional Office',
      facilities: ['BJMPRO-III Main Office']
    },
    {
      label: 'Provincial Jail of Bataan',
      facilities: [
        'Bataan District Jail Male Dormitory',
        'Bataan District Jail Female Dormitory',
        'Balanga City Jail',
        'Mariveles Municipal Jail',
        'Dinalupihan Municipal Jail',
      ]
    },
    {
      label: 'Provincial Jail of Bulacan',
      facilities: [
        'Balagtas District Jail',
        'Baliwag City Jail',
        'Malolos City Jail',
        'Marilao Municipal Jail',
        'Meycauayan City Jail',
        'Plaridel Municipal Jail Male Dorm',
        'Plaridel Municipal Jail Female Dorm',
        'Pulilan Municipal Jail',
        'San Jose Del Monte City Jail Male Dorm',
        'San Jose Del Monte City Jail Female Dorm',
        'Sta. Maria Municipal Jail',
        'Bocaue Municipal Jail',
        'Guiguinto Municipal Jail',
      ]
    },
    {
      label: 'Provincial Jail of Nueva Ecija',
      facilities: [
        'Cabanatuan City District Jail Male Dormitory',
        'Cabanatuan City District Jail Female Dormitory',
        'Gapan City Jail',
        'Guimba District Jail',
        'San Jose City District Jail',
        'Talavera Municipal Jail',
      ]
    },
    {
      label: 'Provincial Jail of Pampanga',
      facilities: [
        'Angeles District Jail Male Dormitory',
        'Angeles District Jail Female Dormitory',
        'Arayat District Jail',
        'Guagua District Jail Male Dorm',
        'Guagua District Jail Female Dormitory',
        'San Fernando District Jail Male Dormitory',
        'San Fernando District Jail Female Dormitory',
      ]
    },
    {
      label: 'Provincial Jail of Tarlac',
      facilities: [
        'Paniqui Municipal Jail',
        'Tarlac City Jail Male Dorm',
        'Tarlac City Jail Female Dorm',
        'Camiling Municipal Jail',
      ]
    },
    {
      label: 'Provincial Jail of Zambales',
      facilities: [
        'Olongapo District Jail Male Dormitory',
        'Olongapo District Jail Female Dormitory',
      ]
    },
    {
      label: 'Provincial Jail of Aurora',
      facilities: [
        'Baler Municipal Jail',
      ]
    },

  ];
  return (
<div className={dst.modal} onClick={handleBackdropClick} id="addPdl" tabIndex="-1">
  <div className={`${dst['modal-content']}`} style={{ width: '650px' }}>
    <div className={`${dst['modal-header']}`}>
      <div className='row w-100'>
        <div className='col-10 d-flex align-items-center'>
          <i className='fa-solid fa-pen'></i>
          <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Edit User Entry</h6>
        </div>
        <div className='col-2 d-flex justify-content-end align-items-center'>
          <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
        </div>
      </div>
    </div>
    <div className={dst['modal-body']}>
      <div className="row d-flex justify-content-center" style={{ display: 'flex' }}>
        <div className="col-12 mb-4" >
        <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {tempImgSrc && (
                          <image
                            x="20%"
                            y="20%"
                            xlinkHref={`../images/users/${tempImgSrc}`}
                            width="100"
                            height="100"
                          />
                        
                      )}
                      {imageSrc && (
                          <image
                            x="20%"
                            y="20%"
                            xlinkHref={imageSrc}
                            width="100"
                            height="100"
                          />
                      )}
                      {!imageSrc && !tempImgSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="14">
                        Upload image here
                      </text>
                      )}     
                  </svg>
          <input
            type="file"
            name='valid_ID'
            id='valid_ID'
            ref={fileInputRef}
            style={{ display: 'none' }}
            accept="image/*"
            onChange={handleChange}
          />
        </div>
      </div>
      <hr></hr>
      <div className="row g-3 align-items-center mb-2">
        <div className="col-12 text-center">
          <p style={{margin: '0'}}><b>Account Credentials</b></p>
        </div>
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-4">
        <label htmlFor="email" className="form-label">
                Email address
              </label>
              <input
                type="email"
                className="form-control mb-2"
                id="emailAdd" name="emailAdd"
                placeholder="Enter your email"
                onChange={handleChange}
                value={formData.emailAdd}
                disabled
              /></div>
        <div className="col-4">
        <label htmlFor="password" className="form-label">
                Password
              </label>
              <input
                type="password"
                className="form-control mb-2"
                id="password" name="password"
                placeholder="Enter your password"
                value={formData.password}
                onChange={handleChange}
                disabled
              /></div>
        <div className="col-4">
          <label htmlFor="password" className="form-label">
                User Type
              </label>
              <select className="form-select" name='userType' id='userType' 
              value={
                parseInt(formData.userType) === 0 ? 'Admin' :
                parseInt(formData.userType) === 1 ? 'Jail Commissary Staff' :
                'Shopkeeper'
                } onChange={handleChange}>
                <option value="" disabled hidden>Select User Type</option>
                <option value="Admin">Admin</option>
                <option value="Jail Commissary Staff">Jail Commissary Staff</option>
                <option value="Shopkeeper">Shopkeeper</option>
              </select>
            </div>
      </div>
      <div className="row g-3 align-items-center mb-2">
        <div className="col-12 text-center">
          <p style={{margin: '0'}}><b>Basic Info</b></p>
        </div>
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-6">
          <label htmlFor="firstName"  className="col-form-label">First Name:</label>
          <input type="text" id="firstName" value={formData.firstName} name="firstName" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter first name" onChange={handleChange} />
        </div>
        <div className="col-6">
          <label htmlFor="lastName"  className="col-form-label">Last Name:</label>
          <input type="text" id="lastName" value={formData.lastName} name="lastName" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter last name" onChange={handleChange} />
        </div>
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-6">
          <label htmlFor="middleName" className="col-form-label">Middle Name:</label>
          <input type="text" id="middleName" value={formData.middleName} name="middleName" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter middle name" onChange={handleChange} />
        </div>
        <div className="col-6">
          <label htmlFor="contact_number" className="col-form-label">Contact Number:</label>
          <input type="number" id="contact_number" name="contact_number" value={formData.contact_number} className="form-control" maxLength={11} placeholder="Enter contact number" onChange={handleChange} />
        </div>
      </div>
      <div className="row g-3 align-items-center">
            <div className="col-12">
            <label htmlFor="complete_address" className="form-label">
                      Complete Address
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      value={formData.complete_address}
                      id="complete_address" name="complete_address"
                      placeholder="Enter your complete address"
                      onChange={handleChange}
                    />
            </div>
          </div>
        <div className="row g-3 align-items-center">
        <div className="col-6 d-flex">
        <div className={`${optionDisplayForUser.length > 0 ? 'col-11' : 'col-12'}`} style={{marginTop: '-5px'}}>
                <label htmlFor="bjmp_branch" >
                    BJMP Branch Location:
                  </label>
                  {!selectBranchState ? <input
                    type="text"
                    id="bjmp_branch"
                    name="bjmp_branch"
                    className="form-control"
                    style={{ boxShadow: "none" }}
                    placeholder="Enter BJMP Branch"
                    onChange={handleChange}
                    value={formData.bjmp_branch}
                  /> : 
                  <select
                      name="bjmp_branch"
                      id="bjmp_branch"
                      className="form-select"
                      style={{ width: '100%', boxShadow: 'none' }}
                      value={formData.bjmp_branch}
                      onChange={handleChange}
                      >
                        <option value="" hidden disabled>Select branch</option> 
                        {/* Map through the preDeterminedOptions array */}
                          {preDeterminedOptions.map((provincialJail, index) => (
                            <optgroup key={index} label={provincialJail.label}>
                              {/* Map through the facilities array within each provincial jail */}
                              {provincialJail.facilities.map((facility, subIndex) => (
                                <option key={subIndex} value={facility}>{facility}</option>
                              ))}
                            </optgroup>
                            ))}
                      

                      {Array.isArray(optionDisplayForUser) &&
                        [...new Set(optionDisplayForUser.map(item => item.bjmp_branch))]
                        .filter(bjmp_branch => 
                          !preDeterminedOptions.some(option => 
                            option.facilities.some(facility => facility.toLowerCase() === bjmp_branch.toLowerCase())
                          )
                        ) 
                          .map((bjmp_branch, index) => (
                            <option key={index} value={bjmp_branch}>
                              {bjmp_branch}
                            </option>
                          ))}

                      </select>
                      }
                </div>
                {optionDisplayForUser.length > 0 ? (<div className="col-1">
                    <button
                      type="button"
                      className="btn"
                      onClick={setAddNewBranchType}
                      style={{ borderColor: "#D0D3D9", marginTop: '21px'}}
                    >
                      {selectBranchState ? 'x' : '+'}
                    </button>
                </div>) : ''}
                </div>
        <div className="col-6">
        <label htmlFor="position" className="form-label">
                      Position
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="position" name="position"
                      placeholder="Enter position"
                      value={formData.position}
                      onChange={handleChange}
                    /></div>
      </div>
    </div>
    <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
    <div className={`${dst['modal-footer']} d-flex justify-content-end`} style={{ border: 'none', marginTop: 'auto' }}>
    
    <button type="button" className="btn" onClick={onClose}>
      Discard
    </button>
  <button
    type="button"
    onClick={handleSubmit}
    className="btn"
    style={{ backgroundColor: '#505050', color: '#fff' }}
  >
    Submit
  </button>
</div>


  </div>
</div>

  );
}

