import React, { useState, useEffect, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import { Bar } from "react-chartjs-2";
import Chart from "chart.js/auto";
import axios from 'axios';
import domain from '../../constants/domain';


function MostPurchases({isSidebarOpen, pdlId}) {
  const [selectedPeriod, setSelectedPeriod] = useState('AllTime');
  const BarChart = () => {
    const [chartData, setChartData] = useState([]);

    useEffect(() => {
      axios.get(selectedPeriod === 'AllTime' ?
      `${domain}/index.php?action=getAllTimePurchases&pdlNo=${pdlId}` :
      selectedPeriod === 'ThisWeek' ? 
      `${domain}/index.php?action=getThisWeeksPurchases&pdlNo=${pdlId}` : 
      `${domain}/index.php?action=getThisMonthsPurchases&pdlNo=${pdlId}`)
      .then((response) => {
        setChartData(response.data);
      })
      .catch((error) => {
        console.error(`Error fetching ${selectedPeriod} spending data: `, error);
      })
    }, [pdlId])
    
    const handlePeriodChange = (event) => {
      setSelectedPeriod(event.target.value);
    };
    const formattedLabels = Array.isArray(chartData)
    ? chartData.map((item) => `${item.prodType} - ${item.prodName}`)
    : [];

    const data = {
      labels: formattedLabels,
      datasets: [
        {
          label: "Amount",
          data: Array.isArray(chartData) ? chartData.map(entry => entry.items_qty_sum) : [],
          backgroundColor: ['#10069F'],
          borderRadius: 50,
          barThickness: 10,
        }
      ]
    }
    const options = {
      indexAxis: 'y',
      scales: {
        y: {
          beginAtZero: true,
          ticks: {
            callback: (value, index) => formattedLabels[index],
          },
        },
      },
      maintainAspectRatio: false,
      responsive: true,
    };
    const chartDataSum =
    Array.isArray(chartData) &&
    chartData.reduce((accumulator, entry) => accumulator + parseInt(entry.items_qty_sum), 0);
    return (
      <div className={dst.pdlCards}>
        <div className='row d-flex align-items-center position-relative'>
          <div className='col-8'>
            <p className="fw-bold ms-3 p-1 fs-6" 
            style={{ position: 'relative', top: '5px', textAlign: 'left' }}>
              Most Purchases
            </p>
          </div>
          <div className='col-4 align-items-end justify-content-end'>
            <div className="position-absolute mb-5 top-0 end-0 p-2">
              <select 
              id="durationSelector"
              className="form-select form-select-sm"
              value={selectedPeriod}
              onChange={handlePeriodChange} >
                  <option value="AllTime">All-Time</option>
                  <option value="ThisWeek">This Week</option>
                  <option value="ThisMonth">This Month</option>
                </select>
            </div>
              {/* Move the span for the specified week to the top right */}
            </div>
          </div>
          <div className={dst.chartContainer} style={{height: '180px'}}>
          {chartDataSum != 0 ? (
              <div style={{ height: '100%' }}>
                <Bar data={data} options={options} />
              </div>
            ) : (
              <div className="text-center p-3">
                <p className="text-muted mb-0">No data available for the selected period.</p>
              </div>
            )}
        </div>
        </div>
        
    );
  };


  return (
    <div className={`${isSidebarOpen ? 'sidebarOpen' : 'sidebarClosed'}`}>
      <div className='mt-2'>
        <BarChart selectedPeriod={selectedPeriod} />
      </div>
    </div>
  );
}

export default MostPurchases;
