import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from './SuccessfulAction';
import axios from 'axios';
import domain from '../../constants/domain';
import DeleteModal from '../modals/DeleteModal';

function StockEntryModal({onClose, itemId}) {
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);
  const [primaryID, setPrimaryID] = useState(null);
  const [imageSrc, setImageSrc] = useState(null);
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const [stockData, setStockData] = useState({});
  const [instance, setInstance] = useState({});
  const [quantity, setQuantity] = useState(0);
  const [changeQtyState, setChangeQtyState] = useState({
    onState: false,
    id: null,
    remainingStock: null
  }
)
  useEffect (() => {
      getStock();
  }, [])
  
function getStock(){
  axios.get(`${domain}/index.php?action=displaySpecificStock&itemId=${itemId}`)
  .then(function(response) {
      setStockData(response.data[0]);
        const imageUrl = response.data[0].stock_image;
        const fileName = imageUrl.split('/').pop();
        setImageSrc(fileName);
  }).catch(error => console.error('Error fetching stock data: ', error));
  axios.get(`${domain}/index.php?action=displayInstance&id=${itemId}`)
  .then(response =>{
    setInstance(response.data);
  }).catch(error => console.error('Error fetching stock data: ', error));
}
const handleChange = (e) => {
  const name = e.target.name;
  const value = parseInt(e.target.value, 10);

  // Check if the value is within the desired range
  if (value >= 1) {
    setQuantity(value);
  }
};
const setChangeQuantity = (item, e, state) => {
  if (state) {
    axios.get(`${domain}/index.php?action=getSpecificInstance&id=${item}`)
      .then(response => {
        if (response.data) {
          setChangeQtyState({
            id: response.data[0].item_id,
            remainingStock: parseInt(response.data[0].remainingStock)
          });
          setQuantity(parseInt(response.data[0].remainingStock));
        }
      });
  } else {
    setChangeQtyState({ id: null, remainingStock: null });
  }
  console.log(changeQtyState);
};
const changeQuantityData = (item, e) => {
  const changeQty = {
    quantity: quantity,
    action: 'updateInstance',
    itemID: item
  }
  axios.post(domain, changeQty).then(response => {
          axios.get(`${domain}/index.php?action=getSpecificInstance&id=${item}`)
          .then(response => {
            if(response.data){
                setChangeQtyState({onState: false, id: null, remainingStock: null});
                getStock();
            }
          })
        })
        .catch(error => {
          console.error('Error adding quantity:', error);
        });
}
const openDeleteModal = (primID, e) => {
  setDeleteModalOpen(true);
  setPrimaryID(primID);
}
const closeDeleteModal = () => {
  setDeleteModalOpen(false);
  getStock();
}
  const openSuccessModal = () => {
    
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
  };

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onClose(); // Close the modal
      }
    };
  
    const getCellStyleForType = (type) => {
      // Map type to corresponding CSS class
      switch (type) {
        case 'Overstocked':
          return dst.Overstocked;
        case 'Healthy':
          return dst.Healthy;
        case 'NearCritical':
          return dst.NearCritical;
        case 'AtCritical':
          return dst.AtCritical;
        case 'BelowCritical':
          return dst.BelowCritical;
        case 'NoStock':
          return dst.NoStock;
        default:
          return ''; // Use a default style if needed
      }
    };
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '1200px'}}>
        <div className={`${dst['modal-header']}`}>
            <div className='row w-100'>
              <div className='col-10 d-flex align-items-center'>
                <i className='fa-solid fa-box'></i>
                <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Stock Details</h6>
              </div>
              <div className='col-2 d-flex justify-content-end align-items-center'>
                <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
              </div>
            </div>
          </div>
          <div className={dst['modal-body']}>
          <div className="row g-3 align-items-center mb-2">
            <div className='col-5' style={{borderRight: '1px solid #ccc'}}>
              <img src={`../images/stocks/${imageSrc}`} 
                style={{ width: '50%', 
                  height: '50%',
                  borderRadius: '100px' }}
                alt="logo" />
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Item No.:</b> {stockData.id}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Product Type:</b> {stockData.prodType}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Product Name:</b> {stockData.prodName}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Price:</b> PHP{stockData.formatted_price}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Category:</b> {stockData.category}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Remaining Stock:</b> {stockData.remainingStock}
                </label>
                <label htmlFor="itemNo" className="col-form-label d-flex">
                  <b style={{marginTop: '1px'}}>Status:</b> 
                  {(parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) >= 21 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Overstocked')}`} style={{fontSize: '9px'}}>
                                  Overstocked
                                 </div>)
                                }
                                {(parseInt(stockData.remainingStock)- parseInt(stockData.criticalStock)) <= 20 &&
                                (parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) >= 11 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Healthy')} mx-2`} style={{fontSize: '9px'}}>
                                  Healthy
                              </div>)
                                }
                                {(parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) <= 10 &&
                                (parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) >= 1 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NearCritical')} mx-2`} style={{fontSize: '9px'}}>
                                  Near Critical
                                </div>)
                                }
                                {parseInt(stockData.remainingStock) === parseInt(stockData.criticalStock) &&
                                parseInt(stockData.remainingStock) !== 0 && parseInt(stockData.criticalStock) !== 0 && 
                                (<div className={`${dst.typeCell} ${getCellStyleForType('AtCritical')} mx-2`} style={{fontSize: '9px'}}>
                                At Critical
                              </div>)
                                }
                                {parseInt(stockData.remainingStock) < parseInt(stockData.criticalStock) &&
                                parseInt(stockData.remainingStock) !== 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('BelowCritical')} mx-2`} style={{fontSize: '9px'}}>
                                Below Critical
                              </div>)
                                }
                                {parseInt(stockData.remainingStock) === 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NoStock')} mx-2`} style={{fontSize: '9px'}}>
                                No Stock
                              </div>)
                                }
                </label>
            </div>
            <div className='col-7'>
              <div className='row d-flex align-items-center justify-content-center'>
                <div className='col-12 d-flex align-items-center justify-content-center'>
                <table
                  className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    overflow: 'auto',
                    fontSize: '12px',
                  }}
                >
                <thead>
                  <tr style={{fontSize: '12px'}}>
                    <th scope="col">
                      Instance ID
                    </th>
                    <th scope="col">
                      Date Added
                    </th>
                    <th scope="col">
                      Expiration Date
                    </th>
                    <th scope="col">
                      Quantity
                    </th>
                    <th scope="col">
                      Actions
                    </th>
                    </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {Array.from({ length: 15 || 1 }, (_, index) => {
                  const entry = instance[index];
                  const hasData = entry !== undefined;
                  const cellStyle = hasData ? {} : { padding: '15px' }; // Adjust the padding value as needed
                    return(<tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                    `}>
                      <td style={cellStyle}>
                        {hasData ? `I-${entry.related_item_id}` : ''}
                      </td>
                      <td style={cellStyle}>
                        {hasData ? `${entry.date_added}` : ''}
                      </td>
                      <td style={cellStyle}>
                        {hasData ? `${entry.exp_date}` : ''}
                      </td>
                      <td style={cellStyle}>
                      {hasData ? (
                              <>
                                {hasData ? (
                                    <>
                                      {!changeQtyState.id || changeQtyState.id !== entry.item_id ? (
                                        <>{entry.remainingStock}</>
                                      ) : (
                                        <>
                                          <input
                                            type="number"
                                            name="quantity"
                                            id="quantity"
                                            onChange={handleChange}
                                            style={{
                                              width: '60px',
                                              height: '15px',
                                              appearance: 'textfield',
                                              '-moz-appearance': 'textfield'
                                            }}
                                            value={quantity}
                                            min="0"
                                          />
                                        </>
                                      )}
                                    </>
                                  ) : (
                                    ''
                                  )}
                              </>                                  
                            ) : (
                            ''
                          )}
                      </td>
                      <td style={cellStyle}>
                        {hasData ? (
                            !changeQtyState.id || changeQtyState.id !== entry.item_id ? (
                              
                              <div className={`${dst.actionButtons} ${dst.exclude}`}>
                                
                              <p onClick={(e) => setChangeQuantity(entry.item_id, e, true)}>
                                <i className={`fa-solid fa-pen-to-square fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Edit</span>
                              </p>
                              <p onClick={(e) => openDeleteModal(entry.item_id, e)}>
                                <i className={`fa-solid fa-trash fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Remove</span>
                              </p>
                            </div>
                            ) : (<div className={`${dst.actionButtons}`}>
                              
                            <p onClick={(e) => changeQuantityData(entry.item_id, e)}>
                              <i className={`fa-solid fa-check fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Confirm</span>
                            </p>
                            <p onClick={(e) => setChangeQuantity(entry.item_id, e, false)}>
                            <i className={`fa-solid fa-x fa-sm ${dst.iconHover}`}></i>
                                <span style={{transform: 'translateX(-20px)'}} className={dst.iconTooltip}>Discard</span>
                            </p>
                          </div>)
                          ) : (
                            ''
                          )}  
                      </td>
                    </tr>);

                })}
                </tbody>
                </table>
                </div>
              </div>
              
           
            </div>
            
            
            </div>

            <div className={`${dst['modal-footer']} d-flex justify-content-end`} style={{ border: 'none', marginTop: 'auto' }}>
  
              <button type="button" className="btn" onClick={onClose} style={{ borderColor: '#D0D3D9' }}>
                Discard
              </button>
            </div>
          </div>
        </div>
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
        {isDeleteModalOpen && <DeleteModal primaryPDLID={primaryID}
       onClose={closeDeleteModal} insPrimaryID={primaryID}/>}
    </div>
  );
}

export default StockEntryModal;
