import React, { useState, useEffect, useRef } from "react";
import dst from "../../styles/DashboardStyle.module.css"; // Import your CSS module
import axios from "axios";
import { useNavigate } from "react-router-dom";
import domain from "../../constants/domain";
function StockEntryModal({ onClose }) {
  const navigate = useNavigate();

  const [inputs, setInputs] = useState({});
  const [selectCategory, setSelectCategory] = useState(""); // Initialize selectCategory
  const [selectStock, setSelectStock] = useState("");
  const [selectPrice, setSelectPrice] = useState(0.00);
  const [formdata, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState('');
  const [optionDisplayForStock, setOptionDisplayForStock] = useState([]);
  const [selectTypeState, setSelectTypeState] = useState(optionDisplayForStock.length != 0 ? false : true);
  const [selectCategoryState, setSelectCategoryState] = useState(true);
  const [imageSrc, setImageSrc] = useState(null);
  const fileInputRef = useRef(null);
  useEffect (() => {
    axios.get(`${domain}/index.php?action=displayStock`)
    .then(response => {
      if (!response.data.fail) {
        // Update the state with the fetched PDL values
        setOptionDisplayForStock(response.data);
      }
    })
    .catch(error => console.error('Error fetching PDL values:', error));
  
  }, [])

  
  const handleSvgClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click(); // Trigger click on the file input
    }
  };
  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.type === 'file' ? event.target.files[0] : event.target.value;
    const action = "createStock";
    if (event.target.type === 'file') {
      const reader = new FileReader();
  
      reader.onload = (event) => {
        setImageSrc(event.target.result);
        setInputs(values => ({ ...values, action, stockimage: value })); // Use e.target.files[0]
      };
      if (value) {
        reader.readAsDataURL(value);
    
      }
      return;
    }
    
    setInputs((values) => ({ action, ...values, [name]: value }));

    if (name === "category") {
      setSelectCategory(value);
      setInputs((values) => ({ ...values, action, category: value }));
      return;
    } 
    if (name === 'productType') {
      setSelectStock(value);
      setInputs((values) => ({...values, action, productType: value}));
      return;
    }

    if(name === 'price'){
      setSelectPrice(parseFloat(value));
      setInputs((values) => ({...values, action, price: parseFloat(value)}));
      return;
    }
    else {
      setInputs((values) => ({ ...values, action, [name]: value }));
      return;
    }
    

  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const {productType, productName, category, price, stockimage, criticalStock} = inputs
    const fileUp = new FormData();
    fileUp.append('stock_image', stockimage);
    fileUp.append('productType', productType);
    fileUp.append('productName', productName);
    fileUp.append('category', category);
    fileUp.append('price', price);
    fileUp.append('action', 'uploadStockImage');
    console.log(inputs);
    if(!productType || !productName || !category || !price || !stockimage || !criticalStock ){
      setErrorMessage('Please input something on the fields above!');
    }
    else{
      setErrorMessage('');
      axios
      .post(domain, inputs)
      .then(function (response) {
            axios.request({
              method: 'POST',
              url: domain,
              headers: {
                'Content-Type': 'multipart/form-data'
              },
              data: fileUp
            }).then(response => {
              if (response.data) {
                onClose();
              }
            }).catch(error => console.error('Error handling file:', error));
      }).catch(error => console.error("Error handling file: ", error));
    }
    
  };
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === "exampleModal") {
      onClose(); // Close the modal
    }
  };

  const setAddNewStockType = () => {
    setSelectTypeState((prevState) => !prevState);
  }

  const setAddNewCategoryType = () => {
    setSelectCategoryState((prevState) => !prevState);
  }

  const rectWidth = imageSrc ? 162 : 502; // Adjust width based on imageSrc
  const rectHeight = imageSrc ? 162 : 162; // Adjust height based on imageSrc

  return (
    <div
      className={dst.modal}
      onClick={handleBackdropClick}
      id="exampleModal"
      tabIndex="-1"
    >
      <div
        className={dst["modal-content"]}
        style={{ width: "600px"}}
      >
         <div className={`${dst['modal-header']}`}>
      <div className='row w-100'>
        <div className='col-10 d-flex align-items-center'>
          <i className='fa-solid fa-plus'></i>
          <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Create Stock Entry</h6>
        </div>
        <div className='col-2 d-flex justify-content-end align-items-center'>
          <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
        </div>
      </div>
    </div>
        <div className={dst["modal-body"]}>
          <form onSubmit={handleSubmit}>
          <div className="row d-flex justify-content-center" style={{ display: 'flex' }}>
        <div className="col-12 mb-4" >
        <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {imageSrc && (
                        <image
                          x="20%"
                          y="20%"
                          xlinkHref={imageSrc}
                          width="100"
                          height="100"
                        />
                      )}
                      {!imageSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="14">
                        Upload image here
                      </text>
                      )}     
                  </svg>
          <input
            type="file"
            name='stockimage'
            id='stockimage'
            ref={fileInputRef}
            style={{ display: 'none' }}
            accept="image/*"
            onChange={handleChange}
          />
        </div>
      </div>
      <hr></hr>
            <div className="row g-2 align-items-center mb-1">
              <div className={`${optionDisplayForStock.length > 0 ? 'col-11' : 'col-12'} `}>
              <label htmlFor="productType" className="col-form-label">
                  Product Type:
                </label>
                {selectTypeState ? <input
                  type="text"
                  id="productType"
                  name="productType"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter product type"
                  onChange={handleChange}
                /> : 
                <select
                    name="productType"
                    id="productType"
                    className="form-select"
                    style={{ width: '100%', boxShadow: 'none' }}
                    value={selectStock}
                    onChange={handleChange}
                    >
                    <option value="" hidden disabled>Select stock</option>   
                    {  Array.isArray(optionDisplayForStock) &&
                      [...new Set(optionDisplayForStock.map(item => item.prodType))].map((prodType, index) => (
                        <option key={index} value={prodType}>
                          {prodType}
                        </option>
                      ))}
                    </select>
                    }
              </div>
              {optionDisplayForStock.length > 0 ? (<div className="col-1">
                  <button
                    type="button"
                    className="btn"
                    onClick={setAddNewStockType}
                    style={{ borderColor: "#D0D3D9", marginTop: '32px'}}
                  >
                    {selectTypeState ? 'x' : '+'}
                  </button>
              </div>) : ''}
            </div>
            <div className="row g-2 align-items-center mb-1">
              <div className="col-12">
              <label htmlFor="productName" className="col-form-label">
                  Product Name:
                </label>
                <input
                  type="text"
                  id="productName"
                  name="productName"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter product name"
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="row g-2 align-items-center mb-1">
              <div className="col-11">
              <label htmlFor="category" className="col-form-label">
                  Category:
                </label>
                {!selectCategoryState ? <input
                  type="text"
                  id="category"
                  name="category"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter category"
                  onChange={handleChange}
                /> : 
                <select
                    name="category"
                    id="category"
                    className="form-select"
                    style={{ width: '100%', boxShadow: 'none' }}
                    value={selectCategory}
                    onChange={handleChange}
                    >
                    <option value="" hidden disabled>Select category</option>  
                    <option value="Personal Hygiene">Personal Hygiene</option>
                    <option value="Consumables">Consumables</option>
                    <option value="Toiletries">Toiletries</option> 
                    {Array.isArray(optionDisplayForStock) && optionDisplayForStock
        .filter(item => !["Personal Hygiene", "Consumables", "Toiletries"].includes(item.category))
        .map((item, index) => (
          <option key={index} value={item.id}>
            {item.category}
          </option>
        ))}
                    </select>
                    }
              </div>
              <div className="col-1">
                  <button
                    type="button"
                    className="btn"
                    onClick={setAddNewCategoryType}
                    style={{ borderColor: "#D0D3D9", marginTop: '32px'}}
                  >
                    {selectCategoryState ? '+' : 'x'}
                  </button>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-6">
              <label htmlFor="price" className="col-form-label">
                  Price:
                </label>
              <input
                  type="number"
                  id="price"
                  name="price"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter the price"
                  onChange={handleChange}
                  step={0.01}
                  value={selectPrice}
                />
              </div>
              <div className="col-6">
              <label htmlFor="criticalStock" className="col-form-label">
                  Critical Stock Threshold:
                </label>
              <input
                  type="number"
                  id="criticalStock"
                  name="criticalStock"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter the critical threshold"
                  onChange={handleChange}
                />
              </div>
            </div>
            <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
            <div className={`${dst['modal-footer']} d-flex justify-content-end`} style={{ border: 'none', marginTop: 'auto' }}>

                  <button
                  type="button"
                  className="btn"
                  onClick={onClose}
                  style={{ borderColor: "#D0D3D9"}}
                >
                  Discard
                </button>
                <button
                  type="submit"
                  className="btn ms-2"
                  style={{
                    backgroundColor: "#505050",
                    color: "#fff",
                  }}
                >
                  Submit
                </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default StockEntryModal;
