import React from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import axios from 'axios';
import domain from '../../constants/domain';

function RetrievePopUp({ onClose, 
  primaryPDLID, pdlID, primaryStockID, stockID, primaryUserID, userID,
  retrieveType }) {
  const handleBackdropClick = (e) => {
    console.log(primaryUserID, userID);
    // Check if the click target is the backdrop element
    if (e.target.id === 'successModal') {
      onClose(); // Close the modal
    }
  };
  const handleRetrieve = async (e) => {
    if(retrieveType === 'PDL'){
        axios.post(domain, {
          action: 'retrievePDLEntry',
          pdlID: pdlID,
          primaryID: primaryPDLID,
        }).then(response => {
          if(response.data);
            onClose();
        })
    }
    else if(retrieveType === 'Stock'){
      axios.post(domain, {
        action: 'retrieveStockEntry',
        stockID: stockID,
        primaryID: primaryStockID,
      }).then(response => {
        if(response.data);
          onClose();
      })
    }
    else if (retrieveType === 'User'){
      axios.post(domain, {
        action: 'retrieveUserEntry',
        userID: userID,
        primaryID: primaryUserID,
      }).then(response => {
        if(response.data);
          onClose();
      })
    }
    
  }
  
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="successModal" tabIndex="-1">
      <div>
        <div className={dst['modal-content']} style={{width: '500px'}}>

          <div className={`text-center ${dst['modal-body']}`}>
            <i className={`fa-regular fa-circle-check`} style={{fontSize: '40px', marginBottom: '20px'}}></i>
            <h3>Confirm Retrieval of Entry?</h3>
            <p style={{ color: '#a1a1a1' }}>Are you sure you want to retrieve the entry? The ID might be changed if it has a conflicting entry</p>
            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
                <button
                type="button"
                className="btn link modal-close"
                style={{ border: '1px solid #505050', width: '40%' , marginRight: '15px'}}
                onClick={onClose}
              >
                No, Cancel
              </button>
              <button
                type="submit"
                className="btn"
                style={{ backgroundColor: '#505050', color: '#fff', width: '40%', display: 'block' }}
                onClick={handleRetrieve}
              >
                OK
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RetrievePopUp;
