import React, {useEffect, useState} from 'react';
import dst from '../../../styles/DashboardStyle.module.css'; // Update the CSS module path
import { useNavigate } from 'react-router-dom';

export default function PurchaseContainer({element}) {

    const e = element;
    const [imageSrc, setImageSrc] = useState(null);
    useEffect(() => {
        setImageSrc(e.stock_image.split('/').pop());
      }, [e]);
  
  return (
        <div className={dst.purchaseContainer}>
        <div className='row w-100 d-flex align-items-center '>
            <div className='col-2'>
            <img
                src={`../images/stocks/${imageSrc}`}
                style={{
                width: '50px', // Use 100% width to stretch across the container
                height: '50px',
                objectFit: 'cover', // Ensure the image covers the container
                borderRadius: '100px',
                }}
                alt="stock"
            />
            </div>
            <div className='col-5'>
                <div className='row'>
                    <div className='col d-flex justify-content-start'>
                        <b>{e.prodType}</b>
                    </div> 
                </div>
                <div className='row'>
                    <div className='col d-flex justify-content-start' style={{fontSize: '12px'}}>
                        {e.prodName}
                    </div>
                    
                </div>
            </div>
            <div className='col-3'>
                <div className='row'>
                    <div className='col d-flex justify-content-end' style={{fontSize: '12px'}}>
                        {e.quantity}
                    </div> 
                </div>
            </div>
            <div className='col-2'>
                <div className='row'>
                    <div className='col d-flex justify-content-start' style={{fontSize: '12px'}}>
                        <b>PHP{(parseFloat(e.formatted_price) * parseInt(e.quantity)).toFixed(2)}</b>
                    </div> 
                </div>
            </div>
        </div>
        </div>

  );
}


