
import React, { useEffect, useState, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link } from 'react-router-dom';
import CreateStock from '../modals/CreateStockEntry';
import EditStock from '../modals/EditStockEntry';
import ConfirmArchive from '../modals/ConfirmArchive';
import AuditDetails from '../modals/LogDetails';
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';
import FilterDropdown from '../modals/FilterDropdown';
import {useReactToPrint} from 'react-to-print';
export default function AuditLogList() {
  const componentPDF = useRef();
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const day = String(currentDate.getDate()).padStart(2, '0');
  
  const formattedDate = `${year}-${month}-${day}`;
  
  const hours = String(currentDate.getHours()).padStart(2, '0');
  const minutes = String(currentDate.getMinutes()).padStart(2, '0');
  const formattedTime = `${hours}:${minutes}`;

  const [auditLogData, setAuditLogData] = useState([]);
  const [selectedLogId, setSelectLogId] = useState(null);
  const [dateRangeFilter, setDateRangeFilter] = useState(['', '']);
  const ROWS_PER_PAGE = 25;
  const [currentPage, setCurrentPage] = useState(1);
  const startIndex = (currentPage - 1) * ROWS_PER_PAGE;
  const endIndex = Math.min(startIndex + ROWS_PER_PAGE, auditLogData.length);
  const visibleRows = auditLogData && auditLogData.slice(startIndex, endIndex);
  const [selectedRows, setSelectedRows] = useState([]);
  const [selectMultipleEnabled, setSelectMultipleEnabled] = useState(false);
  const [activeTab, setActiveTab] = useState("transactions");
  const [filterOptions, setFilterOptions] = useState({
    date_time: ['Newest', 'Oldest'],
    email: [''], // Example for the 'Balance' column
    type: ['Purchase', 'Load'],
    branch: [''],
    amount: ['Highest Amount', 'Lowest Amount'], // Example for the 'Type' column
    pdl: [''],
  });
  const [selectedFilter, setSelectedFilter] = useState({
    date_time: '',
    date_time_filter: '',
    email: '',
    type: '',
    branch: '',
    amount: '',
    pdl: ''
    // Add more columns as needed
  });
  const [selectedFilterLabel, setSelectedFilterLabel] = useState({
    date_time_filter: '',
    date_time: 'Date and Time',
    email: 'Email',
    type: 'Transaction Type',
    branch: 'Branch Location',
    amount: 'Description',
    pdl: 'Involved PDL'
  });
  const tabs = [
    { label: "Transactions", id: "transactions" },
    { label: "Data Management", id: "dataManagement" },
    // Add more tabs as needed
  ];
  const [isFilterDropdownOpen, setFilterDropdownOpen] = useState(false);
  const [filterDropdownColumn, setFilterDropdownColumn] = useState('');
  const [logo, setLogo] = useState(null);
  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setLogo(fileName);
      }
    )

   }, [])
  useEffect(() => {
    getData();
  }, [activeTab])
  function getData(){
    axios.get(`${domain}/index.php?action=displayAudit`)
    .then((response) => {
      if(response.data){
        setAuditLogData([]);
        if(Array.isArray(response.data)){
          setAuditLogData(response.data);
          setFilterOptions(prevFilterOptions => ({
            ...prevFilterOptions,
            email: response.data.map(data => data.email),
            pdl: response.data.map(data => data.to_pdl),
          }));
          console.log(filterOptions);
        }
        else{
          console.error("API response is not an array:", response.data);
        }
      }
      else{
        setAuditLogData([]);
      }
    }).catch(error => console.error('Error fetching audit log data: ', error));
  }
  const isDateInRange = (date, startDate, endDate) => {
    const currentDate = new Date(date);
    const rangeStartDate = new Date(startDate);
    const rangeEndDate = new Date(endDate);
  
    return currentDate >= rangeStartDate && currentDate <= rangeEndDate;
  };
  const filteredData = auditLogData.filter((data) => {

    const isEmailMatch = 
    selectedFilter.email === '' || 
    selectedFilter.email.toUpperCase().includes(data.email.toUpperCase());
    const isTypeMatch = 
      selectedFilter.type === '' ||
      selectedFilter.type.toUpperCase().includes(data.transaction_type.toUpperCase());
    
  // Check for date range filter
    const isDateInRangeFilter =
    dateRangeFilter[0] === '' || dateRangeFilter[1] === '' ||
    isDateInRange(data.date_time, dateRangeFilter[0], dateRangeFilter[1]);

    return isEmailMatch && isTypeMatch && isDateInRangeFilter;
  });
  console.log(filteredData);
  filteredData.sort((a, b) => {
    const parseTransaction = (transaction) => {
      const regex = /^([+-])([^\d]+)(\d+(\.\d{1,2})?) to Balance$/;
      const match = transaction.match(regex);
  
      if (match) {
        const operator = match[1]; // "+" or "-"
        const amount = parseFloat(match[3]); // e.g., 46.50 or 250
        
        const numericValue = operator === '+' ? amount : -amount; // consider the operator
        return { numericValue, operator, amount };
      } else {
        return null;
      }
    };
  
    const getNumericValue = (data) => {
      if (data && data.description) {
        const parsedTransaction = parseTransaction(data.description);
        return parsedTransaction ? parsedTransaction.numericValue : 0;
      }
      return 0;
    };
  
    const numericValueA = getNumericValue(a);
    const numericValueB = getNumericValue(b);
    const dateA = new Date(a.date_time.replace(/-/g, '/'));
    const dateB = new Date(b.date_time.replace(/-/g, '/'));
  
    if (selectedFilter.amount === 'Highest Amount') {
      return numericValueB - numericValueA;
    } else if (selectedFilter.amount === 'Lowest Amount') {
      return numericValueA - numericValueB;
    }
    if (selectedFilter.date_time === 'Newest') {
      return dateB - dateA;
    } else if (selectedFilter.date_time === 'Oldest') {
      return dateA - dateB;
    }
  
    return 0;
  });
  const toggleFilterDropdown = (column) => {
    setFilterDropdownOpen(!isFilterDropdownOpen);
    setFilterDropdownColumn(column);
  };

  const handleFilterSelect = (option, column) => {
    // Implement logic to filter data based on the selected option
    // You can use this information to filter your data accordingly

    // Update the selected filter state for the specific column
    setSelectedFilter({
      ...selectedFilter,
      [column]: option,
    });

    // Update the label for the corresponding column
    switch (column) {
      case 'date_time':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          date_time: option,
        });
        break;
      case 'date_time_filter':
          setSelectedFilterLabel({
            ...selectedFilterLabel,
            date_time_filter: option,
          });
          const [startDateTime, endDateTime] = option.split(" - ");

          // Split each date-time into date and time
          setDateRangeFilter([startDateTime, endDateTime]);
          // Update the state with the selected date-time values
          break;
      case 'email':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          email: option,
        });
        break;
      case 'type':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          type: option,
        });
        break;
      case 'amount':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          amount: option,
          
        });
        break;
      case 'pdl':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          pdl: `PDL-${option}`,
        });
        break;
      
      default:
        break;
    }
    setFilterDropdownOpen(false);
  };


  const renderFilterDropdown = (column) => {
    if (isFilterDropdownOpen && filterDropdownColumn === column) {
      if (column === 'date_time_filter') {
        return (
          <FilterDropdown
            options={filterOptions[column]}
            onSelect={(option) => handleFilterSelect(option, column)}
            type={column}
          />
        );
      } else {
        return (
          <FilterDropdown
            options={filterOptions[column]}
            onSelect={(option) => handleFilterSelect(option, column)}
            type={column}
          />
        );
      }
    }
    return null;
  };
  const totalEntries = filteredData ? filteredData.length : 0;
  const totalPages = Math.ceil(totalEntries / ROWS_PER_PAGE);
  let entriesToShow = [];
  if (Array.isArray(filteredData)) {
    entriesToShow = filteredData.slice(
      (currentPage - 1) * ROWS_PER_PAGE,
      currentPage * ROWS_PER_PAGE
    );
}
const eraseFilters = () => {
  setSelectedFilter({
    date_time: '',
    date_time_filter: '',
    email: '',
    type: '',
    branch: '',
    amount: '',
    pdl: ''
    // Add more columns as needed
  });
  setDateRangeFilter(['', '']);
  setSelectedFilterLabel(({
    date_time_filter: '',
    date_time: 'Date and Time',
    email: 'Email',
    type: 'Transaction Type',
    branch: 'Branch Location',
    amount: 'Description',
    pdl: 'Involved PDL'
  }));
}
  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };
  const [selectedOption, setSelectedOption] = useState('');
  const [isDetailModalOpen, setDetailModalOpen] = useState(false);
  const openDetailModal = (primId, e) => {
    setDetailModalOpen(true);
    setSelectLogId(primId);
    const newUrl = `?logId=${primId}`
    window.history.pushState({}, null, newUrl);
  };
  const closeDetailModal = () => {
    setDetailModalOpen(false);
    const newUrl = `?`
    window.history.pushState({}, null, newUrl);
  };

  const generateReport = useReactToPrint({
    content: () => componentPDF.current,
    documentTitle: "Inventory List",
    });/*() => {
    if(auditLogData){
      const action = "generateAuditLog";
      const actionData = { action };
      axios.post(domain, actionData)
          .then(response => {
              // Handle successful response, if needed
               // Response.data will contain the file URL
              const fileUrl = response.data.fileUrl;
              const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
              const newWindow = window.open(formattedFileUrl, '_blank');

              // Ensure the new window is fully loaded before attempting to print
              newWindow.onload = () => {
                  newWindow.print();
              };
          })
          .catch(error => {
              // Handle error, if needed
              console.error('Error generating report:', error);
          });
    }
}*/console.log(selectedFilterLabel);
  return (
    <div className='px-5 pb-5'>
      <div className='row' style={{display: 'none'}}>
        <div className={`col-5 d-flex justify-content-start ${dst.tabs}`}>
        {tabs.map((tab) => (
              <button
                key={tab.id}
                className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.label}
              </button>
            ))}
          </div>
        </div>
        <div className={`${dst.con} p-4`}>
        {activeTab === 'transactions' ? (
          <div className={`${dst.row} mt-1`}>
            <div className='row d-flex align-items-center position-relative'>
                <div className='col-4 d-flex align-items-center mb-3'>
                <i className='fa-solid fa-receipt'></i>
                <h6 className='fw-bold fs-5 m-0 mx-2'>Transactions</h6>
                </div>
                <div className='col-8 d-flex justify-content-end'>
                <button className={`${dst.tableBtn} me-2`} 
                    style={{left: '0'}}type='button' onClick={generateReport}>
                  <i className={`fa-regular fa-file ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Generate Report</span>
                  </button>
                {
                selectedFilterLabel.date_time_filter !== '' || 
                selectedFilterLabel.date_time !== 'Date and Time' || 
                selectedFilterLabel.email !== 'Email' ||
                selectedFilterLabel.type !== 'Transaction Type' || 
                selectedFilterLabel.branch !== 'Branch Location' || 
                selectedFilterLabel.amount !== 'Description' ||
                selectedFilterLabel.pdl !== 'Involved PDL' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
                
              </div>
              <div className='col-12 mx-auto'>
              <table
                  className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    fontSize: 'small',
                    overflow: 'auto',
                    fontSize: '14px',
                  }}
                >
                <thead>
                  <tr style={{fontSize: '16px'}}>
                    <th scope="col">
                      Transaction ID
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.date_time_filter && `${selectedFilterLabel.date_time_filter} - `} 
                      {selectedFilterLabel.date_time && ((selectedFilterLabel.date_time))}
                      {!selectMultipleEnabled &&(<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('date_time')}></i>
                       <i
                        className="fa-solid fa-calendar-alt ms-2"
                        style={{ cursor: 'pointer' }}
                        onClick={() => toggleFilterDropdown('date_time_filter')}
                      ></i>
                      {renderFilterDropdown('date_time')}
                      {renderFilterDropdown('date_time_filter')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.email}
                      {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('email')}></i>
                      {renderFilterDropdown('email')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.type}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('type')}></i>
                      {renderFilterDropdown('type')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.branch}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('branch')}></i>
                      {renderFilterDropdown('branch')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.amount}
                    {!selectMultipleEnabled && 
                    (<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('amount')}></i>
                      {renderFilterDropdown('amount')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.pdl}
                    {!selectMultipleEnabled && 
                    (<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('pdl')}></i>
                      {renderFilterDropdown('pdl')}</>)}
                    </th>
                    <th scope="col" className={dst.exclude} >
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow.length != 0 ? (<>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const data = entriesToShow[index];
                    const hasData = data !== undefined;
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed

                    return (
                      <tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                      `}
                      style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                      >
                        <td>{hasData ? `T-${data.id}` : ''}</td>
                        <td>{hasData ? data.date_time : ''}</td>
                        <td>{hasData ? data.email : ''}</td>
                        <td>{hasData ? data.transaction_type : ''}</td>
                        <td>{hasData ? data.bjmp_branch : ''}</td>
                        <td>{hasData ? data.description : ''}</td>
                        <td>{hasData ? `PDL-${data.to_pdl}` : ''}</td>
                        <td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons} ${dst.exclude}`}>
                                <p onClick={(e) => openDetailModal(data.transaction_id, e)}>
                                  <i className={`fa-solid fa-eye fa-sm ${dst.iconHover}`}></i>
                                  <span className={dst.iconTooltip}>View</span>
                                </p>
                            </div>
                          )}
                        </td>
                      </tr>
                    );
                  })}</>) : (
                  <tr>
                    <td style={{ padding: '150px', textAlign: 'center' }} colSpan={6}>
                      Nothing to show
                    </td>
                  </tr>)}
                </tbody>
                </table>
                <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '1%', marginBottom: '5px'}}>
                  <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
                </div>
              </div>
            </div>
          </div>

          ) : (
          <div className={`${dst.row} mt-1`}>
            <div className='row d-flex align-items-center position-relative'>
            <div className='col-4 d-flex align-items-center mb-3'>
                <i className='fa-solid fa-pen-to-square'></i>
                <h6 className='fw-bold fs-5 m-0 mx-2'>Edit History</h6>
                </div>
                <div className='col-8 d-flex justify-content-end'>
                <button className={`${dst.tableBtn} me-2`} 
                    style={{left: '0'}}type='button' onClick={generateReport}>
                  <i className={`fa-regular fa-file ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Generate Report</span>
                  </button>
                {
                selectedFilterLabel.date_time !== 'Date and Time' || 
                selectedFilterLabel.email !== 'Email' ||
                selectedFilterLabel.type !== 'Transaction Type' || 
                selectedFilterLabel.amount !== 'Description' ||
                selectedFilterLabel.pdl !== 'Involved PDL' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
                
              </div>
            </div>
          </div>
              
            )}
        </div>
        {isDetailModalOpen && <AuditDetails onClose={closeDetailModal} logId={selectedLogId}/>}
        <div ref={componentPDF} style={{position: 'absolute', width: '100%', zIndex: '-99'}}>
          <div className='m-5'>
            <div className='row d-flex justify-content-start align-items-center'>
              <div className='col-1 justify-content-start'>
              <img className={dst.logo} src={`../logo/${logo}`} alt="BJMP Logo" 
              style={{width: '80px', height: '80px'}}/>
              </div>
              <div className='col-11 text-start'>
                <div className='mx-4'>
                  <h5 className='fw-bold'>Bureau of Jail Management and Penology Region III</h5>
                  <h6 className='fw-bold'>Transaction List</h6>
                  <p>Date Today: {formattedDate} {formattedTime}</p>
                </div>
                
              </div>
              
            </div>
          
          <table
              className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                style={{
                  borderCollapse: 'collapse',
                  color: '#48505E',
                  fontSize: 'small',
                  overflow: 'auto',
                  fontSize: '14px',
                  border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                }}
              >
              <thead>
                <tr style={{fontSize: '13px'}}>
                <th scope="col">
                      Transaction ID
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.date_time_filter}
                    {selectedFilterLabel.date_time}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.email}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.type}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.amount}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.pdl}
                    </th>
                </tr>
              </thead>
              <tbody className={dst.shrinkMedia}>
              {entriesToShow.map((t, index) => (
                  <tr
                    key={index}
                    className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}`}
                    
                    
                  >
                    
                    <td>{t ? `T-${t.id}` : ''}</td>
                    <td>
                      {t ? (
                        <>
                          {t.date_time}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {t ? (
                        <>
                          {t.email}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    
                    <td>
                      {t ? (
                        <>
                          {t.transaction_type}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                  <td>
                      {t ? (
                        <>
                          {t.description}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {t ? (
                        <>
                          PDL-{t.to_pdl}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                  </tr>
                ))}

              </tbody>
              </table>
          </div>
       
       </div>
    
    
    
    </div>  
  );
}

