import React, {useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import bjmplogo from '../../assets/bjmp-logo.png';
import EditPDL from '../modals/EditPDL';
import Fingerprint from '../modals/FingerprintIDScan';
import axios from 'axios';
import domain from '../../constants/domain';

function PrisProfImg({userData, imageSrc, getUser}) {
  

  const [isEditPDLModalOpen, setEditPDLModalOpen] = useState(false);
  const [isFingerprintIDModalOpen, setFingerprintIDModalOpen] = useState(false);
  const [reqFPData, setReqFPData] = useState({});
  const [fingerprintData, setFingerprintData] = useState('');
  const openEditPDLModal = () => {
    setEditPDLModalOpen(true);
  };
  const closeEditPDLModal = () => {
    setEditPDLModalOpen(false);
  };

  const openFingerprintModal = (id, ln, fn, mn, of, e) => {
    setReqFPData({
      userID: id,
      lastName: ln,
      firstName: fn,
      middleName: mn,
      gender: of,
    })
    setFingerprintIDModalOpen(true);
  }
  const closeFingerprintModal = () => {
    setFingerprintIDModalOpen(false);
  }
  
  const generateReport = () => {
    if(userData){
      const action = "generatePDLReportOnce";
      const formData = {...userData, action};
      console.log(formData);
      axios.post(domain, formData)
          .then(response => {
              // Handle successful response, if needed
              console.log('Report generated successfully!');
               // Response.data will contain the file URL
               const fileUrl = response.data.fileUrl;
                const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
              // Open the PDF in a new tab
              const newWindow = window.open(formattedFileUrl, '_blank');

                // Ensure the new window is fully loaded before attempting to print
                newWindow.onload = () => {
                    newWindow.print();
                };
          })
          .catch(error => {
              // Handle error, if needed
              console.error('Error generating report:', error);
          });
    }
}


  return (<>
    <div className={`p-3 ${dst.profileCard}`} >
      {userData.map((data, index) =>
          (<>
          
            
              <div key={index} className="row-1 p-4">
              <div className="col text-center">
                <div className={dst.profileImageContainer}>
                <img src={`../images/pdls/${imageSrc}`} 
                     style={{
                      width: '300px', // Set a fixed width
                      height: '300px', // Set a fixed height
                      objectFit: 'cover',
                      borderRadius: '50%' // Maintain aspect ratio and cover the container
                       // Use '50%' for a circular shape or '100px' for a rounded square
                    }} alt="logo" />
                </div>
                
              </div>
              
              <div className="col text-center mt-3">
                <p className="fw-bold" style={{ fontSize: 'large', color: '#48505E', marginBottom: '0'  }}>{data ? (
                    <>
                      {data.pdl_lastname && data.pdl_lastname.toUpperCase()},{' '}
                      {data.pdl_firstname} {data.pdl_middlename}
                    </>
                  ) : (
                    ''
                  )}</p>
                <p className="text-center" style={{ fontSize: 'small', color: '#48505E', marginBottom: '10px' }}>
                  PDL-{data.id}, Age: {data.age}, Gender: {data.gender === 'Other' ? data.other_gender : data.gender}</p>
              </div>
              <div className="col text-center">
                
                
                {!data.fingerprint_id && (<i className="fa-solid fa-fingerprint fa-xl ms-2" onClick={(e) => openFingerprintModal(data.id, 
                  data.pdl_lastname, 
                  data.pdl_firstname,
                  data.pdl_middlename,
                  data.gender,
                  e)} type="button" style={{ color: '#0F0F0F' }}></i>)}
                <i className="fa-solid fa-print fa-xl ms-2" type="button" style={{ color: '#0F0F0F' }} onClick={generateReport}></i>
              </div>
              <hr />
              <div className="col">
                <p className="text-center" style={{ fontSize: 'small', color: '#48505E' }}>Fingerprint ID: {data.fingerprint_id ? (<>Exists</>) : (<>None yet</>)}</p>
                <p className="position-absolute bottom-0 start-50 translate-middle-x fw-semibold">Balance: PHP 
                                        {data.id != null && (<>
                                        {data.formatted_balance}
                                        </>)}
                                        {data.id == null && (<>
                                        XX.XX
                                        </>)}</p>
              </div>
            </div>
            </>))}
            
            
          </div>
          {isFingerprintIDModalOpen && <Fingerprint onClose={closeFingerprintModal} typeOfFinScan={'create'} pdlData={reqFPData} setFingerprintData={setFingerprintData} getUser={getUser}/>}
            
          </>
  );
}

export default PrisProfImg;
