import React from 'react';
import dst from '../../../styles/DashboardStyle.module.css';

function BlueCard({ totalSpend, duration, onDurationChange }) {
    return (
        <div className={`${dst.blueCard} p-4 text-start position-relative`}>
          {/* Move the dropdown and span to the top right */}
          <div className="position-absolute top-0 end-0 p-2">
            <select
              id="durationSelector"
              className="form-select form-select-sm"
              value={duration}
              onChange={(e) => onDurationChange(e.target.value)}
            >
              <option value="All Time">All Time</option>
              <option value="Weekly">Weekly</option>
              <option value="Monthly">Monthly</option>
            </select>
            {/* Move the span for the specified week to the top right */}

          </div>

          <i className='fa-solid fa-comments-dollar fa-3x'></i>
          <p className="fw-bold mt-3 h4">
            {totalSpend ? <>PHP {parseFloat(totalSpend).toFixed(2)}</> : <>Nothing yet...</>}
          </p>
          <p className="m-0" style={{fontSize: '14px'}}>PDL Spending Total</p>
          {duration === 'Weekly' && (
              <p className=" m-0" style={{fontSize: '12px'}}>
                Week: {/* Add the logic to display the specified week */}
              </p>
            )}
          <div className="mt-4">
            {/* Rest of the content */}
          </div>
        </div>
      );
}

export default BlueCard;
