import React from 'react';
import dst from '../../../styles/DashboardStyle.module.css';

function GreenCard({ moneyInCirculation }) {
    return (
        <div className={`${dst.greenCard} p-4 text-start position-relative`}>
          {/* Move the dropdown and span to the top right */}
        
          <i className='fa-solid fa-money-bill fa-3x'></i>
          <p className="fw-bold mt-3 h4">
            {moneyInCirculation ? <>PHP {parseFloat(moneyInCirculation).toFixed(2)}</> : <>Nothing yet...</>}
          </p>
          <p className="m-0" style={{fontSize: '14px'}}>Money in Circulation</p>
          <div className="mt-4">
            {/* Rest of the content */}
          </div>
        </div>
      );
}

export default GreenCard;
