import React, {useState, useEffect} from 'react';
import dst from '../../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../../constants/domain';
function LargestSpenderCard({duration, onDurationChange}) {

  const [tableData, setTableData] = useState([]);
  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getLargestSpender`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }
  
  return (
    <div className={`${dst.secondCard} p-3 position-relative`}>
      <div className='row d-flex align-items-center'>
      <div className="position-absolute mt-1 top-0 end-0 p-2 d-flex justify-content-end" 
          style={{ width: '35%' }}>
            <select
                id="durationSelector"
                className="form-select form-select-sm"
                value={duration}
                onChange={(e) => onDurationChange(e.target.value)}
              >
                <option value="All Time">All Time</option>
                <option value="Weekly">Weekly</option>
                <option value="Monthly">Monthly</option>
              </select>
            {/* Move the span for the specified week to the top right */}

          </div>
        <div className='col-11 d-flex align-items-center mb-3'>
          
          <i className='fa-solid fa-dollar-sign'></i>
          <h6 className='fw-bold fs-5 m-0 mx-2'>Highest Spender</h6>
        </div>
 
          <div className='col-12 mx-auto '>
            <table
              className={`${dst.cleanTable} w-100`}
              style={{
                borderCollapse: 'collapse',
                color: '#48505E',
                fontSize: 'small',
                overflow: 'auto',
                fontSize: '12px',
              }}
            >
              <thead>
                <tr>
                  <th scope='col'>Name</th>
                  <th scope='col'>Spending Total</th>
                </tr>
              </thead>
              <tbody>
              {Array.from({ length: 10 }, (_, index) => {
                  const data = tableData[index];
                  const hasData = data !== undefined;
                  const cellStyle = hasData ? { padding: '16px' } : { padding: '22px' }; // Adjust the padding value as needed
  
                  return (
                    <tr key={index} className={index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}>
                      <td style={cellStyle}>
                        {data ? (
                          <>
                            {data.pdl_lastname && data.pdl_lastname.toUpperCase()},{' '}
                            {data.pdl_firstname} {data.pdl_middlename}
                          </>
                        ) : (
                          ''
                        )}
                      </td>
                      <td style={cellStyle}>
                        {data ? (`PHP ${data.formatted_alltime_spend}`) : ''}
                      </td>
                    </tr>
                  );
                })}
                {tableData.length === 0 && (
                  <tr>
                    <td colSpan={2} style={{padding: '210px 0', textAlign: 'center'}}>Nothing to show</td>
                  </tr>
                )}
              </tbody>
            </table>
        </div>
      </div>
    </div>
  );
}

export default LargestSpenderCard;
