import React from 'react';
import dst from '../../../styles/DashboardStyle.module.css';

function YellowCard({ transactionsCount, totalTransactionsCound }) {
    return (
      <div className={`${dst.yellowCard} p-4 text-start position-relative`}>
      <i className='fa-solid fa-receipt fa-3x'></i>
      <div className='row mt-3 mb-2'>
        <div className='col'>
            <p className="fw-bold m-0 h4">
              {totalTransactionsCound ? <>{totalTransactionsCound}</> : <>0</>}</p>
            <p className="m-0" style={{ 
              fontSize: '10px'
              }}>All Time</p>
        </div>
        <div className='col'>
            <div>
              <p className="fw-bold m-0 h4">
                {transactionsCount ? <>{transactionsCount}</> : <>0</>}</p>
              <p className="m-0" style={{ 
                fontSize: '10px' }}>This Week</p>
            </div>
          </div>
      </div>
      <p className="m-0" style={{fontSize: '14px'}}>Total Transactions</p>
      
    </div>
      );
}
export default YellowCard;
