import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';
import BlueCard from './subsections/PDLSpendingTotal';
import YellowCard from './subsections/PDLTotalTransactions';
import RedCard from './subsections/MostPopularProduct';
import LargestSpenderCard from './subsections/LargestSpender';
import SpendingTotal from './subsections/SpendingTotal';
import TotalTransactions from './subsections/TotalTransactions';
import PDLsWithLowBalance from './subsections/LowBalance';
import VulnerableRatio from './subsections/VulnerableRatio';
import GreenCard from './subsections/MoneyInCirculation';
import PopularItems from './subsections/PopularItems';
import LowCount from './subsections/ItemsWithLowCount';


function OverallDetails() {
  const [isVisible, setVisibility] = useState(true)
  const [tableData, setTableData] = useState([]);
  const [selectedDurationBlueCard, setSelectedDurationBlueCard] = useState('All Time');
  const [selectedDurationRedCard, setSelectedDurationRedCard] = useState('All Time');
  const [selectedDurationLargestSpender, setSelectedDurationLargestSpender] = useState('All Time');
  const [selectedDurationSpending, setSelectedDurationSpending] = useState('Weekly');
  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getOverallDetails`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }

  // Use draggable and droppable hooks for each card

  const toggleVisibility = () => {
    setVisibility((prevIsVisible) => !prevIsVisible);
  };

  const handleDurationChange = (duration, cardType) => {
    if (cardType === 'blueCard') {
      setSelectedDurationBlueCard(duration);
    } else if (cardType === 'redCard') {
      setSelectedDurationRedCard(duration);
    } else if (cardType === 'largestSpender') {
      setSelectedDurationLargestSpender(duration);
    } else if (cardType === 'spendingTotal'){
      setSelectedDurationSpending(duration);
    }
    
  };

 // Custom hook to make a card draggabl



  return (
    <div className={`m-4 mx-5`}>
      <div className='row d-flex align-items-center'>
        <div className='col-11 d-flex align-items-center'>
          <i className='fa-solid fa-chart-simple'></i>
          <h6 className="fw-bold fs-5 m-0 mx-3">Overview</h6>
        </div>
        <div className='col-1 d-flex justify-content-center align-items-center'>
          
        </div>
      </div>
      <hr></hr>
      <div className='row'>
        <div className='col-12'>
          <div className='row'>
            <div className='col-3'>
              <BlueCard
                totalSpend={tableData.total_weekly_spend}
                duration={selectedDurationBlueCard}
                onDurationChange={(duration) => handleDurationChange(duration, 'blueCard')}
              />
            </div>
            <div className='col-3'>
              <YellowCard
                transactionsCount={tableData.transactions_count}
                totalTransactionsCound={tableData.transactions_count_alltime}
              />
            </div>
            <div className='col-3'>
              <RedCard
                popularItem={tableData.most_popular_item}
                duration={selectedDurationRedCard}
                onDurationChange={(duration) => handleDurationChange(duration, 'redCard')}
              />
            </div>
            <div className='col-3'>
              <GreenCard
                moneyInCirculation={tableData.money_in_circulation}
                duration={selectedDurationBlueCard}
                onDurationChange={(duration) => handleDurationChange(duration, 'blueCard')}
              />
            </div>
          </div>
          <div className='row mt-2'>
              <div className='col-6'>
                <SpendingTotal />
              </div>
              <div className='col-6'>
                <TotalTransactions />
              </div>
          </div>
        </div>
      </div>
      <div className='row d-flex align-items-center mt-5'>
      <div className='col-11 d-flex align-items-center'>
          <i className='fa-solid fa-handcuffs'></i>
          <h6 className="fw-bold fs-5 m-0 mx-3">PDL Overview</h6>
        </div>
      </div>
      <hr></hr>
      <div className='row'>
        <div className='col-3'>
            <PDLsWithLowBalance />
        </div>
        <div className='col-3'>
          <LargestSpenderCard 
          duration={selectedDurationLargestSpender}
          onDurationChange={(duration) => handleDurationChange(duration, 'largestSpender')}/>
        
        </div>
        <div className='col-6'>
            <VulnerableRatio vulnerableRatio={tableData.vulnerable_ratio}/>
        </div>
      </div>
      <div className='row d-flex align-items-center mt-5'>
      <div className='col-11 d-flex align-items-center'>
          <i className='fa-solid fa-warehouse'></i>
          <h6 className="fw-bold fs-5 m-0 mx-3">Inventory Overview</h6>
        </div>
      </div>
      <hr></hr>
      <div className='row'>
        <div className='col-6'>
          <PopularItems />
        </div>
        <div className='col-6'>
          <LowCount />
        </div>
      </div>
    </div>
  );
}
export default OverallDetails;