import React, {useState, useEffect} from 'react';
import dst from '../../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../../constants/domain';

export default function ItemContainer({
  item, index, qtyState, onQtyContainerClick, openSuccessModal,
posElements}) {
    const [imageSrc, setImageSrc] = useState(null);
    const [quantity, setQuantity] = useState(0);
    const [itemWithQty, setItemWithQty] = useState({...item,
      quantity: 0,
      action: 'addPOSElement',
      email: localStorage.getItem('email')});
    const [errorMessage, setErrorMessage] = useState('');

    useEffect(() => {
      setImageSrc(item.stock_image.split('/').pop());
      setQuantity(0); // Reset quantity to 0 when item changes
    }, [item]);

    const [circleStyle, setCircleStyle] = useState({
        left: 0,
        top: 0,
        transform: 'translate(-50%, -50%) scale(0)',
      });
      const [lastCursorPosition, setLastCursorPosition] = useState({
        left: 0,
        top: 0,
      });

    
      const handleMouseMove = (e) => {
        const rect = e.currentTarget.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;
    
        setCircleStyle({
          left: `${Math.min(rect.width, Math.max(0, x))}px`,
          top: `${Math.min(rect.height, Math.max(0, y))}px`,
          transform: 'translate(-50%, -50%) scale(2)',
        });
    
        setLastCursorPosition({
          left: e.clientX,
          top: e.clientY,
        });
      };
    
      const handleMouseLeave = () => {
        setCircleStyle({
          left: `${lastCursorPosition.left}px`,
          top: `${lastCursorPosition.top}px`,
          transform: 'translate(-50%, -50%) scale(0)',
        });
      };
    
      const handleClick = () => {
        setCircleStyle({
          left: '50%',
          top: '50%',
          transform: 'translate(-50%, -50%) scale(20)',
          transition: 'all .5s ease'
        });
        onQtyContainerClick(index); // Call the callback to handle toggling qtyState
      };

      const removeQty = () => {
        setCircleStyle({
          left: `${lastCursorPosition.left}px`,
          top: `${lastCursorPosition.top}px`,
          transform: 'translate(-50%, -50%) scale(0)',
        });

        onQtyContainerClick(null);
        setQuantity(0);
      }
      const handleQuantityChange = (e) => {
        const inputQuantity = parseInt(e.target.value, 10);
        if (!isNaN(inputQuantity) && inputQuantity >= 0 && inputQuantity <= item.remainingStock) {
          setQuantity(inputQuantity);
          setItemWithQty((values) => ({...values, [e.target.name]: parseInt(e.target.value)}))
        }
      };
      
      const handleSubmit = async (e) => {
        e.preventDefault();
        const {quantity} = itemWithQty;
        if(!quantity || parseInt(quantity) == 0){
          setErrorMessage('Please enter a valid amount');
          return;
        }
        const itemIdExists = posElements.some(s => s.item_id === item?.id);
        if (itemIdExists) {
          setErrorMessage('Item with the same ID already exists.');
          return;
        }
        else{
          setErrorMessage('');
          axios.post(domain, itemWithQty).then(
            response => {
              if(response.data){
                openSuccessModal();
                removeQty();
              }
            }
          );
        }

      }

      return (
      <>
        {!qtyState ? (<div
          className={dst.itemContainer}
          onMouseMove={handleMouseMove}
          onMouseLeave={handleMouseLeave}
          onClick={handleClick}
          
        >
          <span style={{backgroundColor: '#449600', right: '0'}}>PHP {item.formatted_price}</span>
          <span style={{backgroundColor: '#10069f', left: '0'}}>Stock: {item.remainingStock}</span>
          <div className='row'>
            <div className='col-12'>
            <img
                src={`../images/stocks/${imageSrc}`}
                style={{
                  width: '100px',
                  height: '100px',
                  objectFit: 'cover', // Ensure the image covers the container
                  borderRadius: '100px',
                }}
                alt="stock"
              />
            </div>
          </div>
          <div className='row'>
            <div className='col-12 text-center' style={{ fontSize: '9px' }}>
            {item.category}
            </div>
          </div>
          <div className='row'>
            <div className='col-12 text-center' style={{ fontSize: '12px' }}>
            {item.prodType} - {item.prodName}
            </div>
          </div>
          <div className={dst.cursorCircle} style={circleStyle}></div>
        </div>) : (
        <div className={`${dst.selectQty} d-flex align-items-center`}>
            <div>
                <div className='row'>
                    <div className='col-12'>
                        <label>{item.remainingStock != 0 ? 'Enter Quantity' : ''}</label>
                          <input
                            type={item.remainingStock != 0 ? 'number' : 'text'}
                            placeholder='Input quantity'
                            name='quantity'
                            id='quantity'
                            value={item.remainingStock != 0 ? quantity : 'Out of Stock'}
                            onChange={handleQuantityChange}
                            disabled={item.remainingStock == 0 ? 'disabled' : ''}
                          />
                    </div>
                    <p style={{color: 'red', fontSize: '8px', margin: '0 0 5px 0', cursor: 'default'}}>{errorMessage}</p>
                </div>
                <div className='row'>
                    <div className={`col-${item.remainingStock == 0 ? '12' : '6'}`}>
                        <button onClick={removeQty}>Cancel</button>
                    </div>
                    {item.remainingStock != 0 ? (<div className='col-6'>
                        <button onClick={handleSubmit} >Enter</button>
                    </div>): ''}
                </div>
            </div>
        </div>)}
        </>
      );
  }
