import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import axios from 'axios';
import domain from '../../constants/domain';

function AuditDetails({onClose, logId}) {
  const [logData, setLogData] = useState({});
  const [receiptLink, setReceiptLink] = useState('');
  useEffect(() => {
    getLog();
    
  }, [])
  useEffect(() => {
    const receiptUrl = logData.transaction_path;
    if (receiptUrl) {
      const formattedUrl = receiptUrl.substring(receiptUrl.indexOf('/receipts/'));
      setReceiptLink(formattedUrl);
    }
  }, [logData]);

  function getLog(){
    axios.get(`${domain}/index.php?action=displaySpecificAudit&logId=${logId}`)
    .then(function(response){
        setLogData(response.data[0]);


    }).catch(error => console.error('Error getting data: ', error));
  }

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onClose(); // Close the modal
      }
    };

  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '600px'}}>
        <div className={`${dst['modal-header']}`}>
        <div className='row w-100'>
          <div className='col-10 d-flex align-items-center'>
            <i className='fa-solid fa-receipt'></i>
            <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Log Details</h6>
          </div>
        <div className='col-2 d-flex justify-content-end align-items-center'>
          <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
        </div>
      </div>
      </div>
      <div className={dst['modal-body']}>
        <div className='row'>
          <div className='col-6'>
            <iframe
              title="PDF Viewer"
              width="100%"
              height="400px"
              
              src={receiptLink}
            ></iframe>
          </div>
          <div className={`col-6 ${dst.userdetails} p-3`}>
            <div className="row align-items-start text-start mb-2">
                  <label htmlFor="time" >
                    Date and Time: 
                  </label>
                  <p htmlFor="time">
                    {logData.date_time}
                  </p>
              </div>
              <div className="row align-items-start text-start mb-2">
                  <label htmlFor="time" >
                    Email: 
                  </label>
                  <p htmlFor="time">
                    {logData.email}
                  </p>
              </div>
              <div className="row align-items-start text-start mb-2">
                  <label htmlFor="time" >
                    Description: 
                  </label>
                  <p htmlFor="time">
                    {logData.description}
                  </p>
              </div>
              <div className="row align-items-start text-start mb-2">
                  <label htmlFor="time" >
                    Involed PDL ID: 
                  </label>
                  <p htmlFor="time">
                    PDL-{logData.to_pdl}
                  </p>
              </div>
              {logData.transaction_type === 'Load' ? (
                <div className="row align-items-start text-start mb-2">
                  <label htmlFor="time" >
                    Lender Name: 
                  </label>
                  <p htmlFor="time">
                    {logData.lender_name}
                  </p>
                </div>

              ) : (<></>)}
          </div>
        </div>

      </div>
        </div>
    </div>
  );
}

export default AuditDetails;
