import React, {useEffect, useState} from 'react';
import dst from '../../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../../constants/domain';

function PDLsWithLowBalance() {
  const [isVisible, setVisibility] = useState(true)
  const [tableData, setTableData] = useState([]);
  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getLowBalanceData`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }

  const toggleVisibility = () => {
    setVisibility((prevIsVisible) => !prevIsVisible);
  };
  return (
    <div className={`${dst.secondCard} p-3 position-relative`}>
      <div className='row d-flex align-items-center'>
        <div className='col-12 d-flex align-items-center mb-3'>
          <i className='fa-solid fa-wallet'></i>
          <h6 className='fw-bold fs-5 m-0 mx-2'>PDLs with Low Balance</h6>
        </div>

          <div className='col-12 mx-auto '>
            <table
              className={`${dst.cleanTable} w-100`}
              style={{
                borderCollapse: 'collapse',
                color: '#48505E',
                fontSize: 'small',
                overflow: 'auto',
                fontSize: '12px',
              }}
            >
              <thead>
              <tr>
                <th scope="col" >
                  Name
                </th>
                <th scope="col" >
                  Balance
                </th>
              </tr>
            </thead>
            <tbody>
            {tableData.length != 0 && Array.from({ length: 10 }, (_, index) => {
                const data = tableData[index];
                const hasData = data != undefined;
                const cellStyle = hasData ? {} : { padding: '25px' }; // Adjust the padding value as needed

                return (
                  <tr key={index} className={index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}>
                    <td style={cellStyle}>
                      {data ? (
                        <>
                          {data.pdl_lastname && data.pdl_lastname.toUpperCase()},{' '}
                          {data.pdl_firstname} {data.pdl_middlename}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td style={cellStyle}>
                       {data ? (data.formatted_balance != 0 ? `PHP ${data.formatted_balance}` : 'No Balance') : ''}
                    </td>
                  </tr>
                );
              })}
              {tableData.length === 0 && (
              <tr>
                <td style={{padding: '210px 0', textAlign: 'center'}} colSpan={2}>Nothing to show</td>
              </tr>
              )}
            </tbody>
            </table>
        </div>
      </div>
    </div>
  );
}

export default PDLsWithLowBalance;