import React, { useState, useRef,useEffect  } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import Fingerprint from './FingerprintIDScan';
import axios from 'axios';
import domain from '../../constants/domain';
import {v4 as uuidv4} from 'uuid';

function PdlModal({ onClose }) {
  const [fingerprintID, setFingerprintID] = useState('');
  const [formData, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState("");
  const [imageSrc, setImageSrc] = useState(null);
  const email = localStorage.getItem("email");
  const [selectMaritalStatus, setSelectMaritalStatus] = useState('');
  const fileInputRef = useRef(null);

  useEffect(() => {
  }, [imageSrc]);

  const handleSvgClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click(); // Trigger click on the file input
    }
  };
  const action = 'createPDL';
  
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;

    if (e.target.type === 'file') {
      const reader = new FileReader();
  
      reader.onload = (e) => {
        setImageSrc(e.target.result);
        setFormData(values => ({ ...values, action, pdlimage: value })); // Use e.target.files[0]
      };
      if (value) {
        reader.readAsDataURL(value);
    
      }
      return;
    }

    else if (name === 'gender') {
      // Handle the "Other" option for gender
      if (value === 'Other') {
        setFormData((values) => ({ ...values, action, gender: 'Other' }));
      } else {
        setFormData((values) => ({ ...values, action, gender: value, otherGender: '' }));
      }
      return;
    } else if (name === 'otherGender') {
      setFormData((values) => ({ ...values, action, otherGender: value }));
      return;
    }
    else {
      setFormData(values => ({ ...values, action, email, [name]: value}));
      return;
    }
    
};
  
  const handleSubmit = async (e) => {
    e.preventDefault();

    const { first_name, middle_name, last_name, pdlNo, age, gender, otherGender, cellNo, pdlimage, condition } = formData;
    const fileUp = new FormData();
    fileUp.append('pdl_image', pdlimage);
    fileUp.append('pdlNo', pdlNo);
    fileUp.append('action', 'uploadPDLImage');
    if(!condition){
      setFormData(values => ({ ...values, action, email, condition: 'None'}));
    }
    if (!first_name || !middle_name || !last_name || !pdlNo || !age || !cellNo || !gender || (gender == 'Other' && !otherGender)) {
      setErrorMessage('Please fill in all required fields!');
      return;
    } 
    else {
        axios.post(domain, formData).then(response => {
          if(response.data.pdlIDDetect){
            setErrorMessage(response.data.message);
          }
          else{
            setErrorMessage('');
            axios.request({
              method: 'POST',
              url: domain,
              headers: {
                'Content-Type': 'multipart/form-data'
              },
              data: fileUp
            }).then(response => {
              if (response.data) {
                onClose();
              }
            }).catch(error => console.error('Error handling file:', error));
          }
        }).catch(error => console.error("Error handling file: ", error));
      }
  };

 
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'addPdl') {
      onClose(); // Close the modal
    }
  };


  const rectWidth = imageSrc ? 162 : 502; // Adjust width based on imageSrc
  const rectHeight = imageSrc ? 162 : 162; // Adjust height based on imageSrc


  return (
<div className={dst.modal} onClick={handleBackdropClick} id="addPdl" tabIndex="-1">
  <div className={`${dst['modal-content']}`} style={{ width: '650px' }}>
    <div className={`${dst['modal-header']}`}>
      <div className='row w-100'>
        <div className='col-10 d-flex align-items-center'>
          <i className='fa-solid fa-plus'></i>
          <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Create PDL Entry</h6>
        </div>
        <div className='col-2 d-flex justify-content-end align-items-center'>
          <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
        </div>
      </div>
    </div>
    <div className={dst['modal-body']}>
      <div className="row d-flex justify-content-center" style={{ display: 'flex' }}>
        <div className="col-12 mb-4" >
        <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {imageSrc && (
                        <image
                          x="20%"
                          y="20%"
                          xlinkHref={imageSrc}
                          width="100"
                          height="100"
                        />
                      )}
                      {!imageSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="14">
                        Upload image here
                      </text>
                      )}     
                  </svg>
          <input
            type="file"
            name='pdlimage'
            id='pdlimage'
            ref={fileInputRef}
            style={{ display: 'none' }}
            accept="image/*"
            onChange={handleChange}
          />
        </div>
      </div>
      <hr></hr>
      <div className="row g-3 align-items-center mb-2">
        <div className="col-12 text-center">
          <p style={{margin: '0'}}><b>Basic Info</b></p>
        </div>
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-6">
          <label htmlFor="first_name" className="col-form-label">First Name:</label>
          <input type="text" id="first_name" name="first_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter first name" onChange={handleChange} />
        </div>
        <div className="col-6">
          <label htmlFor="last_name" className="col-form-label">Last Name:</label>
          <input type="text" id="last_name" name="last_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter last name" onChange={handleChange} />
        </div>
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-6">
          <label htmlFor="middle_name" className="col-form-label">Middle Name:</label>
          <input type="text" id="middle_name" name="middle_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter middle name" onChange={handleChange} />
        </div>
        <div className="col-6">
          <label htmlFor="pdlNo" className="col-form-label">PDL No:</label>
          <input type="text" id="pdlNo" name="pdlNo" className="form-control" maxLength={6} style={{ boxShadow: 'none' }} placeholder="Enter PDL No" onChange={handleChange} />
        </div>
      </div>
      <div className="row g-3 align-items-center">
        <div className="col-4">
          <label htmlFor="age" className="col-form-label">Age:</label>
          <input type="number" id="age" name="age" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter age" onChange={handleChange} />
        </div>
        <div className="col-8" style={{ display: 'flex', flexDirection: 'column' }}>
        <label className="col-form-label">Gender:</label>
          <div className='row'>
            
            <div className={`col-${formData.gender === 'Other' ? '2':'4'} mt-2`}>
            <div className="form-check" style={{ display: 'flex' }}>
              <input
                          value="Male"
                          type="radio"
                          name="gender"
                          id="genderMale"
                          style={{ boxShadow: 'none', width: '12px', height: '18px' }} // Adjust the size as needed
                          onChange={handleChange}
                        />
                        <label htmlFor="genderMale" style={{ marginLeft: '10px', marginBottom: '0' }}>
                          Male
                        </label>
              </div>
            </div>
            <div className={`col-${formData.gender === 'Other' ? '2':'4'} mt-2`}>
            <div className="form-check" style={{ display: 'flex' }}>
              <input
                          value="Female"
                          type="radio"
                          name="gender"
                          id="genderFemale"
                          style={{ boxShadow: 'none', width: '12px', height: '18px' }} // Adjust the size as needed
                          onChange={handleChange}
                        />
                        <label htmlFor="genderFemale" style={{ marginLeft: '10px', marginBottom: '0' }}>
                          Female
                        </label>
              </div>
            </div>
            <div className={`col-${formData.gender === 'Other' ? '2':'4'} mt-2`}>
            <div className="form-check" style={{ display: 'flex' }}>
          <input
                      value="Other"
                      type="radio"
                      name="gender"
                      id="genderOther"
                      style={{ boxShadow: 'none', width: '12px', height: '18px' }} // Adjust the size as needed
                      onChange={handleChange}
                    />
                    <label htmlFor="genderOther" style={{ marginLeft: '10px', marginBottom: '0' }}>
                      Other
                    </label>
          </div>
               </div>
               {formData.gender === 'Other' && (
               <div className={`${formData.gender === 'Other' ? 'col-6': ''}`}>
                  <div className="form-check" style={{ display: 'flex' }}>
                    <input
                      type="text"
                      name="otherGender"
                      className="form-control"
                      style={{ boxShadow: 'none'}}
                      placeholder="Enter other gender"
                      onChange={handleChange}
                    />
                  </div>
                  </div>
                )}
          </div>
          
          
        </div>
        
      </div>
      <div className="row g-3 align-items-center">
            <div className="col-6">
                  <label htmlFor="cellNo" className="col-form-label">Cell No.:</label>
                  <input type="text" id="cellNo" name="cellNo" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter cell no." onChange={handleChange} />
            </div>
            <div className="col-6">
                  <label htmlFor="condition" className="col-form-label">Medical Condition (Leave blank if none):</label>
                  <input type="text" id="condition" name="condition" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter condition." onChange={handleChange} />
            </div>
          </div>
    </div>
    <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
    <div className={`${dst['modal-footer']} d-flex justify-content-end`} style={{ border: 'none', marginTop: 'auto' }}>
    
    <button type="button" className="btn" onClick={onClose}>
      Discard
    </button>
  <button
    type="button"
    onClick={handleSubmit}
    className="btn"
    style={{ backgroundColor: '#505050', color: '#fff' }}
  >
    Submit
  </button>
</div>


  </div>

</div>

  );
}

export default PdlModal;
