import React, { useCallback, useEffect, useState } from 'react';
import dst from '../styles/DashboardStyle.module.css';
import DashboardSidebar from '../components/navbars/DashboardSideNavbar';
import TopNavBar from '../components/navbars/TopNavBar';
import ItemCode from '../components/sections/EnterItemCode';
import ItemList from '../components/sections/ItemList';
import PriceDisplay from '../components/sections/PriceDisplay';
import CheckoutSummary from '../components/sections/CheckoutSummary';
import PDLDisplay from '../components/sections/EnterPDLID';
import { Helmet } from 'react-helmet-async';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import domain from '../constants/domain';
import ItemCheckoutList from '../components/sections/ItemCheckoutList';
import ItemSelection from '../components/sections/ItemSelection';


function PointOfSales({ isSidebarOpen, toggleSidebar}){
  const [itemData, setItemData] = useState([]);
  const [priceVal, setPriceVal] = useState([]);
  const history = useNavigate();
  const email = localStorage.getItem("email");
  const formattedPrices = priceVal.map(item => (parseFloat(item.formatted_price) * parseFloat(item.quantity)));
  const [toggleCheckOut, setToggleCheckout] = useState(true);
  const [posElements, setPosElements] = useState([]);
    const calculateMargin = () => {
        if (window.innerWidth <= 1368) {
          return isSidebarOpen ? '170px' : '78px';
        } else {
          return isSidebarOpen ? '250px' : '78px';
        }
      };
    const calculateCheckout = () => {
        if (window.innerWidth <= 1368) {
          return !toggleCheckOut ? '460px' : '0px';
        } else {
          return !toggleCheckOut ? '460px' : '0px';
        }
      };
      const [margin, setMargin] = useState(calculateMargin);
      const [checkout, setCheckout] = useState(calculateCheckout);
      
    const updateMargin = () => {
      setMargin(calculateMargin());
    };
    const updateCheckout = () => {
      setCheckout(calculateCheckout());
    }

    const toggleCheckoutSize = () => {
      setToggleCheckout(!toggleCheckOut);
    }

    useEffect(() => {
      // Check if user is logged in
      const token = localStorage.getItem('token');
      
      if (!token) {
        alert("User not logged in. Please login to continue");
        // If not logged in, redirect to login page
        history('/');
      }
    
      const handleResize = () => {
        updateMargin();
        updateCheckout();
      };
    
      window.addEventListener('resize', handleResize);
    
      // Update margin when sidebar state changes
      updateMargin();
      updateCheckout();
      
      return () => {
        window.removeEventListener('resize', handleResize);
      };
    }, [history, isSidebarOpen, toggleCheckOut]);

    useEffect(() => {
      getData();
      getStockData();
    }, []);

    function getData(){
      axios.get(`${domain}/index.php?action=getPOSElements&email=${localStorage.getItem('email')}`).then(response => {
        if(response.data){
          setPosElements(response.data);
        }
      }).catch(error => console.error('Error fetching pos data: ', error));
    }
    function getStockData(){
      axios.get(`${domain}/index.php?action=displayStock`)
      .then((res) => {
        setItemData([]);
        if (Array.isArray(res.data)) {
            setItemData(res.data);
            
        } else {
          console.error("API response is not an array:", res.data);
        }
      })
      .catch((error) => console.error("Error fetching Stock data:", error));
    }
      const contentStyle = {
        marginLeft: margin,
        marginRight: checkout,
        transition: 'margin .5s ease',
        backgroundColor: '#f9f9f9',
        height: '100vh',
        overflowY: 'auto',
        overflowX: 'hidden',
       };
    return (
        <div>
        <Helmet>
            <title>BJMP | Point Of Sales</title>
        </Helmet>
        <DashboardSidebar isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        <div style={contentStyle}>
            <TopNavBar />
            <div className='row'  style={{marginTop: '60px'}}>
              <ItemSelection getData={getData}
              posElements={posElements}
              getStockData={getStockData}
              itemData={itemData}/>
              <ItemCheckoutList 
                toggleCheckoutSize={toggleCheckoutSize} 
                toggleCheckOut={toggleCheckOut}
                posElements={posElements}
                getData={getData}
                getStockData={getStockData}/>
            </div>
          </div>
        </div>
    );
    
}
export default PointOfSales;