import React, { useState, useEffect, useRef } from 'react';
import dst from '../../../styles/DashboardStyle.module.css';
import { Pie } from "react-chartjs-2";
import Chart from "chart.js/auto";
import axios from 'axios';
import domain from '../../../constants/domain';


function VulnerableRatio({isSidebarOpen, vulnerableRatio}) {
  const BarChart = () => {
    const vR = vulnerableRatio ? [vulnerableRatio.senior_count, vulnerableRatio.pwd_count,
      vulnerableRatio.lgbt_count, vulnerableRatio.regular_count] : ''
    const data = {
      labels: ['Senior', 'PWD', 'LGBT' , 'Regular'],
      datasets: [
        {
          label: "PDL Count" ,
          data: vR,
          backgroundColor: ['#10069F',  '#E4002B' , '#FFDF00', '#449600'],
          barThickness: 10,
        },
      ],
    };
    const options = {
      
      maintainAspectRatio: false, // Allow chart to resize to container
      responsive: true, // Enable responsiveness
    };
    return (
      <div className={`${dst.fourthCard}`}>
      <div className='row d-flex align-items-center position-relative'>
          <div className='col-8'>
            <p className="fw-bold ms-3 p-1 fs-6" 
            style={{ position: 'relative', top: '5px', textAlign: 'left' }}>
              Vulnerable Ratio
            </p>
          </div>
          {vulnerableRatio ? (<div className={dst.chartContainer} style={{
            height: '483px'}}>
              <div style={{ height: '100%', border: '1px solid #ccc', padding: '40px 100px 80px 100px'}}>
                <Pie data={data} options={options} />
              </div>
        </div>) : (<div className="text-center p-3">
                <p className="text-muted mb-0">No data available for the selected period.</p>
              </div>)}
        </div>
    </div>
    );
  };


  return (
    <div className={`${isSidebarOpen ? 'sidebarOpen' : 'sidebarClosed'}`}>
      <div>
        <BarChart />
      </div>
    </div>
  );
}

export default VulnerableRatio;
