import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import { Line } from "react-chartjs-2";
import Chart from "chart.js/auto";
import axios from 'axios';
import domain from '../../constants/domain';
import Success from '../modals/SuccessfulAction';
function InventoryDetails({onClose, itemId}) {
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const day = String(currentDate.getDate()).padStart(2, '0');
  
  const formattedDate = `${year}-${month}-${day}`;

 
  const hours = String(currentDate.getHours()).padStart(2, '0');
  const minutes = String(currentDate.getMinutes()).padStart(2, '0');
  const formattedTime = `${hours}:${minutes}`;
  const [successModal, setSuccessModal] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [imageSrc, setImageSrc] = useState(null);
  const [stockData, setStockData] = useState({});
  
  const action = "updateInventoryStocks";
  useEffect (() => {
    getStock();
    }, [])

    function getStock(){
    axios.get(`${domain}/index.php?action=displaySpecificStock&itemId=${itemId}`)
    .then(function(response) {
        setStockData(response.data[0]);
          const imageUrl = response.data[0].stock_image;
          const fileName = imageUrl.split('/').pop();
          setImageSrc(fileName);
    }).catch(error => console.error('Error fetching stock data: ', error));
    }
  const [formData, setFormData] = useState({


  });


  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value
    setFormData((values) => ({...values, [name]: value}));
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const {expDate, qty} = formData;
    const updatedFormData = {
      stockID: stockData.id,
      prodType: stockData.prodType,
      prodName: stockData.prodName,
      dateToday: formattedDate,
      time: formattedTime,
      action: 'createInventoryInstance',
      ...formData
    }
    if(!expDate || !qty ){
      setErrorMessage('Please fill in all required fields!');
      return;
    }
    else{
      console.log(updatedFormData);
      axios.post(domain, updatedFormData)
      .then(response => {
        if(response.data.success){
          onClose();
        }
      })
    }
  }

  const openSuccessModal = () => {
    setSuccessModal(true);
  }
  const closeSuccessModal = () => {
    window.location.reload();
    setSuccessModal(false);
  }
  
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'exampleModal') {
      onClose(); // Close the modal
    }
  };

  const getCellStyleForType = (type) => {
    // Map type to corresponding CSS class
    switch (type) {
      case 'Overstocked':
        return dst.Overstocked;
      case 'Healthy':
        return dst.Healthy;
      case 'NearCritical':
        return dst.NearCritical;
      case 'AtCritical':
        return dst.AtCritical;
      case 'BelowCritical':
        return dst.BelowCritical;
      case 'NoStock':
        return dst.NoStock;
      default:
        return ''; // Use a default style if needed
    }
  };

  return (
    <div className={dst.modal} id="exampleModal" onClick={handleBackdropClick} tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '700px'}}>
        <div className={`${dst['modal-header']}`}>
            <div className='row w-100'>
              <div className='col-10 d-flex align-items-center'>
                <i className='fa-solid fa-boxes-packing'></i>
                <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Create Instance</h6>
              </div>
              <div className='col-2 d-flex justify-content-end align-items-center'>
                <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
              </div>
            </div>
          </div>
           <div className={dst['modal-body']}>
          <div className="row g-3 align-items-center mb-2">
            <div className='col-5' style={{borderRight: '1px solid #ccc'}}>
              <img src={`../images/stocks/${imageSrc}`} 
                style={{ width: '70%', 
                  minWidth: '76.5%',
                  maxWidth: '80%', 
                  height: '90%', 
                  minHeight: '70%',
                  maxHeight: '85%', 
                  borderRadius: '100px' }}
                alt="logo" />
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Item No.:</b> {stockData.id}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Product Type:</b> {stockData.prodType}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Product Name:</b> {stockData.prodName}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Price:</b> PHP{stockData.formatted_price}
                </label>
                <label htmlFor="itemNo" className="col-form-label">
                  <b>Category:</b> {stockData.category}
                </label>
                <label htmlFor="itemNo" className="col-form-label d-flex">
                  <b style={{marginTop: '1px'}}>Status:</b> 
                  {(parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) >= 21 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Overstocked')}`} style={{fontSize: '9px'}}>
                                  Overstocked
                                 </div>)
                                }
                                {(parseInt(stockData.remainingStock)- parseInt(stockData.criticalStock)) <= 20 &&
                                (parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) >= 11 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Healthy')} mx-2`} style={{fontSize: '9px'}}>
                                  Healthy
                              </div>)
                                }
                                {(parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) <= 10 &&
                                (parseInt(stockData.remainingStock) - parseInt(stockData.criticalStock)) >= 1 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NearCritical')} mx-2`} style={{fontSize: '9px'}}>
                                  Near Critical
                                </div>)
                                }
                                {parseInt(stockData.remainingStock) === parseInt(stockData.criticalStock) &&
                                parseInt(stockData.remainingStock) !== 0 && parseInt(stockData.criticalStock) !== 0 && 
                                (<div className={`${dst.typeCell} ${getCellStyleForType('AtCritical')} mx-2`} style={{fontSize: '9px'}}>
                                At Critical
                              </div>)
                                }
                                {parseInt(stockData.remainingStock) < parseInt(stockData.criticalStock) &&
                                parseInt(stockData.remainingStock) !== 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('BelowCritical')} mx-2`} style={{fontSize: '9px'}}>
                                Below Critical
                              </div>)
                                }
                                {parseInt(stockData.remainingStock) === 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NoStock')} mx-2`} style={{fontSize: '9px'}}>
                                No Stock
                              </div>)
                                }
                </label>
            </div>
            <div className='col-7'>
            <div className="row g-2 align-items-center mb-1">
                <div className="col-12">
                <label htmlFor="dateToday" className="col-form-label">
                    Date Today:
                  </label>
                  <input
                    type="date"
                    id="dateToday"
                    name="dateToday"
                    className="form-control"
                    style={{ boxShadow: "none" }}
                    onChange={handleChange}
                    value={formattedDate}
                    disabled
                  />
                </div>
              </div>
              <div className="row g-2 align-items-center mb-1">
                <div className="col-12">
                <label htmlFor="dateToday" className="col-form-label">
                    Time:
                  </label>
                  <input
                    type="time"
                    id="time"
                    name="time"
                    className="form-control"
                    style={{ boxShadow: "none" }}
                    onChange={handleChange}
                    value={formattedTime}
                    disabled
                  />
                </div>
              </div>
              <div className="row g-2 align-items-center mb-1">
                <div className="col-12">
                <label htmlFor="expDate" className="col-form-label">
                    Expiration Date:
                  </label>
                  <input
                    type="date"
                    id="expDate"
                    name="expDate"
                    className="form-control"
                    style={{ boxShadow: "none" }}
                    onChange={handleChange}
                    min={formattedDate}
                  />
                </div>
              </div>
              <div className="row g-2 align-items-center mb-1">
                <div className="col-12">
                <label htmlFor="expDate" className="col-form-label">
                    Quantity:
                  </label>
                  <input
                    type="number"
                    id="qty"
                    name="qty"
                    className="form-control"
                    style={{ boxShadow: "none" }}
                    placeholder="Enter quantity"
                    onChange={handleChange}
                  />
                </div>
              </div>
            
            </div>
            </div>
          </div>
          <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
          <div className={`${dst['modal-footer']} d-flex justify-content-end`} style={{ border: 'none', marginTop: 'auto' }}>
  
              <button type="button" className="btn" onClick={onClose}>
                Discard
              </button>
              <button
                type="button"
                className="btn ms-1"
                style={{ backgroundColor: '#505050', color: '#fff' }}
                onClick={handleSubmit}
              >
                Submit
              </button>
            </div>
        </div>
        {successModal && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default InventoryDetails;
