
import React, { useState, useEffect, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link } from 'react-router-dom';
import CreateStock from '../modals/CreateStockEntry';
import EditStock from '../modals/EditStockEntry';
import ConfirmArchive from '../modals/ConfirmArchive';
import StockDetails from '../modals/StockDetails';
import PageNavigator from '../navbars/PageNavigator';
import InvDetails from '../modals/InventoryDetails';
import axios from 'axios';
import domain from '../../constants/domain';
import FilterDropdown from '../modals/FilterDropdown';
import RetrieveModal from '../modals/RetrieveModal';
import DeleteModal from '../modals/DeleteModal';
import {useReactToPrint} from 'react-to-print';
function InvList() {
  const componentPDF = useRef();
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const day = String(currentDate.getDate()).padStart(2, '0');
  
  const formattedDate = `${year}-${month}-${day}`;
  
  const hours = String(currentDate.getHours()).padStart(2, '0');
  const minutes = String(currentDate.getMinutes()).padStart(2, '0');
  const formattedTime = `${hours}:${minutes}`;

  const [stockData, setStock] = useState([]);
  
  const [selectedStockID, setSelectedStockID] = useState(null);
  const [primaryID, setPrimaryId] = useState(null);
  const [selectSearchedStockID, setSelectSearchedStockId] = useState({});
  const [itemSearch, setItemSearch] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [optionDisplay, setOptionDisplay] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [inventoryData, setInventoryData] = useState([]); // New state for inventory data
  const [searchedInventoryData, setSearchedInventoryData] = useState([]); // State for filtered inventory data
  const [activeTab, setActiveTab] = useState("stockManagement"); // Set an initial active tab
  const [selectedOption, setSelectedOption] = useState("");
  const [isCreateStockModalOpen, setCreateStockModalOpen] = useState(false);
  const [isEditStockModalOpen, setEditStockModalOpen] = useState(false);
  const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
  const [isInventoryModalOpen, setInventoryModalOpen] = useState(false);
  const [isDetailModalOpen, setDetailModalOpen] = useState(false);
  const [selectedRows, setSelectedRows] = useState([]);
  const [selectMultipleEnabled, setSelectMultipleEnabled] = useState(false);
  const [filterOptions, setFilterOptions] = useState({
    prodType: [''],
    prodName: [''],
    category: ['Personal Hygiene', 'Consumables', 'Toiletries'], // Example for the 'Balance' column
    price: ['Highest', 'Lowest'],
    status: ['No Stock', 'Below Critical','At Critical',
    'Near Critical', 'Healthy', 'Overstocked'], // Example for the 'Type' column
    remainingStock: ['Highest', 'Lowest']
  });
  const [selectedFilter, setSelectedFilter] = useState({
    prodType: '',
    prodName: '',
    category: '',
    price: '',
    status: '',
    remainingStock: ''
    // Add more columns as needed
  });
  const [selectedFilterLabel, setSelectedFilterLabel] = useState({
    prodType: 'Product Type',
    prodName: 'Product Name',
    category: 'Category',
    price: 'Price',
    status: 'Status',
    remainingStock: 'Remaining Stock'
  });
  const [isFilterDropdownOpen, setFilterDropdownOpen] = useState(false);
  const [filterDropdownColumn, setFilterDropdownColumn] = useState('');
  const [typeOfArchive, setTypeOfArchive] = useState('');
  const [isRetrieveModalOpen, setRetrieveModalOpen] = useState(false);
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);
  const [userType, setUserType] = useState(null);
  const email = localStorage.getItem("email");
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const [entriesPerPage] = useState(10);
  const [logo, setLogo] = useState(null);
  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setLogo(fileName);
      }
    )

   }, [])


  useEffect(() => {
      getData();
      eraseFilters();
      setFilterDropdownOpen(false);
      setSelectMultipleEnabled(false);
  }, [activeTab]);

  function getData(){
    axios
    .get(activeTab === 'stockManagement' ? 
    `${domain}/index.php?action=displayStock` : 
    `${domain}/index.php?action=displayArchivedStocks`
    )
    .then((res) => {
      setStock([]);
      setInventoryData([]);
      setOptionDisplay([]);
      if (Array.isArray(res.data)) {
        setStock(res.data);
        setInventoryData(res.data); // Set the inventory data
        setOptionDisplay(res.data);
        setFilterOptions(prevFilterOptions => ({
          ...prevFilterOptions,
          prodType: res.data.map(s => s.prodType),
          prodName: res.data.map(s => s.prodName),
          category: [...new Set([...prevFilterOptions.category, ...res.data.map((s) => s.category)])],
        }));
      } else {
        console.error("API response is not an array:", res.data);
      }
    })
    .catch((error) => console.error("Error fetching Stock data:", error));
  }
  useEffect(() => {setSearchedInventoryData(inventoryData);}, [inventoryData])

  useEffect(() => {
    axios.get(`${domain}/index.php?action=getUserType&email=${email}`)
    .then(response => {
      if(response.data){
        setUserType(response.data.userType);
        
      }
    })
  },[]) 
  
  const filteredData = stockData.filter((s) => {
    const isProdTypeMatch = s.prodType.toLowerCase().includes(selectedFilter.prodType.toLowerCase());
    const isProdNameMatch = s.prodName.toUpperCase().includes(selectedFilter.prodName.toUpperCase());
    const isCategoryMatch = s.category.toUpperCase().includes(selectedFilter.category.toUpperCase());
    const isPriceMatch =
    selectedFilter.price === '' ||
    (selectedFilter.price === 'Highest' || selectedFilter.price === 'Lowest') && parseFloat(s.price) > 0;
    const isStatusMatch =
      selectedFilter.status === '' ||
      ((parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 21 &&
        selectedFilter.status === 'Overstocked') ||
      ((parseInt(s.remainingStock)- parseInt(s.criticalStock)) <= 20 &&
      (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 11 &&
        selectedFilter.status === 'Healthy') ||
      ((parseInt(s.remainingStock) - parseInt(s.criticalStock)) <= 10 &&
      (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 1 &&
        selectedFilter.status === 'Near Critical') ||
      (parseInt(s.remainingStock) === parseInt(s.criticalStock) &&
      parseInt(s.remainingStock) !== 0 && parseInt(s.criticalStock) !== 0 &&
        selectedFilter.status === 'At Critical') ||
      (parseInt(s.remainingStock) < parseInt(s.criticalStock) &&
        selectedFilter.status === 'Below Critical') ||
      (parseInt(s.remainingStock) === 0 && selectedFilter.status === 'No Stock');
  
    return isProdTypeMatch && isProdNameMatch && isCategoryMatch && isPriceMatch && isStatusMatch;
  });
  filteredData.sort((a, b) => {
    const priceA = a.price;
    const priceB = b.price;
  
    if (selectedFilter.price === 'Highest') {
      return priceB - priceA;
    } else if (selectedFilter.price === 'Lowest') {
      return priceA - priceB;
    }
    return 0;
  });

  const openRetrieveModal = (primID, e, id) => {
    setRetrieveModalOpen(true);
    setSelectedStockID(id);
    setPrimaryId(primID);
  }

  const closeRetrieveModal = () => {
    setRetrieveModalOpen(false);
    getData();
  }
  
  const openDeleteModal = (primID, e, id) => {
    setDeleteModalOpen(true);
    setSelectedStockID(id);
    setPrimaryId(primID);
  }
  const closeDeleteModal = () => {
    setDeleteModalOpen(false);
    getData();
  }


  const selectMultiple = () => {
    setSelectMultipleEnabled((prevState) => !prevState);
    if(!selectMultipleEnabled){
      setSelectedRows([]);
      setSelectedFilterLabel(({
        prodType: 'Product Type',
        prodName: 'Product Name',
        category: 'Category',
        price: 'Price',
        status: 'Status',
        remainingStock: 'Remaining Stock'
      }))
      setSelectedFilter({
        prodType: '',
        prodName: '',
        category: '',
        price: '',
        status: '',
        remainingStock: ''
        // Add more columns as needed
      })
      ;
    }
  }


    const totalEntries = filteredData ? filteredData.length : 0;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    let entriesToShow = [];
    if (Array.isArray(filteredData)) {
      entriesToShow = filteredData.slice(
        (currentPage - 1) * entriesPerPage,
        currentPage * entriesPerPage
      );
  }

  const toggleFilterDropdown = (column) => {
    setFilterDropdownOpen(!isFilterDropdownOpen);
    setFilterDropdownColumn(column);
  };
  const getCellStyleForType = (type) => {
    // Map type to corresponding CSS class
    switch (type) {
      case 'Overstocked':
        return dst.Overstocked;
      case 'Healthy':
        return dst.Healthy;
      case 'NearCritical':
        return dst.NearCritical;
      case 'AtCritical':
        return dst.AtCritical;
      case 'BelowCritical':
        return dst.BelowCritical;
      case 'NoStock':
        return dst.NoStock;
      default:
        return ''; // Use a default style if needed
    }
  };
  const handleFilterSelect = (option, column) => {
    // Implement logic to filter data based on the selected option
    // You can use this information to filter your data accordingly
    // Update the selected filter state for the specific column
    setSelectedFilter({
      ...selectedFilter,
      [column]: option,
    });

    // Update the label for the corresponding column
    switch (column) {
      case 'prodType':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          prodType: option,
        });
        break;
      case 'prodName':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          prodName: option,
        });
        break;
      case 'category':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          category: option,
        });
        break;
      case 'price':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          price: option,
        });
        break;
      case 'status':
          setSelectedFilterLabel({
            ...selectedFilterLabel,
            status: option,
          });
          break;
      case 'remainingStock':
          setSelectedFilterLabel({
            ...selectedFilterLabel,
            remainingStock: option,
          });
          break;
      // Add cases for other columns if needed
      default:
        break;
    }
    setFilterDropdownOpen(false);
  };

  const eraseFilters = () => {
    setSelectedFilter({
      prodType: '',
      prodName: '',
      category: '',
      price: '',
      status: '',
      remainingStock: ''
      // Add more columns as needed
    });
    setSelectedFilterLabel(({
      prodType: 'Product Type',
      prodName: 'Product Name',
      category: 'Category',
      price: 'Price',
      status: 'Status',
      remainingStock: 'Remaining Stock'
    }));
  }
const renderFilterDropdown = (column) => {
    if (isFilterDropdownOpen && filterDropdownColumn === column) {
      return (
        <FilterDropdown
          options={filterOptions[column]}
          onSelect={(option) => handleFilterSelect(option, column)}
          type={column}
        />
      );
    }
    return null;
  };
  const tabs = [
    { label: "Stock Management", id: "stockManagement" },
    { label: "Archived Items", id: "archivedItems" },
    // Add more tabs as needed
  ].filter(tab => tab !== null);


  const filteredStockData = stockData.filter((item) => {
    if (selectedOption === "" || selectedOption === "None") {
      return true; // If no category is selected, show all items
    } else {
      return item.category === selectedOption;
    }
  });



  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };

  const openCreateStockModal = () => {
    setCreateStockModalOpen(true);
  };
  const closeCreateStockModal = () => {
    setCreateStockModalOpen(false);
    getData();
  };

  const openEditStockModal = (id, e, primID) => {
    setEditStockModalOpen(true);
    setSelectedStockID(id);
    setPrimaryId(primID);
    const newUrl = `?itemNo=${id}`;
    window.history.pushState({}, null, newUrl);
  };
  const closeEditStockModal = () => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setEditStockModalOpen(false);
    getData();
  };


  const openArchiveModal = (id, e, primID, type) => {
    setTypeOfArchive(type);
    if(type === 'Single'){
      setArchiveModalOpen(true);
      setSelectedStockID(id);
      setPrimaryId(primID);
    }
    else{
      setArchiveModalOpen(true);
    }

  };
  const closeArchivePDLModal = (id) => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setArchiveModalOpen(false);
    getData();
    setSelectedRows([]);
    setSelectMultipleEnabled(false);
  };

  const displayedItems = filteredStockData ? filteredStockData.slice(startIndex, endIndex) : null;


  const openDetailModal = (id, e) => {
    setDetailModalOpen(true);
    setSelectedStockID(id);
    const newUrl = `?itemNo=${id}`;
    window.history.pushState({}, null, newUrl);
  };
  const closeDetailPDLModal = () => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setDetailModalOpen(false);
    getData();
  };

  const openInventoryModal = (id, e) => {
    setSelectedStockID(id)
    setInventoryModalOpen(true);
  }
  const closeInventoryModal = () => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setInventoryModalOpen(false);
    getData();
  }

  const generateReport = useReactToPrint({
    content: () => componentPDF.current,
    documentTitle: "Inventory List",
    });/*() => {
    if(stockData){
      const action = "generateStocksReport";
      const actionData = { action };
      axios.post(domain, actionData)
          .then(response => {
              // Handle successful response, if needed
               // Response.data will contain the file URL
               const fileUrl = response.data.fileUrl;
              const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
              const newWindow = window.open(formattedFileUrl, '_blank');

              // Ensure the new window is fully loaded before attempting to print
              newWindow.onload = () => {
                  newWindow.print();
              };
          })
          .catch(error => {
              // Handle error, if needed
              console.error('Error generating report:', error);
          });
    }
}*/

  return (
    <div className="px-5 pb-5">
    <div className='row'>
      <div className='col-5 d-flex justify-content-start'>
      {tabs.map((tab) => (
            <button
              key={tab.id}
              className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
              onClick={() => setActiveTab(tab.id)}
            >
              {tab.label}
            </button>
          ))}
        </div>
      </div>
      <div className={`${dst.con} p-4`}>

        {activeTab === 'stockManagement' ? (<>
          <div className='row d-flex align-items-center position-relative'>
                <div className='col-4 d-flex align-items-center mb-3'>
                <i className='fa-solid fa-boxes'></i>
                <h6 className='fw-bold fs-5 m-0 mx-2'>List of Stocks</h6>
                </div>
                <div className='col-8 d-flex justify-content-end align-items-center'>
                  <div className='mx-4' style={{fontSize: '12px'}}>
                    
                    <div >
                      <div className='row d-flex'>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('Overstocked')}`}>
                          Overstocked
                        </div>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('Healthy')}`}>
                          Healthy
                        </div>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('NearCritical')}`}>
                          Near Critical
                        </div>
                      </div>
                      <div className='row d-flex mt-1'>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('AtCritical')}`}>
                          At Critical
                        </div> 
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('BelowCritical')}`}>
                          Below Critical
                        </div>
                        <div style={{padding: '0 5px'}} className={`${dst.typeCell} ${getCellStyleForType('NoStock')}`}>
                          No Stock
                        </div>
                      </div>
                      
                     
                    </div>
                  </div>
                {!selectMultipleEnabled ? (<><button className={`${dst.tableBtn} me-2`} 
                    style={{left: '0'}}type='button' onClick={generateReport}>
                  <i className={`fa-regular fa-file ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Generate Report</span>
                  </button>
                  <button className={`${dst.tableBtn} me-2`} style={{left: '0'}} type='button' onClick={openCreateStockModal}>
                  <i className={`fa-regular fa-plus ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Add Stocks</span>
                  </button></>) : selectMultipleEnabled && selectedRows.length > 0 ?
                  (<><button className={`${dst.tableBtn} me-2`} 
                  style={{left: '0'}}type='button' 
                  onClick={(e) => openArchiveModal(null, e, null, 'Batch')}>
                  <i className={`fa-solid fa-box-archive ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Archive</span>
                  </button></>) : null
                }
                {
                selectedFilterLabel.prodType !== 'Product Type' || 
                selectedFilterLabel.prodName !== 'Product Name' ||
                selectedFilterLabel.category !== 'Category' ||
                selectedFilterLabel.price !== 'Price' ||
                selectedFilterLabel.status !== 'Status' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
                <button className={`${dst.tableBtn} me-2`} style={{left: '0', 
                backgroundColor: selectMultipleEnabled ? '#0099ff' : '',
                color: selectMultipleEnabled ? '#fff' : '',
                border: selectMultipleEnabled ? 'none' : ''
                }}type='button' onClick={selectMultiple}>
                <i className={`fa-solid fa-clipboard-check ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Select Multiple</span>
                </button>
              </div>
            </div>
            <div className='col-12 mx-auto'>
            <table
                  className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    overflow: 'auto',
                    fontSize: '14px',
                    border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                  }}
                >
                <thead>
                  <tr style={{fontSize: '16px'}}>
                    {selectMultipleEnabled && (<th scope="col">
                      Select.
                    </th>)}
                    <th scope="col">
                      Stock ID
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.prodType}
                      {!selectMultipleEnabled &&(<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('prodType')}></i>
                      {renderFilterDropdown('prodType')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.prodName}
                      {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('prodName')}></i>
                      {renderFilterDropdown('prodName')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.category}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('category')}></i>
                      {renderFilterDropdown('category')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.price}
                    {!selectMultipleEnabled && (<><i className={`fa-solid 
                    ${selectedFilterLabel.price == 'Highest' ? 'fa-sort-up' : 
                    selectedFilterLabel.price == 'Lowest' ? 'fa-sort-down' : 'fa-sort'} ms-2`} style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('price')}></i>
                      {renderFilterDropdown('price')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.status}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('status')}></i>
                      {renderFilterDropdown('status')}</>)}
                    </th>
                    {!selectMultipleEnabled && (<th scope="col" className={dst.exclude} >
                      Actions
                    </th>)}
                  </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow != 0 ? (
                <>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const s = filteredData[index];
                    
                    const hasData = s !== undefined;
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    const isSelected = selectedRows.includes(parseInt(s?.stock_id));
                    const handleRowClick = () => {
                      if (selectMultipleEnabled) {
                        
                        if (hasData) {
                          const stockID = parseInt(s.stock_id); // Get the s ID
                          if (isSelected) {
                            setSelectedRows(selectedRows.filter((selectedId) => selectedId !== stockID));
                            
                          } else {
                            setSelectedRows([...selectedRows, stockID]);
                          }
                          
                        }
                      } else {
                        // If selectMultipleEnabled is false, clear the selected rows
                        setSelectedRows([]);
                      }
                    };
                    return (
                      <tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                      ${isSelected && selectMultipleEnabled ? dst.selectedRow : ''}`}
                      onClick={handleRowClick}
                      style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                      >
                        {selectMultipleEnabled && (<td>{hasData ? 
                        (<i className={`fa-regular ${isSelected ? 'fa-square-check' : 'fa-square'} fs-3 text-center`}>

                        </i>) : ''}</td>)}
                        <td>{hasData ? `I-${s.id}` : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            <>
                              {s.prodType}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>{hasData ? (s.prodName) : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                          <div className='d-flex'>
                            {s.category}
                            
                          </div>
                          
                          ) : ''}</td>
                          <td>{hasData ? 
                          (<>PHP{s.formatted_price}</>) :
                           (<></>)}</td>
                           <td className='d-flex align-items-center justify-content-center'>
                              {hasData ? (<div className='d-flex'>
                                
                                {(parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 21 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Overstocked')}`}>
                                 </div>)
                                }
                                {(parseInt(s.remainingStock)- parseInt(s.criticalStock)) <= 20 &&
                                (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 11 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Healthy')}`}>
                              </div>)
                                }
                                {(parseInt(s.remainingStock) - parseInt(s.criticalStock)) <= 10 &&
                                (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 1 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NearCritical')}`}>
                                </div>)
                                }
                                {parseInt(s.remainingStock) === parseInt(s.criticalStock) &&
                                parseInt(s.remainingStock) !== 0 && parseInt(s.criticalStock) !== 0 && 
                                (<div className={`${dst.typeCell} ${getCellStyleForType('AtCritical')}`}>
                              </div>)
                                }
                                {parseInt(s.remainingStock) < parseInt(s.criticalStock) &&
                                parseInt(s.remainingStock) !== 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('BelowCritical')}`}>
                              </div>)
                                }
                                {parseInt(s.remainingStock) === 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NoStock')}`}>
                              </div>)
                                }
                              </div>) : ''}




                           </td>
                        {!selectMultipleEnabled && (<td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons} ${dst.exclude}`}>
                                <p onClick={(e) => openDetailModal(s.id, e, s.stock_id)}>
                                  <i className={`fa-solid fa-eye fa-sm ${dst.iconHover}`}></i>
                                  <span className={dst.iconTooltip}>View</span>
                                </p>
                              <p onClick={(e) => openEditStockModal(s.id, e, s.stock_id)}>
                                <i className={`fa-solid fa-pen-to-square fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Edit</span>
                              </p>
                              <p onClick={(e) => openArchiveModal(s.id, e, s.stock_id, 'Single')}>
                                <i className={`fa-solid fa-box-archive fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Archive</span>
                              </p>
                              <p onClick={(e) => openInventoryModal(s.id, e)}>
                                <i className={`fa-solid fa-truck-ramp-box fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Create Inventory</span>
                              </p>
                            </div>
                          )}
                        </td>)}
                      </tr>
                    );
                  })}</>) : (
                    <tr>
                      <td style={{ padding: '150px', textAlign: 'center' }} colSpan={7}>
                        Nothing to show
                      </td>
                    </tr>


                  )}
                </tbody>
                </table>
            </div>
            <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center'}}>
          <PageNavigator totalPages={Math.ceil(filteredStockData.length / itemsPerPage)} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
            </>
        ) : (<>
          <div className='row d-flex align-items-center position-relative'>
                <div className='col-4 d-flex align-items-center mb-3'>
                <i className='fa-solid fa-boxes'></i>
                <h6 className='fw-bold fs-5 m-0 mx-2'>Archived Stocks</h6>
                </div>
                <div className='col-8 d-flex justify-content-end'>
                {!selectMultipleEnabled ? (<></>) : 
                selectMultipleEnabled && selectedRows.length > 0 ?
                  (<><button className={`${dst.tableBtn} me-2`} 
                  style={{left: '0'}}type='button' 
                  onClick={(e) => openArchiveModal(null, e, null, 'Batch')}>
                  <i className={`fa-solid fa-box-archive ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Archive</span>
                  </button></>) : null
                }
                {
                selectedFilterLabel.prodType !== 'Product Type' || 
                selectedFilterLabel.prodName !== 'Product Name' ||
                selectedFilterLabel.category !== 'Category' ||
                selectedFilterLabel.price !== 'Price' ||
                selectedFilterLabel.status !== 'Status' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
                
              </div>
            </div>
            <div className='col-12 mx-auto'>
            <table
                  className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    overflow: 'auto',
                    fontSize: '14px',
                    border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                  }}
                >
                <thead>
                  <tr style={{fontSize: '16px'}}>
                    {selectMultipleEnabled && (<th scope="col">
                      Select.
                    </th>)}
                    <th scope="col">
                      Stock ID
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.prodType}
                      {!selectMultipleEnabled &&(<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('prodType')}></i>
                      {renderFilterDropdown('prodType')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.prodName}
                      {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('prodName')}></i>
                      {renderFilterDropdown('prodName')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.category}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('category')}></i>
                      {renderFilterDropdown('category')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.price}
                    {!selectMultipleEnabled && (<><i className={`fa-solid 
                    ${selectedFilterLabel.price == 'Highest' ? 'fa-sort-up' : 
                    selectedFilterLabel.price == 'Lowest' ? 'fa-sort-down' : 'fa-sort'} ms-2`} style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('price')}></i>
                      {renderFilterDropdown('price')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.status}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('status')}></i>
                      {renderFilterDropdown('status')}</>)}
                    </th>
                    {!selectMultipleEnabled && (<th scope="col" className={dst.exclude} >
                      Actions
                    </th>)}
                  </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow != 0 ? (
                <>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const s = filteredData[index];
                    
                    const hasData = s !== undefined;
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    const isSelected = selectedRows.includes(parseInt(s?.stock_id));
                    const handleRowClick = () => {
                      if (selectMultipleEnabled) {
                        
                        if (hasData) {
                          const stockID = parseInt(s.stock_id); // Get the s ID
                          if (isSelected) {
                            setSelectedRows(selectedRows.filter((selectedId) => selectedId !== stockID));
                            
                          } else {
                            setSelectedRows([...selectedRows, stockID]);
                          }
                          
                        }
                      } else {
                        // If selectMultipleEnabled is false, clear the selected rows
                        setSelectedRows([]);
                      }
                    };
                    return (
                      <tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                      ${isSelected && selectMultipleEnabled ? dst.selectedRow : ''}`}
                      onClick={handleRowClick}
                      style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                      >
                        {selectMultipleEnabled && (<td>{hasData ? 
                        (<i className={`fa-regular ${isSelected ? 'fa-square-check' : 'fa-square'} fs-3 text-center`}>

                        </i>) : ''}</td>)}
                        <td>{hasData ? `I-${s.id}` : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            <>
                              {s.prodType}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>{hasData ? (s.prodName) : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                          <div className='d-flex'>
                            {s.category}
                            
                          </div>
                          
                          ) : ''}</td>
                          <td>{hasData ? 
                          (<>PHP{s.formatted_price}</>) :
                           (<></>)}</td>
                           <td>
                              {hasData ? (<div className='d-flex'>
                                {(parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 21 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Overstocked')}`}>
                                  Overstocked
                                 </div>)
                                }
                                {(parseInt(s.remainingStock)- parseInt(s.criticalStock)) <= 20 &&
                                (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 11 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Healthy')}`}>
                                  Healthy
                              </div>)
                                }
                                {(parseInt(s.remainingStock) - parseInt(s.criticalStock)) <= 10 &&
                                (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 1 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NearCritical')}`}>
                                  Near Critical
                                </div>)
                                }
                                {parseInt(s.remainingStock) === parseInt(s.criticalStock) &&
                                parseInt(s.remainingStock) !== 0 && parseInt(s.criticalStock) !== 0 && 
                                (<div className={`${dst.typeCell} ${getCellStyleForType('AtCritical')}`}>
                                At Critical
                              </div>)
                                }
                                {parseInt(s.remainingStock) < parseInt(s.criticalStock) &&
                                parseInt(s.remainingStock) !== 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('BelowCritical')}`}>
                                Below Critical
                              </div>)
                                }
                                {parseInt(s.remainingStock) === 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NoStock')}`}>
                                No Stock
                              </div>)
                                }
                              </div>) : ''}




                           </td>
                        {!selectMultipleEnabled && (<td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons}`}>
                              <p onClick={(e) => openRetrieveModal(s.stock_id, e, s.id)}>
                                <i className={`fa-solid fa-rotate-left fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Retrieve</span>
                              </p>
                              <p onClick={(e) => openDeleteModal(s.stock_id, e, s.id)}>
                                <i className={`fa-solid fa-trash fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Delete</span>
                              </p>
                            </div>
                          )}
                        </td>)}
                      </tr>
                    );
                  })}</>) : (
                    <tr>
                      <td style={{ padding: '150px', textAlign: 'center' }} colSpan={7}>
                        Nothing to show
                      </td>
                    </tr>


                  )}
                </tbody>
                </table>
            </div>
            <div className='row' 
            style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center'}}>
          <PageNavigator totalPages={Math.ceil(filteredStockData.length / itemsPerPage)} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
            </>
        )}
      </div>
      {isCreateStockModalOpen && <CreateStock onClose={closeCreateStockModal}/>}
      {isEditStockModalOpen && <EditStock onClose={closeEditStockModal} itemId={selectedStockID} primaryID={primaryID}/>}
      {isArchiveModalOpen && <ConfirmArchive 
      onClose={closeArchivePDLModal} 
      primaryStockID={typeOfArchive === 'Single' ? primaryID : null} 
      archiveType="Stock"
      rowsToArchiveStock={typeOfArchive !== 'Single' ? selectedRows : null}
      
      />}
      {isRetrieveModalOpen && (
          <RetrieveModal
          onClose={closeRetrieveModal}
          primaryStockID={primaryID} 
          stockID={selectedStockID}
          retrieveType="Stock"
          style={{
            // Apply your conditional styles here
            opacity: isRetrieveModalOpen ? 1 : 0,
            transition: 'opacity 0.5s ease-in-out', // Add transition for a fading effect
          }}
        />
      )} 
      {isDeleteModalOpen && <DeleteModal primaryStockID={primaryID}
       onClose={closeDeleteModal}/>}
      {isDetailModalOpen && <StockDetails onClose={closeDetailPDLModal} itemId={selectedStockID}/>}
      {isInventoryModalOpen && <InvDetails onClose={closeInventoryModal} itemId={selectedStockID}/>}
      <div ref={componentPDF} style={{position: 'absolute', width: '100%', zIndex: '-99'}}>
          <div className='m-5'>
            <div className='row d-flex justify-content-start align-items-center'>
              <div className='col-1 justify-content-start'>
              <img className={dst.logo} src={`../logo/${logo}`} alt="BJMP Logo" 
              style={{width: '80px', height: '80px'}}/>
              </div>
              <div className='col-11 text-start'>
                <div className='mx-4'>
                  <h5 className='fw-bold'>Bureau of Jail Management and Penology Region III</h5>
                  <h6 className='fw-bold'>List of Stocks</h6>
                  <p>Date Today: {formattedDate} {formattedTime}</p>
                </div>
                
              </div>
              
            </div>
          
          <table
              className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                style={{
                  borderCollapse: 'collapse',
                  color: '#48505E',
                  fontSize: 'small',
                  overflow: 'auto',
                  fontSize: '14px',
                  border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                }}
              >
              <thead>
                <tr style={{fontSize: '13px'}}>
                <th scope="col">
                      Stock ID
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.prodType}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.prodName}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.category}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.price}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.status}
                    </th>
                    <th scope="col" >
                      Remaining Stocks
                    </th>
                </tr>
              </thead>
              <tbody className={dst.shrinkMedia}>
              {entriesToShow.map((s, index) => (
                  <tr
                    key={index}
                    className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}`}
                    
                    style={{ cursor: selectMultipleEnabled && s ? 'pointer' : 'inherit' }}
                  >
                    
                    <td>{s ? `I-${s.id}` : ''}</td>
                    <td>
                      {s ? (
                        <>
                          {s.prodType}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {s ? (
                        <>
                          {s.prodName}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {s ? (
                        <>
                          {s.category}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {s ? (
                        <>
                          {`PHP${s.formatted_price}`}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                              {s ? (<div className='d-flex'>
                                {(parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 21 &&
                                (<div>
                                  Overstocked
                                 </div>)
                                }
                                {(parseInt(s.remainingStock)- parseInt(s.criticalStock)) <= 20 &&
                                (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 11 &&
                                (<div>
                                  Healthy
                              </div>)
                                }
                                {(parseInt(s.remainingStock) - parseInt(s.criticalStock)) <= 10 &&
                                (parseInt(s.remainingStock) - parseInt(s.criticalStock)) >= 1 &&
                                (<div>
                                  Near Critical
                                </div>)
                                }
                                {parseInt(s.remainingStock) === parseInt(s.criticalStock) &&
                                parseInt(s.remainingStock) !== 0 && parseInt(s.criticalStock) !== 0 && 
                                (<div>
                                At Critical
                              </div>)
                                }
                                {parseInt(s.remainingStock) < parseInt(s.criticalStock) &&
                                parseInt(s.remainingStock) !== 0 &&
                                (<div>
                                Below Critical
                              </div>)
                                }
                                {parseInt(s.remainingStock) === 0 &&
                                (<div>
                                No Stock
                              </div>)
                                }
                              </div>) : ''}




                           </td>
                  <td>
                      {s ? (
                        <>
                          {`${s.remainingStock}pcs`}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                  </tr>
                ))}

              </tbody>
              </table>
          </div>
       
       </div>
    
    
    </div>
    
  );
}

export default InvList;
