import React from 'react';
import dst from '../../../styles/DashboardStyle.module.css';

function RedCard({ popularItem, duration, onDurationChange }) {
    return (
      <div className={`${dst.redCard} p-4 text-start position-relative`}>
        <div className="position-absolute top-0 end-0 p-2">
            <select
              id="durationSelector"
              className="form-select form-select-sm"
              value={duration}
              onChange={(e) => onDurationChange(e.target.value)}
            >
              <option value="All Time">All Time</option>
              <option value="Weekly">Weekly</option>
              <option value="Monthly">Monthly</option>
            </select>
            {/* Move the span for the specified week to the top right */}

          </div>

      <i className='fa-solid fa-cart-flatbed fa-3x'></i>
      <div className='row mt-3 mb-2'>
        <div className='col'>
            <p className="fw-bold m-0 h4">
              {popularItem ? <>{popularItem}</> : (<>Nothing yet...</>)}</p>
        </div>
      </div>
      <p className=" m-0" style={{fontSize: '14px'}}>Most Popular Product</p>
      {duration === 'Weekly' && (
              <p className=" m-0" style={{fontSize: '12px'}}>
                Week: {/* Add the logic to display the specified week */}
              </p>
            )}
    </div>
      );
}
export default RedCard;
