import React, {useState, useEffect, useRef} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';
import LogoutModal from './LogoutModal';
import ConfirmArchive from './ConfirmArchive';
import SuccAcc from './SuccessfulAction';

/* global selectChangeEvent 
   global readersDropDownPopulate 
   global populatePopUpModal 
   global disableEnable 
   global enableDisableScanQualityDiv 
   global disableEnableExport 
   global FingerprintSdkTest 
   global inputOnStart
   global onStop
*/

function SettingsModal({onClose}) {

    const [activeTab, setActiveTab] = useState('general');
    const [inputCheckerMessage, setInputCheckerMessage] = useState('');
    const [formData, setFormData] = useState({});
    const [imageSrc, setImageSrc] = useState(null);
    const [tempImgSrc, setTempImageSrc] = useState(null);
    const [errorMessage, setErrorMessage] = useState('');
    const [emailMessage, setEmailMessage] = useState('');
    const [passwordMessage, setPasswordMessage] = useState('');
    const [successMessage, setSuccessMessage] = useState('');
    const [successModal, setSuccessModal] = useState(false);
    const [spendingData, setSpendingData] = useState({
      action: 'updateSpendingLimit',
      limitData: null,
      oldLimitData: null,
    });
    const fileInputRef = useRef(null);
    const error = localStorage.getItem("errorMessage");
    const [emailData, setEmailData] = useState({
      action: 'updateEmail',
      emailAdd: '',
      oldEmailAdd: '',
    });
    const [logOutModal, setLogOutModal] = useState(false);
    const rectWidth = imageSrc ? 252 : 252; // Adjust width based on imageSrc
    const rectHeight = imageSrc ? 82 : 82; // Adjust height based on imageSrc
    const [methodType, setMethodType] = useState('');
    const [archiveUser, setArchiveUser] = useState(false);
    const email = localStorage.getItem("email");
   useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setTempImageSrc(fileName);
      }
      
    )
    setEmailData({...emailData, 
      emailAdd: localStorage.getItem('email'),
      oldEmailAdd: localStorage.getItem('email')
    });
    axios.get(`${domain}/index.php?action=getSpendingLimit`).then(
      response => {
        setSpendingData(values => 
         ({...values,
            limitData: parseFloat(response.data[0].spending_limit),
          oldLimitData: parseFloat(response.data[0].spending_limit)})
        )
      }
    )
   }, [])

    const handleSvgClick = () => {
      if (fileInputRef.current) {
        fileInputRef.current.click(); // Trigger click on the file input
      }
    };
    
    useEffect(() => {
      // Load your external script
      const script = document.createElement('script');
      script.src = '/websdk.js';
      script.async = true;
      script.defer = true;
  
      // Attach a callback function to execute after the script is loaded
      script.onload = () => {
        test = new FingerprintSdkTest();
        readersDropDownPopulate(true); //To populate readers for drop down selection
        disableEnable(); // Disabling enabling buttons - if reader not selected
        enableDisableScanQualityDiv("content-reader"); // To enable disable scan quality div
        disableEnableExport(true);

      };
      const handleStorageChange = () => {
        const fingerprint = localStorage.getItem('imageSrc');
        if (fingerprint) {
          setInputCheckerMessage('Reader has input');
        }
      };
      
      window.addEventListener('storage', handleStorageChange);

      document.body.appendChild(script);
      // Cleanup: remove the script when the component is unmounted
      return () => {
        document.body.removeChild(script);
        window.removeEventListener('storage', handleStorageChange);
      };
    }, []);
    
    const handleReaderChange = (e) => {
      selectChangeEvent();
    }


    
    const handleChange = (e) => {
      const name = e.target.name;
      const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;
    
      if (e.target.type === 'file') {
        const reader = new FileReader();
        
        reader.onload = (e) => {
          setImageSrc(e.target.result);
          setTempImageSrc(null);
          setFormData(values => ({ ...values, action: 'addLogo', bjmplogo: value }));
        };
    
        if (value) {
          reader.readAsDataURL(value);
        }
    
        return;
      }
    
      if (name === 'emailAdd') {
        setEmailData(values => ({ ...values, emailAdd: value }));
      }
      if(name === 'spendingLimit') {
        setSpendingData(values => ({...values, action: 'updateSpendingLimit',  limitData: parseFloat(value)}));
      }
    };

    function isValidEmail(email) {
      const emailRegex = /^[A-Za-z0-9._%-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/;
      return emailRegex.test(email);
    }

  const requestPasswordChange = async (e) => {
      e.preventDefault();
      const updatedEmailData = {
        email: (emailData.emailAdd),
        action: 'forgotPassword',
        from: 'Settings'
      };
      axios.post(domain, updatedEmailData).then(
        response => {
          if(response.data.success){
            setPasswordMessage(response.data.message);
            localStorage.removeItem('token');
            localStorage.removeItem('email');
            openLogOutModal('ChangePassword');
          }
        }
      )
  }
  const handleSubmit = async (e) => {
    e.preventDefault();
    const { emailAdd, oldEmailAdd } = emailData;
    if (!isValidEmail(emailAdd)) {
      setEmailMessage('Invalid email address.');
    }
    else if(emailAdd === oldEmailAdd){
      setEmailMessage('Email is already in use.');
    }
    else{
      setEmailMessage('');
      
      axios.post(domain, emailData).then(
        response => {
          if(response.data.emailDetect) {
            setEmailMessage(response.data.message);
            
            return;
          }
          else{
            localStorage.removeItem('token');
            localStorage.removeItem('email');
            openLogOutModal('ChangeEmail');
          }
        }
      ).catch(error => console.error('Error: ', error));
      }
    
  };

  const handleImageSubmit = async (e) => {
    e.preventDefault(); // Move this line here to prevent the default form submission
  
    const { bjmplogo, action } = formData;
    const fileUp = new FormData();
    fileUp.append('bjmplogo', bjmplogo);
    fileUp.append('action', action);
    fileUp.append('email', localStorage.getItem("email"));
  
    if (!bjmplogo) {
      setErrorMessage("No image has been inserted!");
    } else {
      axios.post(domain, fileUp)
        .then((response) => {
          if (response.data) {
            setErrorMessage('');
            setSuccessMessage("Image uploaded successfully");
            openSuccessModal();
          }
        })
        .catch(error => {
          console.error("Error handling image:", error);
        });
    }
  };

  const handleLimitChange = (e) => {
    e.preventDefault();
    const {limitData, oldLimitData} = spendingData;
    if(limitData != oldLimitData){
      setErrorMessage('');
      axios.post(domain, spendingData
        ).then(response => {
          openSuccessModal();
      })
    }
    else{
      setErrorMessage('Value is the same as before!')
    }
  }
  
    const handleBackdropClick = (e) => {
        // Check if the click target is the backdrop element
        if (e.target.id === 'settingsModal') {
          onClose(); // Close the modal
        }
      };

    const handleTabClick = (tab) => {
      setActiveTab(tab);
      setErrorMessage('');
      setEmailMessage('');
    };
    const handleReaderRefresh = () => {
      // Call the globally defined function
      readersDropDownPopulate(false)
    };
    const populateModal = () => {
      // Call the globally defined function
      populatePopUpModal();

      
    };

    const openLogOutModal = (action) => {
      setMethodType(action);
      setLogOutModal(true);
    }

    const openArchiveModal = () => {
      setArchiveUser(true);
    }
    const closeArchiveModal = () => {
      setArchiveUser(false);
    }
    
    const successfulArchive = (action) => {
      setMethodType(action);
      setLogOutModal(true);
    }
    const archiveAccount = () => {
      openArchiveModal();
    }

    const openSuccessModal = () => {
      setSuccessModal(true);
    }
    const closeSuccessModal = () => {
      setSuccessModal(false);
      window.location.reload();
    }
    const tabs = [
      { label: "General", id: "general" },
      { label: "Account", id: "account" },
      // Add more tabs as needed
    ];
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="settingsModal" tabIndex="-1">
      <div className={`${dst['modal-content']}`} style={{ width: '650px' }}>
        <div className={`${dst['modal-header']}`}>
          <div className='row w-100'>
            <div className='col-10 d-flex align-items-center'>
              <i className='fa-solid fa-gear'></i>
              <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Settings</h6>
            </div>
            <div className='col-2 d-flex justify-content-end align-items-center'>
              <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none' }}></button>
            </div>
          </div>
        </div>
        <div className='row' style={{}}>
          <div className={`col-5 d-flex justify-content-start ${dst.tabs}`} >
          {tabs.map((tab) => (
                <button
                  key={tab.id}
                  className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
                  onClick={() => setActiveTab(tab.id)}
                >
                  {tab.label}
                </button>
              ))}
            </div>
          </div>
          <div className={dst['modal-body']}>
            {activeTab === 'general' ? (<>
              <div className="row g-3 align-items-center">
                  <div className="col-6">
                    <label htmlFor="first_name" className="col-form-label">BJMP Logo:</label>
                    <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {tempImgSrc && (
                          <image
                            x="40%"
                            y="20%"
                            xlinkHref={`../logo/${tempImgSrc}`}
                            width="50"
                            height="50"
                          />
                        
                      )}
                      {imageSrc && (
                          <image
                            x="40%"
                            y="20%"
                            xlinkHref={imageSrc}
                            width="50"
                            height="50"
                          />
                      )}
                      {!imageSrc && !tempImgSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="9">
                        Upload image here
                      </text>
                      )}  
                  </svg>
                  
                  <input
                    type="file"
                    
                    name='bjmplogo'
                    id='bjmplogo'
                    ref={fileInputRef}
                    style={{display: 'none'}}
                    accept="image/*"
                    onChange={handleChange}
                  />
                  <div className='d-flex justify-content-end mt-2'>
                  <form onSubmit={handleImageSubmit}>
                            <button type="submit" className="btn btn-secondary" style={{ fontSize: 'small'}}>
                              Submit
                            </button>
                          </form>
                  </div>
                </div>
                  <div className='col-6'>
                  <div className="row g-3 align-items-center">
                      <div className="col-12">
                        <label htmlFor="first_name" className="col-form-label">Fingerprint Reader:</label>
                        
                        <div className='d-flex justify-content-end mt-2 align-items-center'>
                        <select className="form-control" id="readersDropDown" onChange={handleReaderChange}></select>
                        <button className="btn btn-secondary ms-2" style={{ fontSize: 'small' }} onClick={handleReaderRefresh}>Refresh.</button>
                        </div>
                        <label htmlFor="first_name" className="col-form-label">Change Spending Limit:</label>
                          <div className='d-flex justify-content-end mt-2 align-items-center'>
                          <input type="number" id="spendingLimit" name="spendingLimit" className="form-control"
                           placeholder="Enter spending limit" onChange={handleChange}
                          value={spendingData.limitData}
                          />
                            <button onClick={handleLimitChange} className="btn btn-secondary ms-2" style={{ fontSize: 'small'}}>Change</button></div>
                      </div>
                    </div>
                  </div>
                </div>
            
            </>) : (<><div className="row g-3 align-items-center">
            <div className="col-12">
              <label htmlFor="first_name" className="col-form-label">Change Email:</label>
              <div className='d-flex justify-content-end align-items-center'>
              <input type="email" id="emailAdd" name="emailAdd" className="form-control"
               placeholder="Enter email" onChange={handleChange}
                          
               value={emailData.emailAdd} /> <button onClick={handleSubmit} className="btn btn-secondary ms-2" style={{ fontSize: 'small'}}>Change</button>   
              </div>
            </div>
            </div>
            <div className="row g-3 align-items-center">
              <div className="col-6">
                <label htmlFor="first_name" className="col-form-label">Request Password Change:</label>
                <button className="btn btn-secondary" onClick={requestPasswordChange} style={{ fontSize: 'small', flex: '1', minWidth: '0' }}>Request Password Change</button>
              </div>
              <div className="col-6">
                <label htmlFor="last_name" className="col-form-label">Archive Account:</label>
                <button className="btn btn-secondary" onClick={archiveAccount} style={{ fontSize: 'small', flex: '1', minWidth: '0', backgroundColor: '#770000' }}>Archive Account</button></div>
            </div></>)}
            
        </div>
      </div>
      <div style={{display: 'none'}}>
                              <div id="content-capture">
                                <div id="status"></div>            
                                <div id="imagediv"></div>
                                <div id="imageGallery"></div>
                                <div id="deviceInfo"></div>   
                                <div id="Scores"><input id="qualityInputBox"></input></div>
                                
          </div>
        </div>
        {logOutModal && <LogoutModal method={methodType} passwordMessage={passwordMessage} />}
  {archiveUser && <ConfirmArchive primaryUserEmail={email} archiveType="User" onClose={closeArchiveModal} successfulArchive={successfulArchive}/>}
  {successModal && <SuccAcc onClose={closeSuccessModal}/>}
    </div>
  );
}

export default SettingsModal;
