import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import axios from 'axios';
import domain from '../../constants/domain';

function DeleteModal({ onClose, 
  primaryPDLID, primaryStockID, posPrimaryID, primaryUserID, insPrimaryID}) {
    console.log(primaryUserID);
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'logoutModal') {
      onClose(); // Close the modal
    }
  };
  const handleDeletion = () => {
    if(primaryPDLID){
      axios.post(domain, {
        action: 'deletePDL',
        primaryID: primaryPDLID
      }).then(response => {
        if(response.data.success){
          onClose();
        }
      })
    }
    if(primaryStockID){
      axios.post(domain, {
        action: 'deleteStock',
        primaryID: primaryStockID
      }).then(response => {
        if(response.data.success){
          onClose();
        }
      })
    }
    if(posPrimaryID){
      const removeElement = {
        email: localStorage.getItem('email'),
        tempID: posPrimaryID,
        action: "removePOSElement"
      }
      axios
        .post(domain, removeElement)
        .then(response => {
          onClose();
        })
        .catch(error => {
          console.error('Error removing entry:', error);
        });
    }
    if(primaryUserID){
      axios.post(domain, {
        action: 'deleteUser',
        primaryID: primaryUserID
      }).then(response => {
        if(response.data.success){
          onClose();
        }
      })
    }
    if(insPrimaryID){
      axios.post(domain, {
        action: 'deleteInstance',
        primaryID: insPrimaryID
      }).then(response => {
        if(response.data.success){
          onClose();
        }
      })
    }
  }
  return (
    <div className={dst.modal}  onClick={handleBackdropClick} id="logoutModal" tabIndex="-1">
      <div>
        <div className={dst['modal-content']} style={{width: '500px'}}>
          <div className={`text-center ${dst['modal-body']}`}>
            <i className={`fa-regular fa-circle-xmark fa-2xl mb-4`} style={{fontSize: '40px'}}></i>
            <h3>Confirm Deletion?</h3>
            <p style={{ color: '#a1a1a1' }}>Are you sure you want to delete entry? This cannot be undone</p>

            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
            <button
                type="button"
                className="btn link modal-close"
                style={{ border: '1px solid #505050', width: '40%' , marginRight: '15px'}}
                onClick={onClose}
              >
                No, Cancel
              </button>
              <button
                type="submit"
                className="btn"
                style={{ backgroundColor: '#505050', color: '#fff', width: '40%', display: 'block' }}
                onClick={handleDeletion}
              >
                OK
              </button>

            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DeleteModal;
