import React, {useState, useEffect} from 'react';
import dst from '../../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../../constants/domain';

function LowCount() {
  const [isVisible, setVisibility] = useState(true)
  const [tableData, setTableData] = useState([]);
  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getLowStockItems`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }

  const toggleVisibility = () => {
    setVisibility((prevIsVisible) => !prevIsVisible);
  };
  const getCellStyleForType = (type) => {
    // Map type to corresponding CSS class
    switch (type) {
      case 'Overstocked':
        return dst.Overstocked;
      case 'Healthy':
        return dst.Healthy;
      case 'NearCritical':
        return dst.NearCritical;
      case 'AtCritical':
        return dst.AtCritical;
      case 'BelowCritical':
        return dst.BelowCritical;
      case 'NoStock':
        return dst.NoStock;
      default:
        return ''; // Use a default style if needed
    }
  };
  
  return (
    <div>
      <div className={`${dst.sixthCard} p-3 position-relative`}>
        <div className='row d-flex align-items-center'>
          <div className='col-11 d-flex align-items-center mb-3'>
          
            <i className='fa-solid fa-truck-ramp-box'></i>
            <h6 className='fw-bold fs-5 m-0 mx-2'>Low Stocked Items</h6>
          </div>
        </div>
          <div className='col-12 mx-auto '>
            <table
              className={`${dst.cleanTable} w-100`}
              style={{
                borderCollapse: 'collapse',
                color: '#48505E',
                fontSize: 'small',
                overflow: 'auto',
                fontSize: '12px',
              }}
            >
              <thead>
              <tr>
                <th scope="col">Items</th>
                <th scope="col">In Stock</th>
                <th scope="col">Status</th>
              </tr>
            </thead>
            <tbody>
              {tableData.length !== 0 ? Array.from({ length: 7 }, (_, index) => {
                    const data = tableData[index];
                    const hasData = data !== undefined;
                    const cellStyle = hasData ? {} : { padding: '18px' }; // Adjust the padding value as needed
                    const isBelowCritical = hasData && data.remainingStock >= data.criticalStock;

                    return (
                      <tr key={index} className={index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}>
                        <td style={cellStyle}>{hasData ? `${data.prodType} - ${data.prodName}` : ''}</td>
                        <td style={cellStyle}>{hasData ? (data.remainingStock != 0 ? 
                        (`${data.remainingStock}pcs`) : ('No stock')) : ''}</td>
                        <td>
                              {hasData ? (<div className='d-flex'>
                                {(parseInt(data.remainingStock) - parseInt(data.criticalStock)) <= 50 &&
                                (parseInt(data.remainingStock) - parseInt(data.criticalStock)) >= 21 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Overstocked')}`}>
                                  Overstocked
                                 </div>)
                                }
                                {(parseInt(data.remainingStock)- parseInt(data.criticalStock)) <= 20 &&
                                (parseInt(data.remainingStock) - parseInt(data.criticalStock)) >= 11 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('Healthy')}`}>
                                  Healthy
                              </div>)
                                }
                                {(parseInt(data.remainingStock) - parseInt(data.criticalStock)) <= 10 &&
                                (parseInt(data.remainingStock) - parseInt(data.criticalStock)) >= 1 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NearCritical')}`}>
                                  Near Critical
                                </div>)
                                }
                                {parseInt(data.remainingStock) === parseInt(data.criticalStock) &&
                                parseInt(data.remainingStock) !== 0 && parseInt(data.criticalStock) !== 0 && 
                                (<div className={`${dst.typeCell} ${getCellStyleForType('AtCritical')}`}>
                                At Critical
                              </div>)
                                }
                                {parseInt(data.remainingStock) < parseInt(data.criticalStock) &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('BelowCritical')}`}>
                                Below Critical
                              </div>)
                                }
                                {parseInt(data.remainingStock) === 0 &&
                                (<div className={`${dst.typeCell} ${getCellStyleForType('NoStock')}`}>
                                No Stock
                              </div>)
                                }
                              </div>) : ''}




                           </td></tr>
                    );
              }) : (
                <tr>
                  <td style={{ padding: '172px 0', textAlign: 'center'}} colSpan={3}>
                    Nothing to show
                  </td>
                </tr>
              )}
            </tbody>
            </table>
      </div>
      </div>
    </div>
    
  );
}

export default LowCount;
