import React, {useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from './LoadSuccess';
import axios from 'axios';
import domain from '../../constants/domain';

function LoadDetails({onClose, onClose2, loadDetails, pdlDetails}) {

  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const action = 'loadPDL';
  console.log(loadDetails, pdlDetails);
  const handleSubmit = async (e) => {
    e.preventDefault();
    const {id, pdl_lastname, pdl_firstname, pdl_middlename, formatted_balance} = pdlDetails;
    const {lenderName, balanceAdd, loadType} = loadDetails;
    const newBalance = parseFloat(pdlDetails.formatted_balance) + parseFloat(loadDetails.balanceAdd);

    const postData = {
      action,
      id,
      email: localStorage.getItem("email"),
      pdl_name: pdl_lastname.toUpperCase() + ', ' + pdl_firstname + " " + pdl_middlename,
      formatted_balance,
      lenderName,
      balanceAdd,
      loadType,
      newBalance: newBalance.toFixed(2) // Ensure newBalance is formatted to 2 decimal places
    };

    try {
      const response = await axios.post(domain, postData);
      if(response.data){
        openSuccessModal();
        const fileUrl = response.data.pdfPath;
          const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/receipts/'));
          const newWindow = window.open(formattedFileUrl, '_blank');
      }
    } catch (error) {
      console.error('Error sending POST request:', error);
    }
  };
  const openSuccessModal = () => {
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
  };
  return (
    <div className={dst.modal} id="exampleModal" tabIndex="-1">
       <div className={dst['modal-content']} style={{ width: '500px'}}>
        <div className={`${dst['modal-header']}`}>
          <div className='row w-100'>
            <div className='col-10 d-flex align-items-center'>
              <i className='fa-solid fa-receipt'></i>
              <h6 className='fw-bold fs-6 m-0 pe-4 ps-2 text-start'>Load Details</h6>
            </div>
            <div className='col-2 d-flex justify-content-end align-items-center'>
              <button type="button" 
              className="btn-close" onClick={onClose2} 
              style={{ boxShadow: 'none', fontSize: '21px', textAlign: 'end'}}></button>
            </div>
          </div>
        </div>
        <div className={dst['modal-body']}>
          <div className='row d-flex justify-content-start align-items-center text-start'>
            <label for="balanceAdd" style={{fontSize: '12px'}}>PDL ID:</label>
            <p>{pdlDetails ? (`PDL-${pdlDetails.id}`) : ''}</p>
          </div>
          <div className='row d-flex justify-content-start align-items-center text-start'>
            <label for="balanceAdd" style={{fontSize: '12px'}}>PDL's Full Name:</label>
            <p>{pdlDetails ? (`${pdlDetails.pdl_lastname.toUpperCase()}, 
            ${pdlDetails.pdl_firstname} ${pdlDetails.pdl_middlename}`) : ''}</p>
          </div>
          <div className='row d-flex justify-content-start align-items-center text-start'>
            <label for="balanceAdd" style={{fontSize: '12px'}}>Lender's Full Name:</label>
            <p>{loadDetails ? loadDetails.lenderName : ''}</p>
          </div>
          <div className='row d-flex justify-content-start align-items-center text-start'>
            <label for="balanceAdd" style={{fontSize: '12px'}}>Old Balance:</label>
            <p>{pdlDetails ? (`PHP${pdlDetails.formatted_balance}`) : ''}</p>
          </div>
          <div className='row d-flex justify-content-start align-items-center text-start'>
            <label for="balanceAdd" style={{fontSize: '12px'}}>Amount Loaded:</label>
            <p>{loadDetails ? (`PHP${(parseFloat(loadDetails.balanceAdd)).toFixed(2)}`) : ''}</p>
          </div>
          <div className='row d-flex justify-content-start align-items-center text-start'>
            <label for="balanceAdd" style={{fontSize: '12px'}}>New Balance:</label>
            <p>{pdlDetails && loadDetails ? (`PHP${(parseFloat(pdlDetails.formatted_balance) + parseFloat(loadDetails.balanceAdd)).toFixed(2)}`) : ''}</p>
          </div>
        </div>
        <div className={`${dst['modal-footer']} d-flex justify-content-end`} style={{ border: 'none' }}>
              <button
                type="button"
                className={`btn ${dst.sort}`}
                onClick={onClose2}
              >
                Back
              </button>
              <button
                type="button"
                className={`btn ${dst.add} ms-2`}
                onClick={handleSubmit}
              >
                Load Now!
              </button>
              
            </div>
       </div>
       {isSuccessModalOpen && <Success onClose={closeSuccessModal} newBalance={(parseFloat(pdlDetails.formatted_balance) + parseFloat(loadDetails.balanceAdd)).toFixed(2)}/>}
    </div>
  );
}

export default LoadDetails;
