import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import PurchaseSuccess from './PurchaseSuccess';
import Success from './SuccessfulAction';
import {v5 as uuidv5} from 'uuid';
import domain from '../../constants/domain';
import axios from 'axios';
import secugenAPI from '../../constants/secugen';

let isLeftCtrlPressed = false;

function FingerprintModal({onBGClick, onClose, typeOfFinScan, pdlData, setFingerprintData, postData, getUser, newBalance}) {
  const [typeOfScan, setTypeofFinScan] = useState(typeOfFinScan);
  const [testButton, setTestButton] = useState('');
  const [isPurchaseSuccessModalOpen, setPurchaseSuccessModalOpen] = useState(false);
  const [inputCheckerMessage, setInputCheckerMessage] = useState('');
  const [startButtonState, setStartButtonState] = useState(false);
  const [stopButtonState, setStopButtonState] = useState(true);
  const [fingerprintIcon, setFingerprintIcon] = useState(true);
  const [fingerprintColor, setFingerprintColor] = useState('#000000');
  const openPurchaseSuccessModal = () => {
    
    setPurchaseSuccessModalOpen(true);
  };
  const closePurchaseSuccessModal = () => {
    setPurchaseSuccessModalOpen(false);
    onClose();
  };
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const openSuccessModal = () => {
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
    
  };

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onBGClick(); // Close the modal
      }
    };
    const inputChecker = () => {
      setStartButtonState(true);
      if(typeOfScan === 'create'){
        axios.post(`${secugenAPI}SGIFPCapture?Timeout=10000&TemplateFormat=ISO`)
        .then(response => {
          // Handle the response
          console.log(response.data);
          const imageDataBase64 = response.data.BMPBase64;
          const errorCode = response.data.ErrorCode;
          const fingerprintData = response.data.TemplateBase64;
          if(errorCode !== 0){
            setFingerprintColor('#ff0000');
            setTimeout(() => {
              setFingerprintColor('#000000');
            }, 1000);
          }
          else{
            setFingerprintColor('#00ff00');
            setTimeout(() => {
              axios.post(domain, {
                fingerprintID: fingerprintData,
                id: pdlData.userID,
                lastName: pdlData.lastName,
                firstName: pdlData.firstName,
                middleName: pdlData.middleName,
                gender: pdlData.gender,
                action: 'setFingerprintData',
              }).then(response => {
                openSuccessModal();
              })
            }, 1000);
          }
          setStartButtonState(false);
            // Update the state with the base64-encoded image data
        })
        .catch(error => {
          // Handle errors
          console.error('Error:', error);
        });
      }
      else{
        const fingerprintData = pdlData.fingerprint_id;
        
        axios.post(`${secugenAPI}SGIFPCapture?Timeout=10000&TemplateFormat=ISO`)
        .then(response => {
          // Handle the response
          console.log(response.data);
          const ISOMatch = response.data.TemplateBase64;
          const errorCode = response.data.ErrorCode;
          if(errorCode !== 0){
            setFingerprintColor('#ff0000');
            setTimeout(() => {
              setFingerprintColor('#000000');
            }, 1000);
          }
          else{
            setFingerprintColor('#ff00ff');
            var xmlhttp = new XMLHttpRequest();

            var params = "template1=" + encodeURIComponent(fingerprintData);
            params += "&template2=" + encodeURIComponent(ISOMatch);
            xmlhttp.open("POST", `${secugenAPI}SGIMatchScore`, false);
            xmlhttp.send(params);
            var result;
            if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
              // Parse the responseText to obtain the result
              result = JSON.parse(xmlhttp.responseText);
            } else {
                // Handle the case where the request was not successful
                result = { ErrorCode: xmlhttp.status, ErrorMessage: "Request failed" };
            }
            if(result.MatchingScore >= 100){
              setFingerprintColor('#00ff00');
              axios.post(domain, postData)
                .then((response) => {
                  if (response.data) {
                    setTimeout(() => {
                      openPurchaseSuccessModal();
                      const fileUrl = response.data.pdfPath;
                      const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/receipts/'));
                      window.open(formattedFileUrl, '_blank');
                    }, 3000);
                    
                  }
                })
                .catch((error) => console.error('Error getting data: ', error));
              
            }
            else{
              setFingerprintColor('#ff0000');
                setTimeout(() => {
                  setFingerprintColor('#000000');
                  return;
                }, 1000);
            }
            /*axios.post(`${secugenAPI}SGIMatchScore?Template1=${ISOMatch}&Template2=${ISOMatch}`)
            .then(response => {
              console.log(response.data);
              if(response.data.MatchingScore >= 100){
                setTimeout(() => {
                  setFingerprintColor('#00ff00');
                  openSuccessModal();
                }, 1000);
              }
              else{
                setFingerprintColor('#ff0000');
                setTimeout(() => {
                  setFingerprintColor('#000000');
                  return;
                }, 1000);
              }
            })*/
          }
          setStartButtonState(false);
            // Update the state with the base64-encoded image data
        })
        .catch(error => {
          // Handle errors
          console.error('Error:', error);
        });
      }
       
    }

  
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '350px'}}>
          <div className={`${dst['modal-header']} text-start`} style={{ border: 'none'}}>
            <h5 className="modal-title fw-bold" id="exampleModalLabel">
              Scan your fingerprint
            </h5>
            <p style={{fontSize: '12px', color: '#666'}}>
              Scan your fingerprint using the biometric device
            </p>
          </div>
          <div className={`content-capture ${dst['modal-body']}`}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col">
              {typeOfScan === 'purchase' && (
              <>
              
              <div className='row mb-3'>
                  <div id='imagediv' style={{height: 'fit-content', margin: 'auto', borderRadius: '25px', width: 'fit-content'}}>
                        
                    </div>
                  <i className="fas fa-fingerprint" style={{ fontSize: '150px', color: fingerprintColor }} 
                  id="defaultFingerprintIcon"></i>
                </div>
              <div className='row'>
                <div className='col-12'> 
                <button className="btn btn-secondary" style={{ fontSize: 'small' }} onClick={inputChecker} disabled={startButtonState}>Start</button>
                </div>
                
              </div>
              </>
              )
              }
              {typeOfScan === 'create' && (
                <>
                <div className='row mb-3'>
                    <div id='imagediv' style={{height: 'fit-content', margin: 'auto', borderRadius: '25px', width: 'fit-content'}}>
                          
                      </div>
                    <i className="fas fa-fingerprint" style={{ fontSize: '150px', color: fingerprintColor }} id="defaultFingerprintIcon"></i>
                
                </div>
                <div className='row'>
                  <div className='col-12'> 
                  <button className="btn btn-secondary" style={{ fontSize: 'small' }} onClick={inputChecker} disabled={startButtonState}>Start</button>
                  </div>
                 
                </div>
              </>
                )
              }
              
                </div>
            </div>
          </div>
        </div>
        
        {isPurchaseSuccessModalOpen && <PurchaseSuccess onClose={closePurchaseSuccessModal} newBalance={newBalance}/>}
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default FingerprintModal;
