import React, { useState, useEffect, useRef } from 'react';
import dst from '../../../styles/DashboardStyle.module.css';
import { Bar } from "react-chartjs-2";
import Chart from "chart.js/auto";
import axios from 'axios';
import domain from '../../../constants/domain';

function TotalTransactions({isSidebarOpen}) {
  const [selectedPeriod, setSelectedPeriod] = useState('Weekly');

  const BarChart = () => {
    const [chartData, setChartData] = useState([]);
    const handlePeriodChange = (event) => {
      setSelectedPeriod(event.target.value);
    };
  
    useEffect(() => {
      axios.get(selectedPeriod === 'Weekly' ? 
      `${domain}/index.php?action=getWeeklyTransactions` :
      `${domain}/index.php?action=getMonthlyTransactions`)
      .then((response) => {
          setChartData(response.data);
      })
      .catch((error) => {
        console.error(`Error fetching ${selectedPeriod} spending data: `, error);
      })
    }, [])
    const titleText = selectedPeriod === 'Weekly' ? 'Weekly Total Transactions' : 'Monthly Total Transactions';

    const labels = selectedPeriod === 'Weekly' ? ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'] : 
      ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'];
  
      const data = {
        labels,
        datasets: [
          {
            label: 'Load Count',
            data: chartData && chartData.map(entry => entry.load_count),
            backgroundColor: '#10069F', // Adjust as needed
            borderRadius: 50,
            barThickness: 10,
          },
          {
            label: 'Purchase Count',
            data: chartData && chartData.map(entry => entry.purchase_count),
            backgroundColor: '#FF5733', // Adjust as needed
            borderRadius: 50,
            barThickness: 10,
          },
        ],
      };
      
    const options = {
      
      scales: {
        y: {
          beginAtZero: true,
          ticks: {
            callback: (value) => `${value} transactions`,
          },
        },
      },
      maintainAspectRatio: false, // Allow chart to resize to container
      responsive: true, // Enable responsiveness
    };

    const totalTransactionSum = chartData.reduce(
      (accumulator, entry) => accumulator + parseInt(entry.load_count) + parseInt(entry.purchase_count),
      0
    );
    return (
      <div className={dst.fifthCard}>
        <div className='row d-flex align-items-center position-relative'>
          <div className='col-8'>
            <p className="fw-bold ms-3 p-1 fs-6" 
            style={{ position: 'relative', top: '5px', textAlign: 'left' }}>
              {titleText}
            </p>
          </div>
          <div className='col-4 align-items-end justify-content-end'>
            <div className="position-absolute mb-5 top-0 end-0 p-2">
              <select
                id="durationSelector"
                className="form-select form-select-sm"
                value={selectedPeriod}
                onChange={handlePeriodChange}
              >
                <option value="Weekly">Weekly</option>
                <option value="Monthly">Monthly</option>
              </select>
              {/* Move the span for the specified week to the top right */}
            </div>
          </div>
          <div className={dst.chartContainer}>
          {totalTransactionSum != 0 ? (
              <div style={{ height: '100%' }}>
                <Bar data={data} options={options} />
              </div>
            ) : (
              <div className="text-center p-3">
                <p className="text-muted mb-0">No data available for the selected period.</p>
              </div>
            )}
        </div>
        </div>
        </div>
    );
  };


  return (
    <div className={`${isSidebarOpen ? 'sidebarOpen' : 'sidebarClosed'}`}>
      <div className='mt-2'>
        <BarChart selectedPeriod={selectedPeriod} />
      </div>
    </div>
  );
}

export default TotalTransactions;
