
import React, { useState, useEffect, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link, useLocation } from 'react-router-dom';
import UserProfile from '../modals/UserProfileModal';
import ConfirmArchive from '../modals/ConfirmArchive';
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';
import FilterDropdown from '../modals/FilterDropdown';
import { useReactToPrint } from 'react-to-print';
import CreateUser from '../modals/CreateUser';
import EditUser from '../modals/EditUser';
import RetrievePopUp from '../modals/RetrieveModal';
import DeleteModal from '../modals/DeleteModal';

function UserList() {
  const componentPDF = useRef();
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const day = String(currentDate.getDate()).padStart(2, '0');
  
  const formattedDate = `${year}-${month}-${day}`;
  
  const hours = String(currentDate.getHours()).padStart(2, '0');
  const minutes = String(currentDate.getMinutes()).padStart(2, '0');
  const formattedTime = `${hours}:${minutes}`;
  const [userData, setUserData] = useState([]);
  const [selectedPdlId, setSelectedPdlId] = useState(null);
  const [primaryID, setPrimaryID] = useState(null);
  const [sortedUserData, setSortedUserData] = useState([]);
  const [selectedOption, setSelectedOption] = useState(''); // Set the default option to "option4"
  const [userProfileModal, setUserProfileModal] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage] = useState(10);
  const [userEmail, setUserEmail] = useState(null);
  const [selectMultipleEnabled, setSelectMultipleEnabled] = useState(false);
  const [selectedRows, setSelectedRows] = useState([]);
  const [activeTab, setActiveTab] = useState("userList");
  const [filterOptions, setFilterOptions] = useState({
    name: [''],
    email: [''], 
    userType: ['Admin', 'Jail Commissary Staff', 'Shopkeeper'], 
  });
  const [selectedFilter, setSelectedFilter] = useState({
    name: '',
    email: '',
    userType: '',
    // Add more columns as needed
  });
  const [selectedFilterLabel, setSelectedFilterLabel] = useState({
    name: 'Name', // Default label for the "Name" column
    email: 'Email', // Default label for the "Balance" column
    userType: 'User Type', // Default label for the "Type" column
    // Add more columns as needed
  });
  useEffect(() => {
    // Make an API call to fetch user data
    getData();
    setSelectMultipleEnabled(false);
  }, [activeTab]);
  const eraseFilters = () => {
    setSelectedFilter({
      name: '',
      email: '',
      userType: '',
      // Add more columns as needed
    });
    setSelectedFilterLabel(({
      name: 'Name', // Default label for the "Name" column
      email: 'Email', // Default label for the "Balance" column
      userType: 'User Type', // Default label for the "Type" column
      // Add more columns as needed
    }));
  }
  const userTypeMapping = {
    0: 'Admin',
    1: 'Jail Commissary Staff',
    2: 'Shopkeeper',
  };
  function getData() {
    axios.get(activeTab === 'userList' ? 
    `${domain}/index.php?action=getUserList` :
    `${domain}/index.php?action=getArchivedUsers`)
    .then(response => {
    // Set the fetched data to the state
      setUserData(response.data);
      setSortedUserData(response.data); 
      setFilterOptions(prevFilterOptions => ({
        ...prevFilterOptions,
        name: response.data.map(user => user.lastName),
        email: response.data.map(user => user.emailAdd),
        userType: response.data.map(user => userTypeMapping[user.userType] || ''),
      }));
  })
  .catch(error => console.error('Error fetching user data:', error));
  }
  // Empty dependency array means this effect runs once on component mount

  const [isFilterDropdownOpen, setFilterDropdownOpen] = useState(false);
  const [filterDropdownColumn, setFilterDropdownColumn] = useState('');
  const [typeOfArchive, setTypeOfArchive] = useState('');
  const [isRetrieveModalOpen, setRetrieveModalOpen] = useState(false);
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);

  const filteredData = sortedUserData.filter((user) => {
    const isNameMatch = 
      user.lastName.toUpperCase().includes(selectedFilter.name.toUpperCase()) ||
      `${user.lastName.toUpperCase()}, ${user.firstName} ${user.middleName}`.toUpperCase()
      .includes(selectedFilter.name.toUpperCase());
    const isEmailMatch = 
      selectedFilter.email === '' ||
      selectedFilter.email.toUpperCase().includes(user.emailAdd.toUpperCase());
      const isUserTypeMatch =
      selectedFilter.userType === '' ||
      selectedFilter.userType === 'Admin' && parseInt(user.userType) === 0 || 
      selectedFilter.userType === 'Jail Commissary Staff' && parseInt(user.userType) === 1 || 
      selectedFilter.userType === 'Shopkeeper' && parseInt(user.userType) === 2;

  return isNameMatch && isEmailMatch && isUserTypeMatch;
  });

  
  const openRetrieveModal = (primID, e, id) => {
    setRetrieveModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
  }

  const closeRetrieveModal = () => {
    setRetrieveModalOpen(false);
    getData();
  }
  
  const openDeleteModal = (primID, e, id) => {
    setDeleteModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
  }
  const closeDeleteModal = () => {
    setDeleteModalOpen(false);
    getData();
  }
  const [logo, setLogo] = useState(null);
  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setLogo(fileName);
      }
    )

   }, [])
   const selectMultiple = () => {
    setSelectMultipleEnabled((prevState) => !prevState);
    if(!selectMultipleEnabled){
      setSelectedRows([]);
      setSelectedFilterLabel(({
        name: 'Name', // Default label for the "Name" column
        email: 'Email', // Default label for the "Balance" column
        userType: 'User Type', // Default label for the "Type" column
        // Add more columns as needed
      }))
      setSelectedFilter({
        name: '',
        email: '',
        userType: '',
        // Add more columns as needed
      })
      ;
    }
  }

    const totalEntries = filteredData ? filteredData.length : 0;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    let entriesToShow = [];
    if (Array.isArray(filteredData)) {
      entriesToShow = filteredData.slice(
        (currentPage - 1) * entriesPerPage,
        currentPage * entriesPerPage
      );
  }

  const toggleFilterDropdown = (column) => {
    setFilterDropdownOpen(!isFilterDropdownOpen);
    setFilterDropdownColumn(column);
  };

  const handleFilterSelect = (option, column) => {
    // Implement logic to filter data based on the selected option
    // You can use this information to filter your data accordingly

    // Update the selected filter state for the specific column
    setSelectedFilter({
      ...selectedFilter,
      [column]: option,
    });

    // Update the label for the corresponding column
    switch (column) {
      case 'name':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          name: option,
        });
        break;
      case 'email':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          email: option,
        });
        break;
      case 'userType':
        setSelectedFilterLabel({
          ...selectedFilterLabel,
          userType: option,
        });
        break;
      // Add cases for other columns if needed
      default:
        break;
    }
    setFilterDropdownOpen(false);
  };


const renderFilterDropdown = (column) => {
    if (isFilterDropdownOpen && filterDropdownColumn === column) {
      return (
        <FilterDropdown
          options={filterOptions[column]}
          onSelect={(option) => handleFilterSelect(option, column)}
          type={column}
          typeOfLastName='user'
        />
      );
    }
    return null;
  };



  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };
  
  const [isCreateUserModalOpen, setCreateUserModalOpen] = useState(false);
  const openCreateUserModal = () => {
    setCreateUserModalOpen(true);
  }
  const closeCreateUserModal = () => {
    setCreateUserModalOpen(false);
    getData();
  }
  const [isEditUserModalOpen, setEditUserModalOpen] = useState(false);
  const openEditUserModalOpen = (email, e) => {
    setUserEmail(email);
    setEditUserModalOpen(true);
  }
  const closeEditUserModalOpen = () => {
    setEditUserModalOpen(false);
    getData();
  }
  
  const openUserProfileModal = (email, e) => {
    setUserEmail(email);
    setUserProfileModal(true);
  };
  const closeUserProfileModal = () => {
    setUserProfileModal(false);
  };


  const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
  const openArchiveModal = (email, e, primID, type) => {
    setTypeOfArchive(type);
    if(type === 'Single'){
      setArchiveModalOpen(true);
      setUserEmail(email);
      setPrimaryID(primID);
    }
    else{
      setArchiveModalOpen(true);
    }
    
    
  };
  const closeArchivePDLModal = (id) => {
    setArchiveModalOpen(false);
    getData();
    setSelectedRows([]);
    setSelectMultipleEnabled(false);
  };


  const generateReport = useReactToPrint({
    content: () => componentPDF.current,
    documentTitle: "User List",

  }) /*() => {
      if(pdlData){
        const action = "generatePDLReport";
        const actionData = { action };
        
        axios.post(domain, actionData)
            .then(response => {
                // Handle successful response, if needed
                 // Response.data will contain the file URL
                 const fileUrl = response.data.fileUrl;
                  const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
                // Open the PDF in a new tab
                window.open(formattedFileUrl, '_blank');
            })
            .catch(error => {
                // Handle error, if needed
                console.error('Error generating report:', error);
            });
      }
  }*/
  const tabs = [
    { label: "User List", id: "userList" },
    { label: "Archived Users", id: "archivedUsers" },
    // Add more tabs as needed
  ];
  return (
    <div className="px-5 pb-5" >
      <div className='row' style={{}}>
        <div className={`col-5 d-flex justify-content-start ${dst.tabs}`}>
        {tabs.map((tab) => (
              <button
                key={tab.id}
                className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.label}
              </button>
            ))}
          </div>
        </div>
      <div className={`${dst.con} p-4`}>
        {activeTab === 'userList' ? (<div className={`${dst.row} mt-1`}>
          <div className='row d-flex align-items-center position-relative'>
            <div className='col-4 d-flex align-items-center mb-3'>
              <i className='fa-solid fa-user'></i>
              <h6 className='fw-bold fs-5 m-0 mx-2'>User List</h6>
            </div>
            <div className='col-8 d-flex justify-content-end'>
                {!selectMultipleEnabled ? (<><button className={`${dst.tableBtn} me-2`} 
                    style={{left: '0'}}type='button' onClick={generateReport}>
                  <i className={`fa-regular fa-file ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Generate Report</span>
                  </button>
                  <button className={`${dst.tableBtn} me-2`} style={{left: '0'}}type='button'
                  onClick={openCreateUserModal} >
                  <i className={`fa-regular fa-plus ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Add User</span>
                  </button></>) : selectMultipleEnabled && selectedRows.length > 0 ?
                  (<><button className={`${dst.tableBtn} me-2`} 
                  style={{left: '0'}}type='button' 
                  onClick={(e) => openArchiveModal(null, e, null, 'Batch')}>
                  <i className={`fa-solid fa-box-archive ${dst.iconHover}`}></i>
                  <span className={dst.iconTooltip}>Archive</span>
                  </button></>) : null
                }
                {
                selectedFilterLabel.name !== 'Name' || 
                selectedFilterLabel.email !== 'Email' ||
                selectedFilterLabel.userType !== 'User Type' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
                <button className={`${dst.tableBtn} me-2`} style={{left: '0', 
                backgroundColor: selectMultipleEnabled ? '#0099ff' : '',
                color: selectMultipleEnabled ? '#fff' : '',
                border: selectMultipleEnabled ? 'none' : ''
                }}type='button' onClick={selectMultiple}>
                <i className={`fa-solid fa-clipboard-check ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Select Multiple</span>
                </button>
              </div>
              <div className={`col-12 mx-auto`} >
              <table
                  className={`${dst.cleanTable} ${dst.selectableTable}  w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    fontSize: 'small',
                    overflow: 'auto',
                    fontSize: '14px',
                    border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                  }}
                >
                <thead>
                  <tr style={{fontSize: '16px'}}>
                    {selectMultipleEnabled && (<th scope="col">
                      Select.
                    </th>)}
                    <th scope="col">
                      User No.
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.name}
                      {!selectMultipleEnabled &&(<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('name')}></i>
                      {renderFilterDropdown('name')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.email}
                      {!selectMultipleEnabled && (<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('email')}></i>
                      {renderFilterDropdown('email')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.userType}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('userType')}></i>
                      {renderFilterDropdown('userType')}</>)}
                    </th>
                    {!selectMultipleEnabled && (<th scope="col" className={dst.exclude} >
                      Actions
                    </th>)}
                  </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow.length != 0 ? (<>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const user = entriesToShow[index];
                    const hasData = user !== undefined;
                    const thisUser = hasData ? (localStorage.getItem("email") != user.emailAdd ? true : false) : '';
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    const isSelected = selectedRows.includes(parseInt(user?.userid));
                    const handleRowClick = () => {
                      if (selectMultipleEnabled) {
                        
                        if (hasData) {
                          const userId = parseInt(user.userid); // Get the user ID
                          if (isSelected) {
                            setSelectedRows(selectedRows.filter((selectedId) => selectedId !== userId));
                            
                          } else {
                            setSelectedRows([...selectedRows, userId]);
                          }
                          
                        }
                      } else {
                        // If selectMultipleEnabled is false, clear the selected rows
                        setSelectedRows([]);
                      }
                    };
                    if (!thisUser && selectMultipleEnabled) {
                      return null;
                    }
                    return (<tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                      ${isSelected && selectMultipleEnabled ? dst.selectedRow : ''}`}
                      onClick={handleRowClick}
                      style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                      >
                        {selectMultipleEnabled && (<td>{hasData ? 
                        (<i className={`fa-regular ${isSelected ? 'fa-square-check' : 'fa-square'} fs-3 text-center`}>

                        </i>) : ''}</td>)}
                        <td>{hasData ? `User #${user.id}` : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            <>
                              {user.lastName.toUpperCase()}, {user.firstName} {user.middleName}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>
                          {hasData ? 
                          (<>{user.emailAdd}</>) : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            parseInt(user.userType) === 0 ? <>Admin</> 
                            : parseInt(user.userType) === 1 ? <>Jail Commissary Staff</>
                            : <>Shopkeeper</>
                          ) : ''}</td>
                        {!selectMultipleEnabled && (<td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons} ${dst.exclude}`}>
                                <p onClick={(e) => openUserProfileModal(user.emailAdd, e)}>
                                  <i className={`fa-solid fa-eye fa-sm ${dst.iconHover}`}></i>
                                  <span className={dst.iconTooltip}>View</span>
                                </p>
                              {localStorage.getItem("email") != user.emailAdd && (
                              <>
                              <p onClick={(e) => openEditUserModalOpen(user.emailAdd, e)}>
                                <i className={`fa-solid fa-pen-to-square fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Edit</span>
                              </p>
                              <p onClick={(e) => openArchiveModal(user.emailAdd, e, user.userid, 'Single')}>
                                <i className={`fa-solid fa-user-minus fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Disable</span>
                              </p></>)}
                            </div>
                          )}
                        </td>)}
                      </tr>
                    );
                  })}</>) : (
                  <tr>
                    <td style={{ padding: '150px', textAlign: 'center' }} colSpan={6}>
                      Nothing to show
                    </td>
                  </tr>)}
                </tbody>
                </table>
                <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '1%', marginBottom: '5px'}}>
                <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
              </div>
              </div>
          </div>
        </div>) : (<div className={`${dst.row} mt-1`}>
          <div className='row d-flex align-items-center position-relative'>
            <div className='col-4 d-flex align-items-center mb-3'>
              <i className='fa-solid fa-user'></i>
              <h6 className='fw-bold fs-5 m-0 mx-2'>Archived Users</h6>
            </div>
            <div className='col-8 d-flex justify-content-end'>
                {
                selectedFilterLabel.name !== 'Name' || 
                selectedFilterLabel.email !== 'Email' ||
                selectedFilterLabel.userType !== 'User Type' ?
                (<button className={`${dst.tableBtn} me-2`} style={{left: '0'
                }}type='button' onClick={eraseFilters}>
                <i className={`fa-solid fa-filter-circle-xmark ${dst.iconHover}`}></i>
                <span className={dst.iconTooltip}>Erase Filters</span>
                </button>) : null}
              </div>
              <div className={`col-12 mx-auto`} >
              <table
                  className={`${dst.cleanTable} ${dst.selectableTable}  w-100 mt-3`}
                  style={{
                    borderCollapse: 'collapse',
                    color: '#48505E',
                    fontSize: 'small',
                    overflow: 'auto',
                    fontSize: '14px',
                    border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                  }}
                >
                <thead>
                  <tr style={{fontSize: '16px'}}>
                    <th scope="col">
                      User No.
                    </th>
                    <th scope="col" >
                      {selectedFilterLabel.name}
                      {!selectMultipleEnabled &&(<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('name')}></i>
                      {renderFilterDropdown('name')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.email}
                      {!selectMultipleEnabled && (<><i className='fa-solid fa-sort ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('email')}></i>
                      {renderFilterDropdown('email')}</>)}
                    </th>
                    <th scope="col" >
                    {selectedFilterLabel.userType}
                    {!selectMultipleEnabled && (<><i className='fa-solid fa-filter ms-2' style={{cursor: 'pointer'}} 
                      onClick={() => toggleFilterDropdown('userType')}></i>
                      {renderFilterDropdown('userType')}</>)}
                    </th>
                    <th scope="col" >
                      Date Archived
                    </th>
                    {!selectMultipleEnabled && (<th scope="col" className={dst.exclude} >
                      Actions
                    </th>)}
                  </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow.length != 0 ? (<>{Array.from({ length: 25 || 1 }, (_, index) => {
                    const user = entriesToShow[index];
                    const hasData = user !== undefined;
                    const thisUser = hasData ? (localStorage.getItem("email") != user.emailAdd ? true : false) : '';
                    const cellStyle = hasData ? {} : { padding: '23px' }; // Adjust the padding value as needed
                    const isSelected = selectedRows.includes(parseInt(user?.userid));
                    const handleRowClick = () => {
                      if (selectMultipleEnabled) {
                        
                        if (hasData) {
                          const userId = parseInt(user.userid); // Get the user ID
                          if (isSelected) {
                            setSelectedRows(selectedRows.filter((selectedId) => selectedId !== userId));
                            
                          } else {
                            setSelectedRows([...selectedRows, userId]);
                          }
                          
                        }
                      } else {
                        // If selectMultipleEnabled is false, clear the selected rows
                        setSelectedRows([]);
                      }
                    };
                    if (!thisUser && selectMultipleEnabled) {
                      return null;
                    }
                    return (<tr key={index} className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd} 
                      ${isSelected && selectMultipleEnabled ? dst.selectedRow : ''}`}
                      onClick={handleRowClick}
                      style={{cursor: selectMultipleEnabled && hasData ? 'pointer' : 'inherit'}}
                      >
                        <td>{hasData ? `User #${user.id}` : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            <>
                              {user.lastName.toUpperCase()}, {user.firstName} {user.middleName}
                            </>
                          ) : (
                            ''
                          )}
                        </td >
                        <td style={cellStyle}>
                          {hasData ? 
                          (<>{user.emailAdd}</>) : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? (
                            parseInt(user.userType) === 0 ? <>Admin</> 
                            : parseInt(user.userType) === 1 ? <>Jail Commissary Staff</>
                            : <>Shopkeeper</>
                          ) : ''}</td>
                        <td style={cellStyle}>
                          {hasData ? user.date_archived : ''}</td>
                         <td style={cellStyle}>
                          {hasData && (
                            <div className={`${dst.actionButtons}`}>
                              <p onClick={(e) => openRetrieveModal(user.userid, e, user.id)}>
                                <i className={`fa-solid fa-rotate-left fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Retrieve</span>
                              </p>
                              <p onClick={(e) => openDeleteModal(user.userid, e, user.id)}>
                                <i className={`fa-solid fa-trash fa-sm ${dst.iconHover}`}></i>
                                <span className={dst.iconTooltip}>Delete</span>
                              </p>
                            </div>
                          )}
                        </td>
                      </tr>
                    );
                  })}</>) : (
                  <tr>
                    <td style={{ padding: '150px', textAlign: 'center' }} colSpan={6}>
                      Nothing to show
                    </td>
                  </tr>)}
                </tbody>
                </table>
                <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '1%', marginBottom: '5px'}}>
                <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
              </div>
              </div>
          </div>
        </div>)}
      </div>
      {isCreateUserModalOpen && <CreateUser onClose={closeCreateUserModal} />}
      {isEditUserModalOpen && <EditUser onClose={closeEditUserModalOpen} userEmail={userEmail} />}
      {userProfileModal && <UserProfile onClose={closeUserProfileModal} userEmail={userEmail}/>}
      {isArchiveModalOpen && 
      <ConfirmArchive onClose={closeArchivePDLModal} 
      primaryUserEmail={userEmail} primaryUserID={primaryID} archiveType="User"
      archiveCount={typeOfArchive} rowsToArchiveUser={typeOfArchive !== 'Single' ? selectedRows : null}/>}
      {isRetrieveModalOpen && (
          <RetrievePopUp
          onClose={closeRetrieveModal}
          primaryUserID={primaryID} 
          userID={selectedPdlId}
          retrieveType="User"
          
        />
      )} 
      {isDeleteModalOpen && <DeleteModal primaryUserID={primaryID}
       onClose={closeDeleteModal}/>}
      <div ref={componentPDF} style={{position: 'absolute', width: '100%', zIndex: '-99'}}>
          <div className='m-5'>
            <div className='row d-flex justify-content-start align-items-center'>
              <div className='col-1 justify-content-start'>
              <img className={dst.logo} src={`../logo/${logo}`} alt="BJMP Logo" 
              style={{width: '80px', height: '80px'}}/>
              </div>
              <div className='col-11 text-start'>
                <div className='mx-4'>
                  <h5 className='fw-bold'>Bureau of Jail Management and Penology Region III</h5>
                  <h6 className='fw-bold'>List of Users</h6>
                  <p>Date Today: {formattedDate} {formattedTime}</p>
                </div>
                
              </div>
              
            </div>
          
          <table
              className={`${dst.cleanTable} ${dst.selectableTable} w-100 mt-3`}
                style={{
                  borderCollapse: 'collapse',
                  color: '#48505E',
                  fontSize: 'small',
                  overflow: 'auto',
                  fontSize: '14px',
                  border: selectMultipleEnabled ? '4px dashed #ccc' : ''
                }}
              >
              <thead>
                <tr style={{fontSize: '16px'}}>
                  <th scope="col">
                    User No.
                  </th>
                  <th scope="col" >
                    {selectedFilterLabel.name}
                  </th>
                  <th scope="col" >
                  {selectedFilterLabel.email}
                  </th>
                  <th scope="col" >
                  {selectedFilterLabel.userType}
                  </th>
                </tr>
              </thead>
              <tbody className={dst.shrinkMedia}>
              {entriesToShow.map((user, index) => (
                  <tr
                    key={index}
                    className={`${index % 2 === 0 ? dst.zebraEven : dst.zebraOdd}`}
                    
                   
                  >
                    <td>{user ? `User #${user.id}` : ''}</td>
                    <td>
                      {user ? (
                        <>
                          {user.lastName.toUpperCase()}, {user.firstName} {user.middleName}
                        </>
                      ) : (
                        ''
                      )}
                    </td>
                    <td>
                      {user ? user.emailAdd : ''}
                    </td>
                    <td>
                      {user ? (
                            parseInt(user.userType) === 0 ? <>Admin</> 
                            : parseInt(user.userType) === 1 ? <>Jail Commissary Staff</>
                            : <>Shopkeeper</>
                          ) : (
                        ''
                      )}
                    </td>
                  </tr>
                ))}

              </tbody>
              </table>
              
          </div>
       
       </div>
    </div>
    
  );
}

export default UserList;
